import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'DepartmentSearch'
})

@Injectable()

export class DepartmentSearch implements PipeTransform {
    transform(value: any, searchTerm: string) 
    {
       let filter = searchTerm.toUpperCase();
       return filter ? value.filter(patient=> patient.name.toUpperCase().indexOf(filter) != -1 ) : value 
    }
}


