import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class NurseServices {

    constructor(private commonServices: CommonServices) {}

    getPractitionerByIdentifier(presenceUserId){
      return this.commonServices.dataServiceHandler("/fhir/practitioner/" + presenceUserId,'GET');
    }

     getPractitionerByRoleAndOrg(role,fhirOrgId){
      return this.commonServices.dataServiceHandler("/fhir/practitioner/" + role + "/" + fhirOrgId,'GET');
    }

    getNurse(organizationId)
    {
        return this.commonServices.dataServiceHandler("/fhir/nurse/" + organizationId, 'GET');
    }
}   


