import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()

export class NursingCareServices {

    constructor(private commonServices:CommonServices){}

    getNursingCare(patientId,encounterId){
        return this.commonServices.dataServiceHandler("/fhir/nursingCare/" + patientId + "/" + encounterId, 'GET')
    }

    createNursingCare(nursingCareCreateData){
        return this.commonServices.dataServiceHandler("/fhir/nursingCare/", 'POST', nursingCareCreateData)
    }

    updateNursingCare(nursingCareId,nursingData) {
        return this.commonServices.dataServiceHandler("/fhir/nursingCare/" + nursingCareId, 'PUT', nursingData);
    }

    getNursingDevices(){
         return this.commonServices.dataServiceHandler("/fhir/nursingDevices/", 'GET');
    }

}


