import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationStart } from '@angular/router';
import { RoleServices } from '../services/presence/role.services';
import { DepartmentServices } from '../services/presence/department.services';
import { ModelMessage } from '../model_message/model.message';
import * as $ from 'jquery';


@Component({
    moduleId: module.id,
    selector: 'departments',
    templateUrl: './departments.component.html',
    styleUrls: ['departments.component.css']
})

export class DepartmentsComponent implements OnInit {

    errorMessage: string;
    departments: any;
    activeBtnGrid: string = "bg-info";
    activeBtnList: string = "";
    departmentGridView: boolean = true;
    departmentListView: boolean = false;
    roleWrite: boolean = false;
    loading: boolean = true;
    deleteDepartmentID: string;
    deleteDepartmentName: string;
    allBeds: any[] = [];
    allCameras: any[] = [];
    allPatients: any[] = [];
    allDepartments:any[] = [];
    departmentSearchBox: any;

    constructor(private router: Router, private route: ActivatedRoute, private roleServices: RoleServices, private departmentServices: DepartmentServices, private modelMessage: ModelMessage) { }

    clickEdit(id) {
        this.router.navigate(['/department/create', id]);
    }

    clickClosePopup() {
        $('.deleteConfirmPopUp').hide();
    }


    clickDeleteConfirm(id, hName) {
        this.deleteDepartmentID = id;
        this.deleteDepartmentName = hName;
        $('.deleteConfirmPopUp').show();
    }

    checkDepartmentDependency() {
       let isDepartmentMaped = false; 
       this.allPatients.every((currentPatient, index, _ary) => {
            if (currentPatient.icuId === this.deleteDepartmentID) {
                isDepartmentMaped = true;
                return false;
            }
            return true;
       });

       if(!isDepartmentMaped){
           this.allBeds.every((currentBed, index, _ary) => {
            if (currentBed.icuId === this.deleteDepartmentID) {
                isDepartmentMaped = true;
                return false;
            }
            return true;
          });
       }

       if(!isDepartmentMaped){
           this.allCameras.every((currentCamera, index, _ary) => {
            if (currentCamera.icuId === this.deleteDepartmentID) {
                isDepartmentMaped = true;
                return false;
            }
            return true;
          });
       }
       
       return isDepartmentMaped;
    } 

    getDepartmentsList() {
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
         if(hospitalId){
           this.departmentServices.getAllDepartmentsByHospitalID(hospitalId).subscribe(
            data => {
                this.departments = data;
            },
            error => {
                console.log(error);
            }
           );
        }else{
          this.departmentServices.getAllDepartments().subscribe(
            data => {
                this.departments = data;
            },
            error => {
                console.log(error);
            }
          );
        }
    }

    clickDelete() {
        let msg = 'Department ' + this.deleteDepartmentName + ' deleted';
        if(!this.checkDepartmentDependency()){
            this.departmentServices.deleteDepartment(this.deleteDepartmentID).subscribe(
            data => {
                this.getDepartmentsList();
                $('.deleteConfirmPopUp').hide();
                this.modelMessage.successMessage(msg);
            },
            error => {
                $('.deleteConfirmPopUp').hide();
                this.modelMessage.errorMessage(error);
            }
          );
        }else{
          $('.deleteConfirmPopUp').hide();
          let deleteErrMsg = "Unable to delete the Department since there are other resources linked to it. Delete dependent resources and try again";
          this.modelMessage.successMessage(deleteErrMsg);
        }    
    }

     prepareDepartmentData() {
        let data = [];
        this.allDepartments.forEach(currentDepartment => {
                            let departmentDetails = {
                                id: currentDepartment.id,
                                name: currentDepartment.name,
                                type: currentDepartment.type
                                };
                            data.push(departmentDetails);
                        });
                    
        this.departments = data;
        this.loading = false;
    }

    clickDepartmentGridView() {
        this.activeBtnGrid = "bg-info";
        this.activeBtnList = "";
        this.departmentGridView = true;
        this.departmentListView = false;
    }

    clickDepartmentListView() {
        this.activeBtnGrid = "";
        this.activeBtnList = "bg-info";
        this.departmentGridView = false;
        this.departmentListView = true;
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeCreateDepartment() {
        this.router.navigate(['/department/create']);
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('department');
        if (userRole.write) {
            this.roleWrite = true;
        }
        this.allDepartments =  this.route.snapshot.data['departmentList'];
        this.allPatients =  this.route.snapshot.data['patientList'];
        this.allBeds =  this.route.snapshot.data['bedList'];
        this.allCameras =  this.route.snapshot.data['cameraList'];
        this.prepareDepartmentData();

        this.route.params.subscribe(params => {
            if (params['search']) {
                this.departmentSearchBox= params['search'];
            }
        });
    }
}



