import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class UserServices {

    constructor(private commonServices: CommonServices) {}
    
    createUser(formData) {
        return this.commonServices.dataServiceHandler("/user/",'POST',formData);
    }

    updateUser(formData) {
        return this.commonServices.dataServiceHandler("/user/",'PUT',formData);
    }

    getAllUsers() {
        return this.commonServices.dataServiceHandler('/api/user/all','GET');
    }

    getUserInfo(id) {
        return this.commonServices.dataServiceHandler('/api/user/' + id,'GET');
    }

    getAllUsersByHospitalID(id) {
        return this.commonServices.dataServiceHandler('/api/user/all/hospital/' + id,'GET');
    }

    deleteUser(id,userName,fhirUserId) {
        return this.commonServices.dataServiceHandler("/user/"+ id +"/" + userName+ "/"+ fhirUserId,'DELETE');
    }
    
    getTenantInfo(tenantName){
        return this.commonServices.dataServiceHandler("/api/tenant/"+ tenantName,'GET');
    }
}



