import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { HospitalServices } from '../presence/hospital.services';

@Injectable()
export class HospitalListResolver implements Resolve<any> {
    constructor( private hospitalServices:HospitalServices){
     }

    resolve(){
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
            return this.hospitalServices.getHospitalDataByHospitalId(hospitalId)
                   .map(hospital => {
                       let hospitalList = [hospital];
                       return hospitalList;
                    });
        }else{
             return this.hospitalServices.getAllHospitals().map(hospitalList => hospitalList);
        }
    }
}


