import { Injectable, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserServices } from './services/presence/user.services';
import { LoginServices } from './services/presence/login.services';
import { HospitalServices } from './services/presence/hospital.services';
import { CommonServices } from './services/common.services';
// import { Socket } from 'ng-socket-io';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
@Injectable()
export class AppComponent implements OnInit {

  
  private loginView: boolean = true;
  private appView: boolean = false;
  private location: string;

  constructor(private router: Router, private commonServices: CommonServices, private userServices: UserServices, private loginServices: LoginServices, private hospitalServices: HospitalServices) {
    this.location = router.url;
  }

  // getSocket(){
  //   this.socket.on('test',data=>{
  //     console.log(data);
  //   });
  // }

  getLoggedInUSerInfo(){
        this.userServices.getUserInfo(JSON.parse(sessionStorage.getItem('userRoleInfo')).uuid).subscribe(
            data => {
                sessionStorage.setItem('userData', JSON.stringify(data));
                let userData: any = data;
                if(userData.userType === 'CC_ADMIN'){
                   this.userServices.getTenantInfo(sessionStorage.getItem("tenantName")).subscribe(
                       data => {
                             let tenantInfo:any = data;
                             sessionStorage.setItem('fhirOrgId', tenantInfo.fhir_org_logical_id);
                            //  this.router.navigate(['/']);
                            //  location.reload();
                            this.appView = true;
                            this.loginView = false;
                       },
                       error => {
                           this.loginServices.logout();
                        }
                   )
                }else if(userData.userType.startsWith('HOS')){
                     this.hospitalServices.getFhirOrgByHospitalId(userData.hospitalId).subscribe(
                        data => {
                            let orgInfo: any = data;
                            if(orgInfo.id){
                                sessionStorage.setItem('fhirHospitalId', orgInfo.id);
                                // this.router.navigate(['/']);
                                // location.reload();
                                this.appView = true;
                                this.loginView = false;
                            }else{
                               this.loginServices.logout();
                            }
                        },
                        error => {
                            this.loginServices.logout();
                        }
                    );
                }else{
                //   this.router.navigate(['/']);
                //   location.reload();
                     this.appView = true;
                     this.loginView = false;
                }  
            },
            error => {  
                this.loginServices.logout();
            }
        );
    }

  handleLoginEvent(eventData){
      if(eventData === "authenticated"){
         this.getLoggedInUSerInfo();
      }
  }

  ngOnInit() {
      if (JSON.parse(sessionStorage.getItem('userRoleInfo'))) {
            this.appView = true;
            this.loginView = false;
            this.commonServices.prepareReqOptions();
      }
      // this.getSocket();
  }
}



