import { Routes } from '@angular/router';

import { LoginRoutes } from './login/index';
import { DashboardRoutes } from './dashboard/index';
import { PatientsSummaryRoutes } from './patients_summary/index';
import { PatientsDetailsRoutes } from './patients_details/index';
import { PatientCreateRoutes } from './patient_create/index';
import { BedsRoutes } from './beds/index';
import { BedCreateRoutes } from './bed_create/index';
import { CamerasRoutes } from './cameras/index';
import { CameraCreateRoutes } from './camera_create/index';
import { DepartmentsRoutes } from './departments/index';
import { DepartmentCreateRoutes } from './department_create/index';
import { HospitalsRoutes } from './hospitals/index';
import { HospitalCreateRoutes } from './hospital_create/index';
import { ConsultantsRoutes } from './consultants/index';
import { UsersRoutes } from './users/index';
import { UserCreateRoutes } from './user_create/index';


export const routes: Routes = [
    ...LoginRoutes,
    ...DashboardRoutes,
    ...PatientsSummaryRoutes,
    ...PatientsDetailsRoutes,
    ...BedsRoutes,
    ...BedCreateRoutes,
    ...CamerasRoutes,
    ...CameraCreateRoutes,
    ...DepartmentsRoutes,
    ...DepartmentCreateRoutes,
    ...HospitalsRoutes,
    ...HospitalCreateRoutes,
    ...ConsultantsRoutes,
    ...UsersRoutes,
    ...UserCreateRoutes,
    ...PatientCreateRoutes,
];



