import {Injectable, Pipe, PipeTransform} from '@angular/core';
import * as $ from 'jquery';

@Pipe({
  name: 'BedSearch'
})

@Injectable()

export class BedSearch implements PipeTransform {
    transform(value: any, searchTerm: string) 
    {
       let filter = searchTerm.toUpperCase();
       return filter ? value.filter(bed=> bed.name.toUpperCase().indexOf(filter) != -1) : value;
    }
}



