import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { BedInterface } from './bed.interface';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ModelMessage } from '../model_message/model.message';
import { BedServices } from '../services/presence/bed.services';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { PatientServices } from '../services/presence/patient.services';
import { CameraServices } from '../services/presence/camera.services';
import { DeviceServices } from '../services/fhir/devices.services';

@Component({
    moduleId: module.id,
    selector: 'cm-bed-create',
    templateUrl: 'bed.create.component.html',
    styleUrls: ['bed.create.component.css']
})

export class BedCreateComponent implements OnInit {
    public createBedForm: FormGroup;
    id: any;
    private sub: any;
    public submitted: boolean;
    icus: any;
    beds: any;
    public events: any[] = [];
    hospitalList = [];
    departments: any;
    patients: any;
    cameras: any;
    devices: any;
    userData: any;
    bedData: any;
    hospitalName: any;
    mode: string = 'Create';
    createMode: boolean = true;
    selectedCamera: any;
    selectedDevices = [];
    bedDevices = [];
    bedDevice: any;
    disableCreateAction: boolean = false;

    constructor(private router: Router, private route: ActivatedRoute, private fb: FormBuilder, private bedServices: BedServices, private departmentServices: DepartmentServices, private hospitalServices: HospitalServices, private patientServices:PatientServices, private cameraServices: CameraServices, private deviceServices: DeviceServices, private modelMessage: ModelMessage) { }


    save(model: BedInterface, isValid: boolean) {
        this.submitted = true;
        if (model.hospitalId === '') {
            model.hospitalId = this.userData.hospitalId;
        }
        if (isValid) {
            if (!this.createMode) {
                this.disableCreateAction = true;
                if(this.selectedDevices.length > 0){
                    delete this.bedDevice.locationId;
                    if(this.bedDevice.patientId){
                        delete this.bedDevice.patientId;   
                    }
                    this.bedDevice.statusCode = "available";
                    this.bedDevice.statusDisplay = "Device Available";
                    this.selectedDevices.push(this.bedDevice);
                    model.devices = this.selectedDevices;
                }
                this.bedServices.updateBed(model).subscribe(
                    data => {
                        let msg = 'Bed ' + model.name + ' was updated ';
                        this.modelMessage.successMessage(msg);
                        this.router.navigate(['/beds']);
                    },
                    error => {
                        this.disableCreateAction = false;
                        this.modelMessage.errorMessage(error);
                    }
                );
            } else {
                this.disableCreateAction = true;   
                model.devices = this.selectedDevices;
                model.fhirOrgId = sessionStorage.getItem("fhirHospitalId");
                this.bedServices.createBed(model).subscribe(
                    data => {
                        this.selectedDevices = [];
                        let msg = 'Bed ' + model.name + ' was created ';
                        this.modelMessage.successMessage(msg);
                        this.router.navigate(['/beds']);
                    },
                    error => {
                        this.disableCreateAction = false;
                        this.selectedDevices = [];
                        this.modelMessage.errorMessage(error);
                    }
                );
            }
        }
    }

    onChangeDevice(deviceId){
        if(this.createMode){
          let selectedDevice = this.devices.find(device => device.id == deviceId);
          selectedDevice.statusCode = "not-available";
          selectedDevice.statusDisplay = "Device Not Available";
          //this.selectedDevices.push(selectedDevice);
          this.selectedDevices = [selectedDevice];
        }else{
          this.selectedDevices = [];  
          let selectedDevice = this.devices.find(device => device.id == deviceId);
          if(selectedDevice.id !== this.bedDevice.id){
            selectedDevice.statusCode = "not-available";
            selectedDevice.statusDisplay = "Device Not Available";
            selectedDevice.locationId = this.bedDevice.locationId;
            if(this.bedDevice.patientId){
              selectedDevice.patientId = this.bedDevice.patientId;  
            }
            this.selectedDevices = [selectedDevice];
          }
        } 
    }

    onChangeIcu(icuId){
        this.cameraServices.getAllCamerasByDepartmentID(icuId).subscribe(
            data => {
                this.cameras = data;
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
    }

    createBed() {
        this.createBedForm = new FormGroup({
            cameraId: new FormControl('', <any>Validators.required),
            hospitalId: new FormControl(this.userData.hospitalId),
            icuId: new FormControl('', <any>Validators.required),
            id: new FormControl(''),
            name: new FormControl('', <any>Validators.required),
            deviceId: new FormControl(''),
            patientId: new FormControl('')
        });
    }

    editBed(data) {
        this.createBedForm = new FormGroup({
            cameraId: new FormControl(data.cameraId),
            hospitalId: new FormControl(data.hospitalId),
            icuId: new FormControl(data.icuId, <any>Validators.required),
            id: new FormControl(data.id),
            name: new FormControl(data.name),
            deviceId: new FormControl(data.deviceId),
            patientId: new FormControl('')
        });
    }

    clickBedGridView() {
        this.router.navigate(['/beds']);
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeBeds() {
        this.router.navigate(['/beds']);
    }

    ngOnInit() {
        this.userData = JSON.parse(sessionStorage.getItem('userData'));
        this.hospitalList = this.route.snapshot.data['hospitalList'];
        this.hospitalName = this.hospitalList.find(hospital => hospital.id == this.userData.hospitalId).name;
        this.departments = this.route.snapshot.data['departmentList'];
        this.beds = this.route.snapshot.data['bedList'];
        this.devices = this.route.snapshot.data['deviceList'];;
        this.createBed();
        this.sub = this.route.params.subscribe(params => {
            if (params['id']) {
                this.createMode = false;
                this.mode = 'Edit';
                this.bedData = this.beds.find(bed => bed.id == params['id']);
                this.selectedCamera = this.bedData.cameraId;
                this.deviceServices.getAllDevicesByLocationIdentifier(this.bedData.id).subscribe(
                   data => {
                       let responseData:any = data || [];
                        if(responseData.length){
                           this.bedDevices = responseData;
                           this.bedDevice = this.bedDevices[0];
                           this.bedData.deviceId = this.bedDevice.id;
                           this.devices.push(this.bedDevice);
                           this.cameraServices.getAllCamerasByDepartmentID(this.bedData.icuId).subscribe(
                                data => {
                                    this.cameras = data;
                                    this.editBed(this.bedData);
                                },
                                error => {
                                    this.modelMessage.errorMessage(error);
                                }
                            );
                        }else{
                           let errorResponse = {status: 500, statusText: "Failed to get bed devices"};
                           this.modelMessage.errorMessage(errorResponse);
                        }
                    },
                    error => {
                        this.modelMessage.errorMessage(error);
                    }  
                );

                    // this.cameraServices.getAllCamerasByDepartmentID(this.bedData.icuId).subscribe(
                    //     data => {
                    //         this.cameras = data;
                    //         this.editBed(this.bedData);
                    //     },
                    //     error => {
                    //         this.modelMessage.errorMessage(error);
                    //     }
                    // );
            }
        });
    }

    ngOnDestroy() {
        this.sub.unsubscribe();
    }

}


