import { Component, OnInit, Input, Output,EventEmitter } from '@angular/core';
import { DiagnosisServices } from '../services/fhir/diagnosis.services';
import { ModelMessage } from '../model_message/model.message';
import { RoleServices } from '../services/presence/role.services';

@Component({
    moduleId: module.id,
    selector: 'patient-diagnosis',
    templateUrl: 'patient.diagnosis.component.html',
    styleUrls: ['patient.diagnosis.component.css'],
})

export class PatientDiagnosisComponent implements OnInit {

    @Output()
    cancelEvent: EventEmitter<string> = new EventEmitter(); 

    loading: boolean = true;
    entryDiagnosisNote: any;
    diagnosislist:any = [];
    roleWrite: boolean = false;
    errorMessage: string;

    @Input() patientData: any;
    @Input() practitionerData:any;
    @Input() patientName:string;

    constructor(private roleServices: RoleServices, private diagnosisServices: DiagnosisServices, private modelMessage: ModelMessage) { }
    
    clickDiagnosisCancel(){
        this.diagnosislist=null;
        this.cancelEvent.emit();
    }

    clickDiagnosisAdd(){
        if(this.entryDiagnosisNote){
            this.errorMessage = '';
            let diagnosisData = {
            "patientId": this.patientData.id,
            "practitionerId": this.practitionerData.id,
            "note": this.entryDiagnosisNote,
            "name": this.practitionerData.name,
            "encounterId":this.patientData.encounterId
        };
        this.diagnosisServices.createDiagnosis(diagnosisData).subscribe(
            data => {
                this.diagnosislist.unshift(data);
                this.entryDiagnosisNote=null;
            },
            error => {
                this.modelMessage.errorMessage(error);
            });   
        }else{
            this.errorMessage = 'Please Enter the Diagnosis notes';
        }
        
    }

    getDiagnosis(patientId,encounterId) {
        this.diagnosisServices.getDiagnosis(patientId, encounterId).subscribe(
            data => {
                let responseData:any = data;
                if(responseData.length){
                    this.diagnosislist = responseData;
                    this.loading=false;
                }else{
                    if(!this.roleWrite){
                        this.cancelEvent.emit();
                        this.modelMessage.infoMessage("No Diagnosis Notes");
                        this.loading=false;
                    }else{
                        this.loading=false;
                    }
                }
            },
            error => {
                this.loading=false;
                this.cancelEvent.emit();
                this.modelMessage.errorMessage(error);
            }
        );
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write) {
            this.roleWrite = true;
        }
        this.getDiagnosis(this.patientData.id, this.patientData.encounterId);
    }
}


