import { Component, AfterViewInit, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment-timezone';
import { GridOptions } from "ag-grid/main";
import { FlowsheetServices } from "../services/fhir/flowsheet.services";

const observationWidth: number = 150;
const timestampWidth: number = 100;

@Component({
  moduleId: module.id,
  selector: 'patient-labs',
  templateUrl: 'patient.labs.component.html',
  styleUrls: ['patient.labs.component.css'],
})

export class LabsComponent implements OnInit 
{
  @Input() patientData: any;

  columnDefs: any[];
  rowData: any[];
  flowsheetData: any;
  public gridOptions: GridOptions;
  coldefs: any = [];
  startGrid: any;
  newColumnTime: any;

  onCreateColDefs:any = [];
  globalWidth = 67;
  
  constructor(private flowsheetServices: FlowsheetServices) {
      this.gridOptions = <GridOptions>{};
      this.gridOptions.columnDefs = this.createColumnDefs();
  }

  public getNodeChildDetails(rowItem) {
      if (rowItem.maincategory) {
          return {
              group: true,
              expanded: rowItem.open,
              children: rowItem.subcategory,
              field: 'observation',
              key: rowItem.observation
          };
      } else {
          return null;
      }
  }

  private createColumnDefs() {
    let coldefs =  { headerName: "Observation", field: "observation", cellRenderer: "group", pinned: true, minWidth:230, suppressMovable: true }
    this.coldefs.push(coldefs)
    return this.coldefs;
  }

  private onCellValueChanged($event) { }

  onCreateNewColumn()
  {
    let sortedLabsData;
    let newColDef = {
      headerName: this.newColumnTime, 
      field: this.newColumnTime, 
      editable: true, 
      minWidth: this.globalWidth,
      suppressMovable: true
    }
    this.onCreateColDefs.push(newColDef);
    // Sort columns
    if(this.onCreateColDefs.length > 1)
    {
        sortedLabsData = this.onCreateColDefs.sort((first, second)=>{
        // Date will be as per Pagination
        let firstTime:any = moment("2017-08-24T"+first.headerName)
        let secondTime:any = moment("2017-08-24T"+second.headerName)
        return firstTime - secondTime;
      })
    }else{
      sortedLabsData = this.onCreateColDefs;
    }
    this.gridOptions.api.setColumnDefs(sortedLabsData)
    this.gridOptions.rowData = this.flowsheetData[0].observationData;
  }

  onGridReady(params) {
      
  }

  ngOnInit() {
    this.flowsheetServices.getFlowSheetData(this.patientData.id, "labs").subscribe(
      data => {
        this.flowsheetData = data;
        this.gridOptions.rowData = data[0].observationData;
        let observationColDefs = {
          headerName: "Observation", field: "observation", cellRenderer: "group", pinned: true, minWidth:230, suppressMovable: true
        }
        this.onCreateColDefs.push(observationColDefs);
        this.startGrid = true;
      },
      error =>
      {
        console.log(error)
      })
  }

  selectAllRows() {
      this.gridOptions.api.selectAll();
  }
}


