import { Route } from '@angular/router';
import { ConsultantsComponent } from './index';

export const ConsultantsRoutes: Route[] = [
  {
    path: 'consultants',
    component: ConsultantsComponent
  },
  {
    path: 'consultants/:search',
    component: ConsultantsComponent
  }
];



