import { Route } from '@angular/router';
import { UsersComponent } from './index';
import { UserListResolver } from '../services/resolvers/user-resolver.services';

export const UsersRoutes: Route[] = [
    {
        path: 'users',
        component: UsersComponent,
        resolve:{userList:UserListResolver}
    },
    {
        path: 'users/:search',
        component: UsersComponent,
        resolve:{userList:UserListResolver}
    }
];



