import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class DashboardServices {

    constructor(private commonServices: CommonServices) {}

    getDashboard() {
        return  this.commonServices.dataServiceHandler('/api/summaryreport','GET');
    }

    getDashboardByHospital(id) {
        return  this.commonServices.dataServiceHandler('/api/summaryreport/hospital/' + id,'GET');
    }

    getBedSummaryreportByHospital(id) {
        return  this.commonServices.dataServiceHandler('/api/summaryreport/allbeds/hospital/' + id,'GET');
    }
}


