import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ModelMessage } from '../model_message/model.message';
import * as moment from 'moment/moment';

@Component({
  moduleId: module.id,
  selector: 'patient-clinical-document',
  templateUrl: 'patient.clinical.document.component.html',
  styleUrls: ['patient.clinical.document.component.css'],
})

export class ClinicalDocumentComponent implements OnInit {

  currentTab: string = 'history';

  @Input() patientData: any;
  @Input() practitionerData: any;
  @Input() patientName:any;

  constructor(private router: Router, private modelMessage: ModelMessage) { }
  
  changeCurrentTab(tabName: string) {
    this.currentTab = tabName;
  }


  ngOnInit() {}
}



