import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class OrdersServices {

    constructor( private commonServices: CommonServices) {}

    getOrders(patientId,encounterId,tag) {
        return this.commonServices.dataServiceHandler("/fhir/orders/" + patientId + "/" + encounterId + "/" + tag,'GET');
    }

    createOrders(ordersData) {
        return this.commonServices.dataServiceHandler("/fhir/orders", 'POST', ordersData);
    }
}


