import { Route } from '@angular/router';
import { HospitalCreateComponent } from './index';
import { HospitalListResolver } from '../services/resolvers/hospital-resolver.services';

export const HospitalCreateRoutes: Route[] = [
  {
    path: 'hospital/create',
    component: HospitalCreateComponent,
    resolve:{hospitalList:HospitalListResolver}
  },

  {
    path: 'hospital/create/:id',
    component: HospitalCreateComponent,
    resolve:{hospitalList:HospitalListResolver}
  }
];



