import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { Router } from '@angular/router';
import { SpecialInstructionsServices } from '../services/fhir/special.instructions.services';
import { ModelMessage } from '../model_message/model.message';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import * as moment from 'moment/moment';
import { RoleServices } from '../services/presence/role.services';

@Component({
    moduleId: module.id,
    selector: 'patient-special-instructions',
    templateUrl: 'patient.special-instructions.component.html',
    styleUrls: ['patient.special-instructions.component.css'],
})

export class PatientSpecialInstructionsComponent implements OnInit {

    @Output()
    cancelEvent: EventEmitter<string> = new EventEmitter(); 

    loading: boolean = true;
    entrySpecialInstruction: any;
    specialInstructionObservationData:any;
    roleWrite: boolean = false;
    submitted: boolean;
    specialInslist: any = [];
    efEntry: any;
    bpEntrySys: any;
    bpEntryDia: any;
    paEntry: any;
    public createSpecialInstructionForm: FormGroup;
    efValidationError: boolean = true;
    bpSysValidationError: boolean = true;
    bpDiaValidationError: boolean = true;
    paValidationError: boolean = true;


    @Input() patientData: any;
    @Input() practitionerData:any;
    @Input() patientName:string;

    constructor(private router: Router, private roleServices: RoleServices, 
                     private specialInstructionsServices: SpecialInstructionsServices, private modelMessage: ModelMessage, 
                     private fb: FormBuilder) { }


    efValueValidate() {
        if (this.efEntry > 24 && this.efEntry < 76) {
            this.efValidationError = true;
        }
        else {
            this.efValidationError = false;
        }
    }
    bpSysValueValidate() {
        if (this.bpEntrySys > -1 && this.bpEntrySys < 221) {
            this.bpSysValidationError = true;
        }
        else {
            this.bpSysValidationError = false;
        }
    }
    bpDiaValueValidate() {
        if (this.bpEntryDia > -1 && this.bpEntryDia < 101) {
            this.bpDiaValidationError = true;
        }
        else {
            this.bpDiaValidationError = false;
        }
    }
    paValueValidate() {
        if (this.paEntry > -1 && this.paEntry < 51) {
            this.paValidationError = true;
        }
        else {
            this.paValidationError = false;
        }
    }

    createSpecialInstruction() {
        this.createSpecialInstructionForm = new FormGroup
            ({
                name: new FormControl(),
                EF: new FormControl(),
                BPDia: new FormControl(),
                BPSys: new FormControl(),
                PA: new FormControl(),
                Doctor: new FormControl(),
                textArea: new FormControl()
            })
    }

    clickSpecialInsReset() {
        this.createSpecialInstruction();
        this.entrySpecialInstruction="";
        this.efEntry = "";
        this.bpEntrySys = "";
        this.bpEntryDia = "";
        this.paEntry = "";
        this.submitted = false;
        this.efValidationError = true;
        this.bpSysValidationError = true;
        this.bpDiaValidationError = true;
        this.paValidationError = true;
    }

    getSpecialInstructionsVitalsData(patientId,patientAdmittedDate):Promise<any>{
      return new Promise((resolve, reject)=>{
        this.specialInstructionsServices.getSpecialInstructionsVitals(patientId,patientAdmittedDate).subscribe(
            data => {
              resolve(data);
            },
            error => {
                reject(error);
            }
        );
      })
    }

    getSpecialInstructionsNotes(patientId,encounterID):Promise<any> {
      return new Promise((resolve, reject)=>{
        this.specialInstructionsServices.getSpecialInstructionsNotes(patientId,encounterID).subscribe(
            data => {
              resolve(data);
            },
            error => {
              reject(error)
            }
        );
      })
    }

    clickSpecialInstructionsCancel(){
        this.specialInslist=null;
        this.clickSpecialInsReset();
        this.cancelEvent.emit();
    }

    save(isValid) {
      this.submitted = true;
        if (isValid && (this.efValidationError && this.bpDiaValidationError && this.bpSysValidationError && this.paValidationError)) {
             let specialInstructionData = {
                 "patientId": this.patientData.id,
                 "practitionerId": this.practitionerData.id,
                 "note": this.entrySpecialInstruction,
                 "ef": this.efEntry,
                 "bpSys": this.bpEntrySys,
                 "bpDia": this.bpEntryDia,
                 "pa": this.paEntry,
                 "name": this.practitionerData.name,
                 "encounterId": this.patientData.encounterId
             };

            this.specialInstructionsServices.postSpecialInstructionsNote(specialInstructionData).subscribe(
                data => {
                  this.specialInslist.unshift(data);
                  this.clickSpecialInsReset();
                },
                error => {
                    this.modelMessage.errorMessage(error);
                }
            );
        }
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write) {
            this.roleWrite = true;
        }

        let specialInstructionsNotes:any = this.getSpecialInstructionsNotes(this.patientData.id, this.patientData.encounterId);
        let specialInstructionsVital:any = this.getSpecialInstructionsVitalsData(this.patientData.id, this.patientData.encounterId);
        Promise.all([specialInstructionsNotes,specialInstructionsVital]).then(data=>{

          //setting the SI Notes List
          if(data[0]){
               if(data[0].length){
                  this.specialInslist = data[0];
                  this.loading=false;
              }else{
                  if(!this.roleWrite){
                      this.cancelEvent.emit();
                      this.modelMessage.infoMessage("No Special Instructions Notes");
                  }
                   this.loading=false;
              }
          }

          //setting the Vital values to variables
          if(data[1]){
              this.efEntry = data[1].ef;
              this.bpEntrySys = data[1].bpS;
              this.bpEntryDia = data[1].bpD;
              this.paEntry = data[1].pa;
          }

        },err=>{
          this.loading=false;
          this.cancelEvent.emit();
          this.modelMessage.errorMessage(err);
        });

        //create the Form Group object
        this.createSpecialInstruction();
    }
}


