import { Component, AfterViewInit, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment-timezone';
import { GridOptions } from "ag-grid/main";
import { CommonServices } from "../services/common.services";
import { FlowsheetServices } from "../services/fhir/flowsheet.services";

const observationWidth: number = 150;
const timestampWidth: number = 100;

@Component({
    moduleId: module.id,
    selector: 'patient-fluids',
    templateUrl: 'patient.fluids.component.html',
    styleUrls: ['patient.fluids.component.css'],
})

export class FluidsComponent implements OnInit {

    @Input() patientData: any;

    columnDefs: any[];
    rowData: any;
    observationDataIndex: any = 0;
    observedDate: any;
    private colEditable: boolean = true;
    changedObservations: any = [];
    sortedFlowsheetData: any
    inputTotal: any;
    outputTotal: any;
    IOBalance: any;
    initGridData: boolean = true;
    orgTimeZone:any;

    public gridOptions: GridOptions;
    private coldefs: any;
    private flowsheetData: any;

    startGrid: boolean = false;

    constructor(private router: Router, private commonServices: CommonServices, private flowsheetServices: FlowsheetServices) { 
        this.orgTimeZone = this.commonServices.getAppTimeZone();
    }

    pageHandler(pageActition) {
        let length = this.flowsheetData.length;

        if (pageActition == "pageDown" && this.observationDataIndex < length - 1) {
            let processedObservedData = [];
            this.observationDataIndex++;
            let observedDataIntake = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                return subcategoryData.observation === "Intake"
            });
            let observedDataOutput = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                return subcategoryData.observation === "Output (mL)"
            });
            processedObservedData.push(observedDataIntake);
            processedObservedData.push(observedDataOutput);

            this.observedDate = this.sortedFlowsheetData[this.observationDataIndex].observedDate;
            let inputTotal = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                return subcategoryData.observation === "Intake Total"
            });
            this.inputTotal = [inputTotal];

            let outputTotal = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                return subcategoryData.observation === "Output Total"
            });
            this.outputTotal = [outputTotal];

            let ioBalance = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                return subcategoryData.observation === "Balance"
            });
            this.IOBalance = [ioBalance];
            let coloumnDefs = this.createColumnDefs(this.observedDate);
            this.gridOptions.api.setColumnDefs(coloumnDefs)
            this.rowData = processedObservedData;
            this.initGridData = true;
            this.processFluidTotal(this.sortedFlowsheetData[this.observationDataIndex].observationData);
            this.onFloatingBottomCount();
            this.initGridData = false;
        } else
            if (pageActition == "pageUp" && this.observationDataIndex > 0) {
                this.observationDataIndex--;
                let processedObservedData = [];
                let observedDataIntake = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Intake"
                });
                let observedDataOutput = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Output (mL)"
                });
                processedObservedData.push(observedDataIntake);
                processedObservedData.push(observedDataOutput);

                this.observedDate = this.sortedFlowsheetData[this.observationDataIndex].observedDate;
                let inputTotal = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Intake Total"
                });
                this.inputTotal = [inputTotal];

                let outputTotal = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Output Total"
                });
                this.outputTotal = [outputTotal];

                let ioBalance = this.sortedFlowsheetData[this.observationDataIndex].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Balance"
                });
                this.IOBalance = [ioBalance];
                let coloumnDefs = this.createColumnDefs(this.observedDate);
                this.gridOptions.api.setColumnDefs(coloumnDefs)
                this.rowData = processedObservedData;
                this.initGridData = true;
                this.processFluidTotal(this.sortedFlowsheetData[this.observationDataIndex].observationData);
                this.onFloatingBottomCount();
                this.initGridData = false;
            }
    }

    private createColumnDefs(effectiveDateTime) {
        let globalWidth = 67;
        let colDefs = [];
        let addmittedDate = moment(effectiveDateTime).tz(this.orgTimeZone);
        let observedDate = moment(effectiveDateTime).tz(this.orgTimeZone);
        let todayDate = moment().tz(this.orgTimeZone);
        let morningDate = moment(effectiveDateTime).tz(this.orgTimeZone);
        let eveningDate = moment(effectiveDateTime).tz(this.orgTimeZone);
        let nightDate = moment(effectiveDateTime).tz(this.orgTimeZone);
        let isAdmittedDay = true;
        let isMorningShift = false;
        let isEveningShift = false;
        let isNightShift = false;
        todayDate.add(1, 'day');
        colDefs.push({ headerName: "Observation", field: "observation", cellRenderer: "group", pinned: true, suppressMovable: true, minWidth: 200 });
        isMorningShift = true;
        morningDate.subtract(morningDate.seconds(), 'seconds');
        morningDate.subtract(morningDate.minutes(), 'minutes');
        morningDate.subtract(morningDate.hours(), 'hours');
        morningDate.add(8, 'hours');
        if (isAdmittedDay && addmittedDate < morningDate) {
            let previousNightDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            previousNightDate.subtract(previousNightDate.seconds(), 'seconds');
            previousNightDate.subtract(previousNightDate.minutes(), 'minutes');
            previousNightDate.subtract(previousNightDate.hours(), 'hours');

            let previousNightRangeStartDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            let previousNightRangeEndDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            previousNightRangeStartDate.subtract(previousNightRangeStartDate.seconds(), 'seconds');
            previousNightRangeStartDate.subtract(previousNightRangeStartDate.minutes(), 'minutes');
            previousNightRangeStartDate.subtract(previousNightRangeStartDate.hours(), 'hours');
            previousNightRangeEndDate.subtract(previousNightRangeEndDate.seconds(), 'seconds');
            previousNightRangeEndDate.subtract(previousNightRangeEndDate.minutes(), 'minutes');
            previousNightRangeEndDate.subtract(previousNightRangeEndDate.hours(), 'hours');
            previousNightDate.add(30, 'minutes');
            previousNightRangeEndDate.add(30, 'minutes');
            while (previousNightDate.format('HH:mm') !== '07:30') {
                if (addmittedDate >= previousNightRangeStartDate && addmittedDate <= previousNightRangeEndDate) {
                    break;
                }
                previousNightDate.add(30, 'minutes');
                previousNightRangeStartDate.add(30, 'minutes');
                previousNightRangeEndDate.add(30, 'minutes');
            }

            let previousNightColDef = {
                headerName: "Night",
                suppressMovable: true,
                children: []
            };
            let previousNightColData = {
                headerName: previousNightDate.format('HH:mm'),
                newValueHandler: this.checkNewValue,
                suppressMovable: true,
                field: previousNightDate.format(),
                editable: this.colEditable, minWidth: globalWidth
            };
            previousNightColDef.children.push(previousNightColData);
            while (previousNightDate.format('HH:mm') !== '07:30') {
                previousNightColData = {
                    headerName: previousNightDate.add(30, 'minutes').format('HH:mm'),
                    newValueHandler: this.checkNewValue,
                    suppressMovable: true,
                    field: previousNightDate.format(),
                    editable: this.colEditable, minWidth: globalWidth
                };
                previousNightColDef.children.push(previousNightColData);
            }
            colDefs.push(previousNightColDef);
        } else if (isAdmittedDay && addmittedDate > morningDate) {
            isMorningShift = false;
            let morningRangeStartDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            let morningRangeEndDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            morningRangeStartDate.subtract(morningRangeStartDate.seconds(), 'seconds');
            morningRangeStartDate.subtract(morningRangeStartDate.minutes(), 'minutes');
            morningRangeStartDate.subtract(morningRangeStartDate.hours(), 'hours');
            morningRangeEndDate.subtract(morningRangeEndDate.seconds(), 'seconds');
            morningRangeEndDate.subtract(morningRangeEndDate.minutes(), 'minutes');
            morningRangeEndDate.subtract(morningRangeEndDate.hours(), 'hours');
            morningRangeStartDate.add(8, 'hours');
            morningRangeEndDate.add(8, 'hours');
            morningRangeEndDate.add(30, 'minutes');
            morningDate.add(30, 'minutes');
            while (morningDate.format('HH:mm') !== '13:30') {
                if (addmittedDate >= morningRangeStartDate && addmittedDate <= morningRangeEndDate) {
                    isMorningShift = true;
                    break;
                }
                morningDate.add(30, 'minutes');
                morningRangeStartDate.add(30, 'minutes');
                morningRangeEndDate.add(30, 'minutes');
            }
        }
        if (isMorningShift) {
            let morningColDef = {
                headerName: "Morning",
                suppressMovable: true,
                children: []
            };

            let morningColData = {
                headerName: morningDate.format('HH:mm'),
                newValueHandler: this.checkNewValue,
                suppressMovable: true,
                field: morningDate.format(),
                editable: this.colEditable, minWidth: globalWidth
            };
            morningColDef.children.push(morningColData);

            while (morningDate.format('HH:mm') !== '13:30') {
                morningColData = {
                    headerName: morningDate.add(30, 'minutes').format('HH:mm'),
                    newValueHandler: this.checkNewValue,
                    suppressMovable: true,
                    field: morningDate.format(),
                    editable: this.colEditable, minWidth: globalWidth
                };
                morningColDef.children.push(morningColData);
            }
            colDefs.push(morningColDef);
        }

        isEveningShift = true;
        eveningDate.subtract(eveningDate.seconds(), 'seconds');
        eveningDate.subtract(eveningDate.minutes(), 'minutes');
        eveningDate.subtract(eveningDate.hours(), 'hours');
        eveningDate.add(14, 'hours');
        if (isAdmittedDay && addmittedDate > eveningDate) {
            isEveningShift = false;
            let eveningRangeStartDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            let eveningRangeEndDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            eveningRangeStartDate.subtract(eveningRangeStartDate.seconds(), 'seconds');
            eveningRangeStartDate.subtract(eveningRangeStartDate.minutes(), 'minutes');
            eveningRangeStartDate.subtract(eveningRangeStartDate.hours(), 'hours');
            eveningRangeEndDate.subtract(eveningRangeEndDate.seconds(), 'seconds');
            eveningRangeEndDate.subtract(eveningRangeEndDate.minutes(), 'minutes');
            eveningRangeEndDate.subtract(eveningRangeEndDate.hours(), 'hours');
            eveningRangeStartDate.add(14, 'hours');
            eveningRangeEndDate.add(14, 'hours');
            eveningRangeEndDate.add(30, 'minutes');
            eveningDate.add(30, 'minutes');
            while (eveningDate.format('HH:mm') !== '19:30') {
                if (addmittedDate >= eveningRangeStartDate && addmittedDate <= eveningRangeEndDate) {
                    isEveningShift = true;
                    break;
                }
                eveningDate.add(30, 'minutes');
                eveningRangeStartDate.add(30, 'minutes');
                eveningRangeEndDate.add(30, 'minutes');
            }
        }
        if (isEveningShift) {
            let eveningColDef = {
                headerName: "Evening",
                suppressMovable: true,
                children: []
            };
            let eveningColData = {
                headerName: eveningDate.format('HH:mm'),
                newValueHandler: this.checkNewValue,
                suppressMovable: true,
                field: eveningDate.format(),
                editable: this.colEditable, minWidth: globalWidth
            };
            eveningColDef.children.push(eveningColData);

            while (eveningDate.format('HH:mm') !== '19:30') {
                eveningColData = {
                    headerName: eveningDate.add(30, 'minutes').format('HH:mm'),
                    newValueHandler: this.checkNewValue,
                    suppressMovable: true,
                    field: eveningDate.format(),
                    editable: this.colEditable, minWidth: globalWidth
                };
                eveningColDef.children.push(eveningColData);
            }
            colDefs.push(eveningColDef);
        }

        isNightShift = true;
        nightDate.subtract(nightDate.seconds(), 'seconds');
        nightDate.subtract(nightDate.minutes(), 'minutes');
        nightDate.subtract(nightDate.hours(), 'hours');
        nightDate.add(20, 'hours');
        if (isAdmittedDay && addmittedDate > nightDate) {
            isNightShift = false;
            let nightRangeStartDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            let nightRangeEndDate = moment(effectiveDateTime).tz(this.orgTimeZone);
            nightRangeStartDate.subtract(nightRangeStartDate.seconds(), 'seconds');
            nightRangeStartDate.subtract(nightRangeStartDate.minutes(), 'minutes');
            nightRangeStartDate.subtract(nightRangeStartDate.hours(), 'hours');
            nightRangeEndDate.subtract(nightRangeEndDate.seconds(), 'seconds');
            nightRangeEndDate.subtract(nightRangeEndDate.minutes(), 'minutes');
            nightRangeEndDate.subtract(nightRangeEndDate.hours(), 'hours');
            nightRangeStartDate.add(20, 'hours');
            nightRangeEndDate.add(20, 'hours');
            nightRangeEndDate.add(30, 'minutes');
            nightDate.add(30, 'minutes');
            while (nightDate.format('HH:mm') !== '07:30') {
                if (addmittedDate >= nightRangeStartDate && addmittedDate <= nightRangeEndDate) {
                    isNightShift = true;
                    break;
                }
                nightDate.add(30, 'minutes');
                nightRangeStartDate.add(30, 'minutes');
                nightRangeEndDate.add(30, 'minutes');
            }
        }
        if (isNightShift) {
            let nightColDef = {
                headerName: "Night",
                suppressMovable: true,
                children: []
            };
            let nightColData = {
                headerName: nightDate.format('HH:mm'),
                newValueHandler: this.checkNewValue,
                suppressMovable: true,
                field: nightDate.format(),
                editable: this.colEditable, minWidth: globalWidth
            };
            nightColDef.children.push(nightColData);
            while (nightDate.format('HH:mm') !== '07:30') {
                nightColData = {
                    headerName: nightDate.add(30, 'minutes').format('HH:mm'),
                    newValueHandler: this.checkNewValue,
                    suppressMovable: true,
                    field: nightDate.format(),
                    editable: this.colEditable, minWidth: globalWidth
                };
                nightColDef.children.push(nightColData);
            }
            colDefs.push(nightColDef);
        }
        isAdmittedDay = false;

        colDefs.push(
            { headerName: "Morning Total", pinned: "right", field: "morning_total", suppressMovable: true, minWidth: 80 },
            { headerName: "Evening Total", pinned: "right", field: "evening_total", suppressMovable: true, minWidth: 80 },
            { headerName: "Night Total", pinned: "right", field: "night_total", suppressMovable: true, minWidth: 80 },
            { headerName: "Day Total", pinned: "right", field: "day_total", suppressMovable: true, minWidth: 80 },
        )

        this.coldefs = colDefs;
        return this.coldefs;
    }

    checkNewValue(params) {
        let valueAsNumber = params.newValue
        if (isNaN(valueAsNumber) || params.newValue == "") {
            console.log("Invalid value " + params.newValue + ", must be a number");
        } else {
            params.data[params.colDef.field] = valueAsNumber;
        }
    }

    private onCellValueChanged($event) {
        let changedObservations = this.getChangedObservations();
        if (((!$event.oldValue && $event.newValue !== "") || ($event.newValue !== "" && $event.oldValue !== $event.newValue && changedObservations.length == 0)) && ($event.newValue)) {
            let observation: any = {};
            observation.effectiveDataTime = $event.colDef.field;
            observation.value = $event.newValue;
            observation.category = $event.data.category;
            if ($event.data[$event.colDef.field + "-id"]) {
                observation.id = $event.data[$event.colDef.field + "-id"];
            }
            changedObservations.push(observation);
        } else
            if ($event.newValue !== "" && $event.oldValue !== $event.newValue) {
                let observationCategoryId = $event.data.category.subCategory.id;
                let observationIndex = changedObservations.findIndex(observation => observation.effectiveDataTime == $event.colDef.field && observation.category.subCategory.id == observationCategoryId);
                if (observationIndex !== -1 && changedObservations[observationIndex].value !== $event.newValue) {
                    changedObservations[observationIndex].value = $event.newValue;
                } else {
                    let observation: any = {};
                    observation.effectiveDataTime = $event.colDef.field;
                    observation.value = $event.newValue;
                    observation.category = $event.data.category;
                    if ($event.data[$event.colDef.field + "-id"]) {
                        observation.id = $event.data[$event.colDef.field + "-id"];
                    }
                    changedObservations.push(observation);
                }
            }
        this.setChangedObservations(changedObservations);

        if ($event.newValue !== $event.oldValue) {
            this.manualDataChange($event.data, $event.colDef.field, $event)
        }
    }

    setChangedObservations(observations) {
        if (observations.length == 0) {
            this.changedObservations = [];
        } else {
            this.changedObservations = observations;
        }
    }

    getChangedObservations() {
        return this.changedObservations;
    }

    manualDataChange(data, field, $event) {
        let shifts = this.gridOptions.columnDefs;
        let updatedMorningRow = [];
        let updatedEveningRow = [];
        let updatedNightRow = [];
        let newVlaue;
        let shiftTiming;
        if ($event.oldValue) {
            newVlaue = $event.newValue - $event.oldValue
        } else {
            newVlaue = $event.newValue
        }

        let morningShifts = shifts.filter(shift => shift.headerName === "Morning");
        for (let shift in morningShifts) {
            for (let i in morningShifts[shift]["children"]) {
                let shiftField = morningShifts[shift]["children"][i].field;
                if (shiftField == field) {
                    shiftTiming = "Morning";
                    data["morning_total"] = parseInt(data.morning_total) + parseInt(newVlaue);
                    data["day_total"] = data.day_total + parseInt(newVlaue);
                    updatedMorningRow.push($event.node)
                    this.gridOptions.api.refreshRows(updatedMorningRow);
                }
            }
        }

        let eveningShifts = shifts.filter(shift => shift.headerName === "Evening");
        for (let shift in eveningShifts) {
            for (let i in eveningShifts[shift]["children"]) {
                let shiftField = eveningShifts[shift]["children"][i].field;
                if (shiftField == field) {
                    shiftTiming = "Evening";
                    data["evening_total"] = data.evening_total + parseInt(newVlaue);
                    data["day_total"] = data.day_total + parseInt(newVlaue);
                    updatedEveningRow.push($event.node)
                    this.gridOptions.api.refreshRows(updatedEveningRow);
                }
            }
        }

        let nightShifts = shifts.filter(shift => shift.headerName === "Night");
        for (let shift in nightShifts) {
            for (let i in nightShifts[shift]["children"]) {
                let shiftField = nightShifts[shift]["children"][i].field;
                if (shiftField == field) {
                    shiftTiming = "Night";
                    data["night_total"] = data.night_total + parseInt(newVlaue);
                    data["day_total"] = data.day_total + parseInt(newVlaue);
                    updatedNightRow.push($event.node)
                    this.gridOptions.api.refreshRows(updatedNightRow);
                }
            }
        }

        this.manualFloatingRow(newVlaue, $event, shiftTiming)
    }

    manualFloatingRow(newVlaue, $event, shiftTiming) {
        let rows = this.createManualFloatingData(newVlaue, $event, shiftTiming);
        this.gridOptions.api.setFloatingBottomRowData(rows);
    }

    createManualFloatingData(newVlaue, $event, shiftTiming) {
        let result = [];
        let rowKey = this.subCategoryDatahandeler($event.node.parent);
        let field = $event.colDef.field;
        result.push(this.inputTotal[0]);
        result.push(this.outputTotal[0]);
        result.push(this.IOBalance[0]);
        if (rowKey == "Intake") {
            //0 => input, 1 => output, 2 => ioBalance
            if (!isNaN(result[0][field])) {
                // each vertical column total for input total row
                result[0][field] = result[0][field] + parseInt(newVlaue);
            }
            else {
                result[0][field] = parseInt(newVlaue);
            }

            if (!isNaN(result[2][field])) {
                // each vertical column total for io-balance row
                result[2][field] = result[2][field] + parseInt(newVlaue);
            }
            else {
                result[2][field] = parseInt(newVlaue);
            }

            if (shiftTiming == "Morning") {
                result[0]["morning_total"] = result[0]["morning_total"] + parseInt(newVlaue);
                result[0]["day_total"] = result[0]["day_total"] + parseInt(newVlaue);
                result[2]["morning_total"] = result[2]["morning_total"] + parseInt(newVlaue);
                result[2]["day_total"] = result[2]["day_total"] + parseInt(newVlaue);
            }
            else if (shiftTiming == "Evening") {
                result[0]["evening_total"] = result[0]["evening_total"] + parseInt(newVlaue);
                result[0]["day_total"] = result[0]["day_total"] + parseInt(newVlaue);
                result[2]["evening_total"] = result[2]["evening_total"] + parseInt(newVlaue);
                result[2]["day_total"] = result[2]["day_total"] + parseInt(newVlaue);
            }
            else if (shiftTiming == "Night") {
                result[0]["night_total"] = result[0]["night_total"] + parseInt(newVlaue);
                result[0]["day_total"] = result[0]["day_total"] + parseInt(newVlaue);
                result[2]["night_total"] = result[2]["night_total"] + parseInt(newVlaue);
                result[2]["day_total"] = result[2]["day_total"] + parseInt(newVlaue);
            }
        }


        else {
            //Floating output total and io-balance row
            //0 => input, 1 => output, 2 => ioBalance
            if (!isNaN(result[1][field])) {
                // each vertical column total for input total row
                result[1][field] = result[1][field] + parseInt(newVlaue);
            }
            else {
                result[1][field] = parseInt(newVlaue);
            }

            if (!isNaN(result[2][field])) {
                // each vertical column total for input total row
                result[2][field] = result[2][field] - parseInt(newVlaue);
            }
            else {
                result[2][field] = -parseInt(newVlaue);
            }

            if (shiftTiming == "Morning") {
                result[1]["morning_total"] = result[1]["morning_total"] + parseInt(newVlaue);
                result[1]["day_total"] = result[1]["day_total"] + parseInt(newVlaue);
                result[2]["morning_total"] = result[2]["morning_total"] - parseInt(newVlaue);
                result[2]["day_total"] = result[2]["day_total"] - parseInt(newVlaue);
            }
            else if (shiftTiming == "Evening") {
                result[1]["evening_total"] = result[1]["evening_total"] + parseInt(newVlaue);
                result[1]["day_total"] = result[1]["day_total"] + parseInt(newVlaue);
                result[2]["evening_total"] = result[2]["evening_total"] - parseInt(newVlaue);
                result[2]["day_total"] = result[2]["day_total"] - parseInt(newVlaue);
            }
            else if (shiftTiming == "Night") {
                result[1]["night_total"] = result[1]["night_total"] + parseInt(newVlaue);
                result[1]["day_total"] = result[1]["day_total"] + parseInt(newVlaue);
                result[2]["night_total"] = result[2]["night_total"] - parseInt(newVlaue);
                result[2]["day_total"] = result[2]["day_total"] - parseInt(newVlaue);
            }
        }

        let changedObservationsIntake = this.getChangedObservations();
        let intakeValueSet = this.inputTotal[0];
        let intakeObservationCategoryId = intakeValueSet.category.subCategory.id;
        let intakeObservationIndex = changedObservationsIntake.findIndex(observation => observation.effectiveDataTime == $event.colDef.field && observation.category.subCategory.id == intakeObservationCategoryId);
        let intakeTotalValue = result[0][field];
        if (intakeTotalValue) {
            this.changedObservationHandler(intakeObservationIndex, intakeValueSet, changedObservationsIntake, intakeTotalValue, $event);
        }

        let changedObservationsOutput = this.getChangedObservations();
        let outputObservationCategoryId = this.outputTotal[0].category.subCategory.id;
        let outputValueSet = this.outputTotal[0];
        let outputObservationIndex = changedObservationsOutput.findIndex(observation => observation.effectiveDataTime == $event.colDef.field && observation.category.subCategory.id == outputObservationCategoryId);
        let outputTotalValue = result[1][field];
        if (outputTotalValue) {
            this.changedObservationHandler(outputObservationIndex, outputValueSet, changedObservationsOutput, outputTotalValue, $event)
        }

        let changedObservationsIO = this.getChangedObservations();
        let ioBalanceCategoryId = this.IOBalance[0].category.subCategory.id;
        let ioBalanceValueSet = this.IOBalance[0];
        let ioBalanceObservationIndex = changedObservationsIO.findIndex(observation => observation.effectiveDataTime == $event.colDef.field && observation.category.subCategory.id == ioBalanceCategoryId);
        let ioBalanceTotal = result[2][field];
        if (ioBalanceTotal) {
            this.changedObservationHandler(ioBalanceObservationIndex, ioBalanceValueSet, changedObservationsIO, ioBalanceTotal, $event);
        }

        //floating row total for right side pinned column
        return result
    }

    changedObservationHandler(observationIndex, valueSet, changedObservations, updatedValue, $event) {
        if (observationIndex !== -1 && changedObservations[observationIndex].value !== updatedValue) {
            changedObservations[observationIndex].value = updatedValue;
        } else {
            let observation: any = {};
            observation.effectiveDataTime = $event.colDef.field;
            observation.value = updatedValue;
            observation.category = valueSet.category;
            if ($event.data[$event.colDef.field + "-id"]) {
                observation.id = $event.data[$event.colDef.field + "-id"];
            }
            changedObservations.push(observation);
        }
        this.setChangedObservations(changedObservations);
    }

    subCategoryDatahandeler(subCategoryData) {
        if (subCategoryData.key == "Output (mL)") {
            return 'output';
        }
        else if (subCategoryData.key == "Intake") {
            return 'Intake';
        }
        else {
            return this.subCategoryDatahandeler(subCategoryData.parent);
        }
    }

    public getNodeChildDetails(rowItem) {
        if (rowItem && rowItem.maincategory) {
            return {
                group: true,
                expanded: rowItem.open,
                children: rowItem.subcategory,
                field: 'observation',
                key: rowItem.observation
            };
        } else {
            return null;
        }
    }

    processFluidTotal(data) {
        let fluid = data[0].subcategory
        for (let index in fluid) {
            this.handleSubCategoryData(fluid[index].observation, fluid[index].subcategory)
        }
    }

    handleSubCategoryData(mainCategoryName, subCategoryData) {
        for (let index in subCategoryData) {
            if (subCategoryData[index].maincategory == true) {
                this.handleSubCategoryData(mainCategoryName, subCategoryData[index].subcategory)
            }
            else {
                let observationData = subCategoryData[index];
                this.processSubCategoryData(mainCategoryName, observationData)
            }
        }
    }

    processSubCategoryData(mainCategoryName, observationData) {
        if (mainCategoryName == "Intake") {
            this.calculateFluidData(observationData, this.inputTotal)
        }
        if (mainCategoryName == "Output (mL)") {
            this.calculateFluidData(observationData, this.outputTotal)
        }
    }

    calculateFluidData(fluidData, fluidTotalData) {
        let shifts = this.gridOptions.columnDefs;
        let morningTotalData = 0;
        let eveningTotalData = 0;
        let nightTotalData = 0;
        let dayTotal = 0;
        let keys = Object.keys(fluidData);

        //vertical total
        if (!this.initGridData) {
            for (let i = 1; i < keys.length; i++) {
                if (fluidTotalData[0][keys[i]]) {
                    fluidTotalData[0][keys[i]] = parseInt(fluidTotalData[0][keys[i]]) + parseInt(fluidData[keys[i]]);
                }
                else {
                    fluidTotalData[0][keys[i]] = parseInt(fluidData[keys[i]]);
                }
            }
        }

        let morningShift = shifts.filter(shift => shift.headerName === "Morning");
        morningTotalData = this.shiftTotalCalculations(morningShift, fluidData);

        let eveningShift = shifts.filter(shift => shift.headerName === "Evening");
        eveningTotalData = this.shiftTotalCalculations(eveningShift, fluidData);

        let nightShift = shifts.filter(shift => shift.headerName === "Night");
        nightTotalData = this.shiftTotalCalculations(nightShift, fluidData);

        dayTotal = morningTotalData + eveningTotalData + nightTotalData;
        fluidData["morning_total"] = morningTotalData;
        fluidData["evening_total"] = eveningTotalData;
        fluidData["night_total"] = nightTotalData;
        fluidData["day_total"] = dayTotal;
    }

    shiftTotalCalculations(shiftTiming, fluidData) {
        let total = 0;
        for (let shift in shiftTiming) {
            for (let i in shiftTiming[shift]["children"]) {
                let shiftField = shiftTiming[shift]["children"][i].field;
                if (fluidData[shiftField] !== NaN && fluidData[shiftField]) {
                    total = total + parseInt(fluidData[shiftField]);
                }
            }
        }
        return total;
    }

    onFloatingBottomCount() {
        let rows = this.processFloatingRowData();
        this.gridOptions.api.setFloatingBottomRowData(rows);
    }


    processFloatingRowData() {
        let shifts = this.gridOptions.columnDefs;
        let morningTotalData = 0;
        let eveningTotalData = 0;
        let nightTotalData = 0;

        let morningOutputTotal = 0;
        let eveningOutputTotal = 0;
        let nightOutputTotal = 0;

        let morningIOTotal = 0;
        let eveningIOTotal = 0;
        let nightTotalIO = 0;

        let dayTotal = 0;
        let fixedRowsData = [];

        fixedRowsData.push(this.inputTotal[0]);
        fixedRowsData.push(this.outputTotal[0]);
        fixedRowsData.push(this.IOBalance[0]);

        for (let index = 1; index < 4; index++) {
            if (shifts[index].headerName === "Morning") {
                for (let i in shifts[index]["children"]) {
                    let shiftField = shifts[index]["children"][i].field;
                    if (fixedRowsData[0][shiftField]) {
                        //right side fixed column total
                        morningTotalData = morningTotalData + parseInt(fixedRowsData[0][shiftField]);
                    }
                    if (fixedRowsData[1][shiftField]) {
                        morningOutputTotal = morningOutputTotal + parseInt(fixedRowsData[1][shiftField]);
                    }
                    if (fixedRowsData[2][shiftField]) {
                        morningIOTotal = morningIOTotal + parseInt(fixedRowsData[2][shiftField]);
                    }
                }
            }

            if (shifts[index].headerName === "Evening") {
                for (let i in shifts[index]["children"]) {
                    let shiftField = shifts[index]["children"][i].field;
                    if (fixedRowsData[0][shiftField]) {
                        eveningTotalData = eveningTotalData + parseInt(fixedRowsData[0][shiftField]);
                    }
                    if (fixedRowsData[1][shiftField]) {
                        eveningOutputTotal = eveningOutputTotal + parseInt(fixedRowsData[1][shiftField]);
                    }
                    if (fixedRowsData[2][shiftField]) {
                        eveningIOTotal = eveningIOTotal + parseInt(fixedRowsData[2][shiftField]);
                    }
                }
            }

            if (shifts[index].headerName === "Night") {
                for (let i in shifts[index]["children"]) {
                    let shiftField = shifts[index]["children"][i].field;
                    if (fixedRowsData[0][shiftField]) {
                        nightTotalData = nightTotalData + parseInt(fixedRowsData[0][shiftField]);
                    }
                    if (fixedRowsData[1][shiftField]) {
                        nightOutputTotal = nightOutputTotal + parseInt(fixedRowsData[1][shiftField]);
                    }
                    if (fixedRowsData[2][shiftField]) {
                        nightTotalIO = nightTotalIO + parseInt(fixedRowsData[2][shiftField]);
                    }
                }
            }
        }

        // floating row data to plot

        this.inputTotal[0]["morning_total"] = morningTotalData;
        this.inputTotal[0]["evening_total"] = eveningTotalData;
        this.inputTotal[0]["night_total"] = nightTotalData;

        this.outputTotal[0]["morning_total"] = morningOutputTotal;
        this.outputTotal[0]["evening_total"] = eveningOutputTotal;
        this.outputTotal[0]["night_total"] = nightOutputTotal;

        dayTotal = morningTotalData + eveningTotalData + nightTotalData;
        this.inputTotal[0]["day_total"] = dayTotal;

        let outputDayTotal = morningOutputTotal + eveningOutputTotal + nightOutputTotal;
        this.outputTotal[0]["day_total"] = outputDayTotal;

        this.IOBalance[0]["morning_total"] = morningIOTotal;
        this.IOBalance[0]["evening_total"] = eveningIOTotal;
        this.IOBalance[0]["night_total"] = nightTotalIO;
        let ioDayTotal = morningIOTotal + eveningIOTotal + nightTotalIO;
        this.IOBalance[0]["day_total"] = ioDayTotal;
        return fixedRowsData;
    }

    onGridReady(params) {
        this.processFluidTotal(this.sortedFlowsheetData[0].observationData);
        this.onFloatingBottomCount();
        this.initGridData = false;
        params.api.sizeColumnsToFit();
    }

    private saveChangedObservations() {
        let changedObservations = this.getChangedObservations();
        let effectiveDate = moment(this.observedDate,'DD/MM/YYYY h:mm a').tz(this.orgTimeZone).format();
        if (changedObservations.length > 0) {
            this.flowsheetServices.updateFlowsheetData(this.patientData.id, this.patientData.encounterId, "fluids", effectiveDate, changedObservations).subscribe(
                data => {
                },
                error => {
                    console.log(error)
                }
            );
        }
    }

    ngOnInit() {
        this.flowsheetServices.getFlowSheetData(this.patientData.id, "fluids").subscribe(
            data => {
                this.flowsheetData = data;
                let processedObservedData = [];
                if (this.flowsheetData.length > 0) {
                    this.sortedFlowsheetData = this.flowsheetData.sort((first, second) => {
                        let firstDateTime: any = moment(first.observedDate)
                        let secondDateTime: any = moment(second.observedDate)
                        return secondDateTime - firstDateTime;
                    })
                } else {
                    this.sortedFlowsheetData = this.flowsheetData;
                }
                let observedDataIntake = this.sortedFlowsheetData[0].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Intake"
                });
                let observedDataOutput = this.sortedFlowsheetData[0].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Output (mL)"
                });

                let inputTotal = this.sortedFlowsheetData[0].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Intake Total"
                });
                this.inputTotal = [inputTotal];

                let outputTotal = this.sortedFlowsheetData[0].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Output Total"
                });
                this.outputTotal = [outputTotal];

                let ioBalance = this.sortedFlowsheetData[0].observationData[0].subcategory.find((subcategoryData) => {
                    return subcategoryData.observation === "Balance"
                });
                this.IOBalance = [ioBalance];


                processedObservedData.push(observedDataIntake);
                processedObservedData.push(observedDataOutput);
                this.gridOptions = <GridOptions>{};
                this.gridOptions.rowData = processedObservedData
                this.observedDate = this.sortedFlowsheetData[0].observedDate;
                this.gridOptions.columnDefs = this.createColumnDefs(this.observedDate);
                this.startGrid = true;
            },
            error => {
                console.log(error)
            }
        );
    }

    selectAllRows() {
        this.gridOptions.api.selectAll();
    }
}


