import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ModelMessage } from '../model_message/model.message'
import * as moment from 'moment/moment';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { RoleServices } from '../services/presence/role.services';
import { OrdersServices } from '../services/fhir/orders.services';
import { MedicationIdServices } from '../services/fhir/medicationId.services'

@Component({
  moduleId: module.id,
  selector: 'patient-orders',
  templateUrl: 'patient.orders.component.html',
  styleUrls: ['patient.orders.component.css'],
})

export class OrdersComponent implements OnInit {

  tagName: string = 'Medication';
  orderNoteEntry:any;
  medicationList: any = [];
  radiologyList: any = [];
  laboratoryList: any = [];
  roleWrite: boolean = false;
  errorMessage:string;
  medicationId:any;
  encounterId:any;

  @Input() patientData: any;
  @Input() practitioner: any;

  constructor(private router: Router, private ordersServices: OrdersServices, private roleServices: RoleServices, private medicationIdServices: MedicationIdServices, private modelMessage: ModelMessage) { }

  changeCurrentTab(tag: string) {
    this.tagName = tag;
    this.getOrders(this.patientData.id,this.patientData.encounterId,this.tagName);
  }
  createCarePlanAdd(tag){
    if(this.orderNoteEntry){
      let orderData = {
        "patientId" : this.patientData.id,
        "encounterId" : this.patientData.encounterId,
        "note" : this.orderNoteEntry,
        "practitionerId" : this.practitioner.id,
        "practitionerName" : this.practitioner.name,
        "medicationId" : this.medicationId,
        "tag" : tag
      }
      this.ordersServices.createOrders(orderData).subscribe(
        data=>{
          if(tag==='Medication'){
         this.medicationList.unshift(data);
         this.orderNoteEntry = null;
        }else if(tag==='Radiology'){
          this.radiologyList.unshift(data);
          this.orderNoteEntry = null;
        }else if(tag==='Laboratory'){
          this.laboratoryList.unshift(data);
          this.orderNoteEntry = null;
        }
        },
        error => {
          this.modelMessage.errorMessage(error);
        }
      )
    }
  }

  getOrders(patientId,encounterId,tag){
    this.ordersServices.getOrders(patientId,encounterId,tag).subscribe(
      data=>{
        if(tag==='Medication'){
         this.medicationList = data;
        }else if(tag==='Radiology'){
          this.radiologyList = data.filter(o => Object.keys(o).length);
        }else if(tag==='Laboratory'){
          this.laboratoryList = data.filter(o => Object.keys(o).length);
        }
      },
      error=> {
        this.modelMessage.errorMessage(error);
      }
    );
  }

  getMedicationId(orgId){
    this.medicationIdServices.getMedicationId(orgId).subscribe(
      data =>{
      this.medicationId = data.medicationId;
      },
      error=>{
        this.modelMessage.errorMessage(error);
      }
    )
  }

  clickCancel(){
    this.orderNoteEntry = null;
    this.errorMessage = null;
  }

  ngOnInit() {
    let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write){
            this.roleWrite = true;
        }
    this.getMedicationId(this.patientData.orgId);
    this.getOrders(this.patientData.id,this.patientData.encounterId,this.tagName);
  }
}



