import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class MedicationIdServices {

    constructor( private commonServices: CommonServices) {}

    getMedicationId(orgId) {
        return this.commonServices.dataServiceHandler("/fhir/medicationid/" + orgId,'GET');
    }
}


