import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class CommonServices {

    private tenantName: any;
    private headers: any;
    private options: any;
    private getReqHeaders: any;
    private getReqOptions: any;
    appTimeZone:any = "Asia/Calcutta";
    constructor(private http: Http) {}

    //function to find the String is JSON or not
    isJson(str:string) {
        try {
            JSON.parse(str);
        } catch (e) {
            return false;
        }
        return true;
    }

    dataServiceHandler(dataUrl,requestType,requestData?: any) {
            if(requestType === 'GET'){
               return this.http.get(dataUrl, this.getServiceOptions()).map(res =>{
                   let response :any = res;
                   if(response._body  && this.isJson(response._body)){
                       return response.json();
                   }else{
                       return response;
                   }
               });
            }else if(requestType === 'POST'){
                return this.http.post(dataUrl, JSON.stringify(requestData), this.postServiceOptions()).map(res => {
                   let response :any = res;
                   if(response._body && this.isJson(response._body)){
                        return response.json();
                   }else{
                       return response;
                   }
                });
            }else if(requestType === 'PUT'){
                return this.http.put(dataUrl, JSON.stringify(requestData), this.postServiceOptions()).map(res => {
                  let response :any = res;
                  if(response._body  && this.isJson(response._body)){
                      return response.json();
                  }else{
                      return response;
                  }
                });;
            }else if(requestType === 'DELETE'){
                return this.http.delete(dataUrl, this.getServiceOptions()).map(res => {
                  let response :any = res;
                  if(response._body  && this.isJson(response._body)){
                      return response.json();
                  }else{
                      return response;
                  }
                });
            }   
    }

    prepareReqOptions(){
        this.tenantName = sessionStorage.getItem('tenantName');
        this.getReqHeaders = new Headers({'X-TennantID': this.tenantName});
        this.getReqOptions =  new RequestOptions({ headers: this.getReqHeaders });
        this.headers = new Headers({ 'Content-Type': 'application/json', 'charset': 'UTF-8','X-TennantID': this.tenantName});
        this.options = new RequestOptions({ headers: this.headers });
    }

    // get http request options
    getServiceOptions() {
        return this.getReqOptions;
    }

    // post http request options
    postServiceOptions() {
        return this.options;
    }

    getUserTypes(){
        let userTypes= [
                {
                    id: 'CC_ADMIN',
                    name: 'Command Centre Admin'
                }, {
                    id: 'CC_CONSULTANT',
                    name: 'Command Centre Consultant'
                }, {
                    id: 'HOSPITAL_ADMIN',
                    name: 'Hospital Admin'
                }, {
                    id: 'HOSPITAL_DOCTOR',
                    name: 'Hospital Doctor'
                },
                {
                    id: 'HOSPITAL_NURSE',
                    name: 'Hospital Nurse'
                }
            ];

       return userTypes;
    }

    getAppTimeZone(){
        return this.appTimeZone;
    }
}



