import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AgGridModule } from "ag-grid-angular/main";
import { PatientsDetailsComponent } from './patients.details.component';
import { PatientClinicalProgressNoteComponent } from '../patient_clinical_progress_note/index';
import { PatientSummaryComponent } from '../patient_summary/index';
import { PatientFluidSummaryComponent } from '../patient_fluid_summary/index';
import { PatientNursingCareComponent } from '../patient_nursing_care/index';
import { FlowSheetComponent } from '../patient_flow_sheet/index';
import { LabsComponent } from '../patient_labs/index';
import { ChecklistComponent } from '../patient_checklist/index';
import { DischargeSummaryComponent } from '../patient_discharge_summary/index';
import { ClinicalDocumentComponent } from '../patient_clinical_document/index';
import { PatientVitalsGraphsComponent } from '../patient_vitals_graphs/index';
import { PatientVitalsFlowsheetComponent } from '../patient_vitals_flowsheet/index';
import { PatientPlanComponent } from '../patient_plan/index';
import { FluidsComponent } from '../patient_fluids/index';

import { NursingDocumentComponent } from '../patient_nursing_document/index';
import { LabChartComponent } from '../patient_lab_chart/index';
import { OrdersComponent } from '../patient_orders/index';
import { FluidChartComponent } from '../patient_fluid_chart/index';
import { PatientProgressNotesComponent } from '../patient_progress_notes/index';
import { PatientHistoryComponent } from '../patient_history/index';
import { PatientPhysicalExamComponent } from '../patient_physical_exam/index';

import { NoteDisplayComponent } from "../note/index";

// Highcharts
import * as highcharts from 'highcharts';
import { HighchartsStatic } from 'angular2-highcharts/dist/HighchartsService';
import {ChartModule} from "angular2-highcharts";

//socket.io
import { SocketIoModule, SocketIoConfig } from 'ng-socket-io';
const config: SocketIoConfig = { url: 'http://localhost:3001', options: {} };

declare var require: any;

export function highchartsFactory() {
    const sc = require('highcharts/highstock');
    const dd = require('highcharts/modules/drilldown');
    const hcm = require('highcharts/highcharts-more');
    const exp = require('highcharts/modules/exporting');

    dd(sc);
    hcm(sc);
    exp(sc);

    return sc;
}

@NgModule({

    imports: [CommonModule, FormsModule, ReactiveFormsModule,ChartModule, 
                  AgGridModule.withComponents([FluidsComponent]),
                  SocketIoModule.forRoot(config),
                 ],
    providers: [{
        provide: HighchartsStatic,
        useFactory: highchartsFactory
      }],
    declarations: [NoteDisplayComponent,PatientsDetailsComponent, PatientClinicalProgressNoteComponent, 
                         PatientSummaryComponent, PatientFluidSummaryComponent, PatientNursingCareComponent, 
                         FlowSheetComponent, LabsComponent, ChecklistComponent, DischargeSummaryComponent, ClinicalDocumentComponent, 
                         PatientVitalsGraphsComponent, PatientPlanComponent, NursingDocumentComponent, LabChartComponent, OrdersComponent,
                         FluidChartComponent, PatientProgressNotesComponent, PatientHistoryComponent, PatientPhysicalExamComponent,
                         FluidsComponent,PatientVitalsFlowsheetComponent],
    exports: [PatientsDetailsComponent, FormsModule, ReactiveFormsModule,  PatientClinicalProgressNoteComponent, PatientSummaryComponent, 
                  PatientFluidSummaryComponent, PatientNursingCareComponent, FlowSheetComponent, LabsComponent, ChecklistComponent, 
                  DischargeSummaryComponent, ClinicalDocumentComponent, PatientVitalsGraphsComponent, PatientPlanComponent, 
                  NursingDocumentComponent, LabChartComponent, OrdersComponent, FluidChartComponent, PatientProgressNotesComponent, 
                  FluidsComponent, PatientVitalsFlowsheetComponent,PatientHistoryComponent, PatientPhysicalExamComponent]

})

export class PatientsDetailsModule { }



