import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class HospitalServices {
    
    constructor(private commonServices: CommonServices) {}
   
    createHospital(hospitalData,fhirOrgId) {
        return this.commonServices.dataServiceHandler("/presence/hospital/" + fhirOrgId,'POST',hospitalData);
    }

    updateHospital(hospitalData,fhirOrgId) {
       return this.commonServices.dataServiceHandler("/presence/hospital/" + fhirOrgId,'PUT',hospitalData);
    }

    getAllHospitals() {
        return this.commonServices.dataServiceHandler('/api/hospital/all','GET');
    }

    getHospitalDataByHospitalId(hospitalId) {
        return this.commonServices.dataServiceHandler('/api/hospital/' + hospitalId,'GET');
    }

    getFhirOrgByHospitalId(hospitalId) {
        return this.commonServices.dataServiceHandler("/presence/hospital/" + hospitalId,'GET');
    }

    deleteHospital(hospitalId, fhirOrgId) {
        return this.commonServices.dataServiceHandler("/presence/hospital/" + hospitalId + "/org/" + fhirOrgId,'DELETE');
    }
}


