import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class DiagnosisServices {

    constructor( private commonServices: CommonServices) {}

    getDiagnosis(patientId,encounterId) {
        return this.commonServices.dataServiceHandler("/fhir/diagnosis/" + patientId + "/" + encounterId,'GET');
    }

    createDiagnosis(diagnosisData) {
        return this.commonServices.dataServiceHandler("/fhir/diagnosis", 'POST',diagnosisData);
    }
}


