import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()

export class HistoryServices {

    constructor(private commonServices:CommonServices){}

    getClinicalDocumentHistoryForm(patientId,encounterId){
        return this.commonServices.dataServiceHandler("/fhir/clinicalDocumentHistoryForm/" + patientId + "/" + encounterId, 'GET')
    }

    createClinicaldocumentHistoryForm(historyData){
        return this.commonServices.dataServiceHandler("/fhir/clinicalDocumentHistoryForm/", 'POST', historyData)
    }

    updateClinicaldocumentHistoryForm(historyData){
        return this.commonServices.dataServiceHandler("/fhir/clinicalDocumentHistoryForm/",'PUT', historyData);
    }
}



