import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { PractitionerServices } from '../fhir/practitioner.services';

@Injectable()
export class NursesListResolver implements Resolve<any> {
    constructor( private practitionerServices:PractitionerServices){

    }

    resolve(){
        let fhirOrgId = sessionStorage.getItem('fhirHospitalId');
        return this.practitionerServices.getPractitionerByRoleAndOrg('Nurse', fhirOrgId).map(nursesList => nursesList);
    }
}


