import { Component, OnInit, Input } from '@angular/core';
import { CarePlanServices } from '../services/fhir/care.plan.services';
import { ModelMessage } from '../model_message/model.message';
import { RoleServices } from '../services/presence/role.services';

@Component({
  moduleId: module.id,
  selector: 'patient-plan',
  templateUrl: 'patient.plan.component.html',
  styleUrls: ['patient.plan.component.css'],
})

export class PatientPlanComponent implements OnInit {
  
  entryCarePlanNote:any;
  carePlanList:any = [];
  errorMessage:any;
  roleWrite: boolean = false;

  @Input() patientData: any;
  @Input() practitioner: any;
  @Input() patientName:any;
  
  constructor(private roleServices: RoleServices, private carePlanServices: CarePlanServices, private modelMessage: ModelMessage ) { }

  clickCarePlanAdd(){
    if(this.entryCarePlanNote){
      this.errorMessage = '';
      let carePlanData = {
        "patientId" : this.patientData.id,
        "encounterId" : this.patientData.encounterId,
        "note" : this.entryCarePlanNote,
        "practitionerId" : this.practitioner.id,
        "practitionerName" : this.practitioner.name,
        "patientName" : this.patientName.name
      }
    
      this.carePlanServices.createCarePlan(carePlanData).subscribe(
        data => {
          this.carePlanList.unshift(data);
          this.entryCarePlanNote = null;
        },
        error => {
          this.modelMessage.errorMessage(error);
        });
      }else{
        this.errorMessage = 'Please enter care plan notes'
      }
  }

  clickCancel(){
    this.entryCarePlanNote = null;
    this.errorMessage = null;
  }

  getCarePlan(patientId,encounterId){
    this.carePlanServices.getCarePlan(patientId,encounterId).subscribe(
      data=> {
          this.carePlanList = data;
      },
      error=> {
        this.modelMessage.errorMessage(error);
      }
    );
  }

  ngOnInit() {
    let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write) {
            this.roleWrite = true;
        }
    
    this.getCarePlan(this.patientData.id,this.patientData.encounterId);  
  }      
}



