import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DashboardServices } from '../services/presence/dashboard.services';
import { NotesServices } from '../services/presence/notes.services';
import { RoleServices } from '../services/presence/role.services';
import { ModelMessage } from '../model_message/model.message';
import { PatientServices } from '../services/presence/patient.services';

@Component({
    moduleId: module.id,
    selector: 'sd-dashboard',
    templateUrl: 'dashboard.component.html',
    styleUrls: ['dashboard.component.css']
})
export class DashboardComponent {

    countPatients: number;
    countHospitals: number;
    countBeds: number;
    countConsultants: number;
    countCameras: number;
    countIcus: number;
    showPatients: boolean = false;
    showHospitals: boolean = false;
    showBeds: boolean = false;
    showConsultants: boolean = false;
    showCameras: boolean = false;
    showDepartments: boolean = false;
    notes: any;
    userScope: any[];
    roleInfo: any;
    userInfo: any;
    diagnosis: any;
    diagnosislist: any;

    constructor(private router: Router, private patientServices: PatientServices, private dashboardServices: DashboardServices, private notesServices: NotesServices, private roleServices:RoleServices, private modelMessage:ModelMessage) { }

    getDashboardData() {
        let role = JSON.parse(sessionStorage.getItem('userRoleInfo')).role;
        let test = "qe";
        this.roleInfo = this.roleServices.getRoleInfo(role);
        this.showPatients = this.roleInfo.patients;
        // this.showPatients = true;
        this.showHospitals = this.roleInfo.hospital;
        this.showBeds = this.roleInfo.bed;
        // this.showBeds = true;
        this.showConsultants = this.roleInfo.consultant;
        this.showCameras = this.roleInfo.camera;
        // this.showCameras = true;
        this.showDepartments = this.roleInfo.department;
        // this.showDepartments = true;

        if (this.userInfo.hospitalId) {
            this.dashboardServices.getDashboardByHospital(this.userInfo.hospitalId).subscribe(
                data => {
                    let responseData:any = data || {};
                    this.countPatients = responseData.patients;
                    this.countHospitals = responseData.hospitals;
                    this.countIcus = responseData.icus;
                    this.countBeds = responseData.beds;
                    this.countCameras = responseData.cameras;
                    this.countConsultants = responseData.consultants;
                },
                error => {
                    this.modelMessage.errorMessage(error);
                }
            );
        } else {
            this.dashboardServices.getDashboard().subscribe(
                data => {
                    let responseData:any = data || {};
                    this.countPatients = responseData.patients;
                    this.countHospitals = responseData.hospitals;
                    this.countIcus = responseData.icus;
                    this.countBeds = responseData.beds;
                    this.countCameras = responseData.cameras;
                    this.countConsultants = responseData.consultants;
                },
                error => {
                    this.modelMessage.errorMessage(error);
                }
            );
        }
    }

    getAllNotes() {
        this.notesServices.getAllNotes().subscribe(    
            data => this.notes = data,
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
    }

    routePatients() {
        this.router.navigate(['/patients']);
    }

    routeHospitals() {
        this.router.navigate(['/hospitals']);
    }

    routeBeds() {
        this.router.navigate(['/beds']);
    }

    routeConsultants() {
        this.router.navigate(['/consultants']);
    }

    routeCameras() {
        this.router.navigate(['/cameras']);
    }

    routeDepartments() {
        this.router.navigate(['/departments']);
    }

    ngOnInit() {
        this.userScope = JSON.parse(sessionStorage.getItem('userRoleInfo')).role;
        this.userInfo = JSON.parse(sessionStorage.getItem('userData'));
        if (this.userScope) {
            this.getDashboardData();
            this.getAllNotes();
        }
    }
}



