import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class CarePlanServices{

    constructor(private commonServices: CommonServices) {}

    getCarePlan(patientId,encounterId){
        return this.commonServices.dataServiceHandler("fhir/carePlan/" + patientId + "/" + encounterId, 'GET')
    }

    createCarePlan(carePlanData){
        return this.commonServices.dataServiceHandler("fhir/carePlan", 'POST', carePlanData)
    }
}



