import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { nursingCareInterface } from './nursingcare.interface';
import { NursingCareServices } from '../services/fhir/nursingcare.services';
import { ModelMessage } from '../model_message/model.message';
import { RoleServices } from '../services/presence/role.services';

@Component({
  moduleId: module.id,
  selector: 'patient-nursing-care',
  templateUrl: 'patient.nursing.care.component.html',
  styleUrls: ['patient.nursing.care.component.css'],
})

export class PatientNursingCareComponent implements OnInit {

  devicelist: any;
  selectedDevice: any;
  nursingCareData: any;
  nursingCareCreateData: any;
  size: any;
  site: any;
  dateOfInsertion: any;
  plannedDateOfRemoval: any;
  actualDateOfRemoval: any;
  patientId: any;
  catheterType: any;
  list: any;
  edit: boolean = false;
  editedActualDateOfRemoval: any;
  nursingCare: any;
  roleWrite: boolean = false;

  //form error
  sizeHasError: boolean = false;
  siteHasError: boolean = false;
  dateOfInsertionHasError: boolean = false;
  plannedDateOfRemovalHasError: boolean = false;
  actualDateOfRemovalHasError: boolean = false;
  canAdd: boolean = false;

  @Input() patientData: any;
  @Input() practitioner: any;

  constructor(private router: Router, private nursingCareServices: NursingCareServices, private modelMessage: ModelMessage, private roleServices: RoleServices) { }

    addNursingCare(nursingData: nursingCareInterface) {
        let nursingCareCreateData = {
            'patientId' : this.patientData.id,
            'practitionerId' : this.practitioner.id,
            'catheterType' : this.catheterType,
            'size' : this.size,
            'bodySite' : this.site,
            'dateofInsertion' : this.dateOfInsertion,
            'plannedDateOfRemoval': this.plannedDateOfRemoval,
            'actualDateOfRemoval': this.actualDateOfRemoval,
            'encounterId' : this.patientData.encounterId
        }
        this.checkError();
            if(this.canAdd){
                this.nursingCareServices.createNursingCare(nursingCareCreateData).subscribe(
                    data => {
                        this.nursingCare = data;
                        this.getNursingData(this.patientData.id, this.patientData.encounterId);
                        this.plannedDateOfRemoval = "";
                        this.size = "";
                        this.site = "";
                        this.dateOfInsertion = "";
                        this.actualDateOfRemoval = "";
                    },
                    error => {
                        this.modelMessage.errorMessage(error);
                    });
            }else{
                this.modelMessage.infoMessage("Enter all the fields");
            }
        }

    getNursingData(patientId,encounterId){
        this.nursingCareServices.getNursingCare(patientId,encounterId).subscribe(
            data => {
                this.nursingCareData = data;
                this.prepareNursingData();
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
    }
    connectLastNursingData(i){
        this.nursingCareData = this.nursingCareData.concat(i)
    }

    prepareNursingData() {
        let data = [];
        this.nursingCareData.forEach(currentData => {
            let dateofInsertion: any = new Date(currentData.dateofInsertion); 
            let actualDateOfRemoval: any = new Date(currentData.actualDateOfRemoval);
            let diff: any = actualDateOfRemoval - dateofInsertion;
            let dayinUse: any = Math.floor(diff*365 / 31536000000);

            let patientDetails = {
                id : currentData.id,
                catheterType : currentData.catheterType,
                practitionerId: currentData.practitionerId,
                bodySite: currentData.bodySite,
                dateofInsertion : currentData.dateofInsertion,
                plannedDateOfRemoval : currentData.plannedDateOfRemoval,
                actualDateOfRemoval : currentData.actualDateOfRemoval,
                size : currentData.size,
                dayInUse: dayinUse
            };
            data.push(patientDetails);
        });
        this.list = data;
    }

    getDevice(){
        this.nursingCareServices.getNursingDevices().subscribe(
            data => {
                this.devicelist = data;
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
    }
    

    onChangeDevice(deviceName){
        this.catheterType=deviceName;
    }

    clickEdit(){
        this.edit = true;
    }

    update(editableData){
        let editdata = {
            actualDateOfRemoval : this.editedActualDateOfRemoval ,
            bodySite: editableData.bodySite ,
            catheterType: editableData.catheterType ,
            dateofInsertion: editableData.dateofInsertion ,
            id: editableData.id ,
            plannedDateOfRemoval: editableData.plannedDateOfRemoval ,
            practitionerId: editableData.practitionerId ,
            size: editableData.size ,
            patientId: this.patientData.id,
            encounterId: this.patientData.encounterId       
        };

        this.nursingCareServices.updateNursingCare(editableData.id,editdata).subscribe(
            data => {
                let diagnosis = data;
                this.edit=false;
            },
            error => {
                console.log(error);
            }
        );
    }

    updateActualDateOfRemoval(editableData ,removalDate){
        this.editedActualDateOfRemoval = removalDate;
        let editdata = {
            actualDateOfRemoval : this.editedActualDateOfRemoval ,
            bodySite: editableData.bodySite ,
            catheterType: editableData.catheterType ,
            dateofInsertion: editableData.dateofInsertion ,
            id: editableData.id ,
            plannedDateOfRemoval: editableData.plannedDateOfRemoval ,
            practitionerId: editableData.practitionerId ,
            size: editableData.size ,
            patientId: this.patientData.id,
            encounterId: this.patientData.encounterId
        };

        this.nursingCareServices.updateNursingCare(editableData.id,editdata).subscribe(
            data => {
                this.getNursingData(this.patientData.id, this.patientData.encounterId);
            },
            error => {
                console.log(error);
            }
        );
    }

    sizeCheck(size){
        if(size){
             this.sizeHasError=true;
        }else{
             this.sizeHasError=false;
        }
    }

    siteCheck(site){
        if(site){
             this.siteHasError=true;
        }else{
             this.siteHasError=false;
        }
    }

     dateOfInsertionCheck(dateOfInsertion){
        if(dateOfInsertion){
             this.dateOfInsertionHasError = true;
        }else{
             this.dateOfInsertionHasError=false;
        }
    }

    plannedDateOfRemovalCheck(plannedDateOfRemoval){
        if(plannedDateOfRemoval){
             this.plannedDateOfRemovalHasError = true;
        }else{
             this.plannedDateOfRemovalHasError=false;
        }
    }

    actualDateOfRemovalCheck(actualDateOfRemoval){
        if(actualDateOfRemoval){
             this.actualDateOfRemovalHasError = true;
        }else{
             this.actualDateOfRemovalHasError=false;
        }
    }
 
    checkError(){
        if(this.sizeHasError && this.siteHasError && this.dateOfInsertionHasError && this.plannedDateOfRemovalHasError && this.actualDateOfRemovalHasError ){
             this.canAdd = true;
        }else{
             this.canAdd = false;
        }
    }

    ngOnInit() { 
        let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write) {
             this.roleWrite = true;
        }
        this.getDevice();
        this.getNursingData(this.patientData.id, this.patientData.encounterId);  
    }
}


   


