import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class DepartmentServices {
    
    constructor(private commonServices: CommonServices) {}

    createDepartment(formData) {
        return this.commonServices.dataServiceHandler("/api/dept",'POST',formData);
    }

    updateDepartment(formData) {
        return this.commonServices.dataServiceHandler("/api/dept/" + formData.id,'PUT',formData);
    }

    getAllDepartments() {
        return this.commonServices.dataServiceHandler('/api/dept/all','GET');
    }

    getDepartment(id) {
        return this.commonServices.dataServiceHandler('/api/dept/' + id,'GET');
    }

    deleteDepartment(id) {
        return this.commonServices.dataServiceHandler("/api/dept/" + id,'DELETE');
    }

    getAllDepartmentsByHospitalID(id) {
        return this.commonServices.dataServiceHandler('/api/dept/all/hospital/' + id,'GET');
    }
}


