import { Component, OnInit, Input } from '@angular/core';
import { ClinicalProgressNoteServices } from '../services/fhir/clinical.progress.note.services';
import { ModelMessage } from '../model_message/model.message';
import { RoleServices } from '../services/presence/role.services';

@Component({
  moduleId: module.id,
  selector: 'patient-clinical-progress-note',
  templateUrl: 'patient.clinical.progress.note.component.html',
  styleUrls: ['patient.clinical.progress.note.component.css']
})

export class PatientClinicalProgressNoteComponent implements OnInit {

  entryProgressNote:any;
  progressNoteList:any = [];
  errorMessage:any;
  roleWrite: boolean = false;

  @Input() patientData: any;
  @Input() practitioner: any;
  
  constructor(private roleServices: RoleServices, private clinicalProgressNoteServices: ClinicalProgressNoteServices, private modelMessage: ModelMessage ) { }

  clickProgressNoteAdd(){
    if(this.entryProgressNote){
      this.errorMessage = '';
      let progressData = {
        "patientId" : this.patientData.id,
        "encounterId" : this.patientData.encounterId,
        "note" : this.entryProgressNote,
        "practitionerId" : this.practitioner.id,
        "practitionerName" : this.practitioner.name,
      }

      this.clinicalProgressNoteServices.createProgressNote(progressData).subscribe(
        data => {
          this.progressNoteList.unshift(data);
          this.entryProgressNote = null;
        },
        error => {
          this.modelMessage.errorMessage(error);
        });
      }else{
        this.errorMessage = 'Please enter progress notes'
      }
  }

  clickCancel(){
    this.entryProgressNote = null;
    this.errorMessage = null;
  }

  getProgressNote(patientId,encounterId){
    this.clinicalProgressNoteServices.getProgressNote(patientId,encounterId).subscribe(
      data=> {
          this.progressNoteList = data;
      },
      error=> {
        this.modelMessage.errorMessage(error);
      }
    );
  }

  ngOnInit() {
    let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write) {
            this.roleWrite = true;
        }
    
    this.getProgressNote(this.patientData.id,this.patientData.encounterId)   
  }      
}



