import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  moduleId: module.id,
  selector: 'patient-nursing-document',
  templateUrl: 'patient.nursing.component.html',
  styleUrls: ['patient.nursing.component.css']
})

export class NursingDocumentComponent implements OnInit {

  currentTab: string = 'vitals';
  @Input() patientData: any;
  @Input() practitionerData: any;

  constructor(private router: Router) { }


  changeCurrentTab(tabName: string) {
    this.currentTab = tabName;
  }

  ngOnInit() { }
}



