import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { DepartmentServices } from '../presence/department.services';

@Injectable()
export class DepartmentListResolver implements Resolve<any> {
    constructor( private departmentServices:DepartmentServices){

    }

    resolve(){
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
           return this.departmentServices.getAllDepartmentsByHospitalID(hospitalId).map(departmentList => departmentList);  
        }else{
            return this.departmentServices.getAllDepartments().map(departmentList => departmentList);
        }
    }
}


