import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationStart } from '@angular/router';
import { RoleServices } from '../services/presence/role.services';
import { ModelMessage } from '../model_message/model.message';
import { BedServices } from '../services/presence/bed.services';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { PatientServices } from '../services/presence/patient.services';
import { CameraServices } from '../services/presence/camera.services';
import { BedSearch } from '../beds/search';
import * as $ from 'jquery';

@Component({
    moduleId: module.id,
    selector: 'cm-beds',
    templateUrl: 'beds.component.html',
    styleUrls: ['beds.component.css']
})

export class BedsComponent implements OnInit {

    errorMessage: string;
    activeBtnGrid: string = "bg-info";
    activeBtnList: string = "";
    bedGridView: boolean = true;
    bedListView: boolean = false;
    roleWrite: boolean = false;
    loading: boolean = true;
    userData: any;
    userRoleType: any;
    beds: any[] = [];
    allBeds: any;
    allIcus: any[] = [];
    allCameras: any[] = [];
    allPatients: any[] = [];
    allHospitals: any[] = [];
    bedSearchboxInput: any;
    backButton: boolean = false;
    deleteBedID: string;
    deleteBedname: string;

    constructor(private router: Router,private route: ActivatedRoute, private roleServices: RoleServices, private bedServices: BedServices, private departmentServices: DepartmentServices, private hospitalServices: HospitalServices, private patientServices:PatientServices, private cameraServices: CameraServices, private modelMessage: ModelMessage) { }

    prepareBedData() {
        let data = [];
        
        this.allBeds.forEach(currentBed => {
            this.allIcus.forEach(currentIcu => {
                if (currentBed.icuId === currentIcu.id) {
                    this.allCameras.forEach(currentCamera => {
                        if (currentBed.cameraId === currentCamera.id) {
                            this.allHospitals.forEach(currentHospital => {
                                if (currentBed.hospitalId === currentHospital.id) {
                                    let assignedPatientName = null, assignedPatientId = null;
                                    this.allPatients.every((currentPatient, index, _ary) => {
                                        if (currentPatient.bedId === currentBed.id) {
                                            assignedPatientName = currentPatient.name;
                                            assignedPatientId = currentPatient.id;
                                            return false;
                                           
                                        }
                                        return true;
                                    });
                                    let bedDetails = {
                                        id: currentBed.id,
                                        name: currentBed.name,
                                        hospital: currentHospital.name,
                                        icu: currentIcu.name,
                                        camera: currentCamera.name,
                                        patient: assignedPatientName,
                                        patientID: assignedPatientId
                                    };
                                    data.push(bedDetails);
                                };
                            });
                        }
                    });
                }
            });
        });
        this.beds = data;
        this.loading = false;
    }
   
    userCheck() {
        this.backButton = false;
        this.clickBedGridView();  
    }

    clickEdit(id) {
        this.router.navigate(['/bed/create', id]);
    }

    clickDeleteConfirm(id, bName) {
        this.deleteBedID = id;
        this.deleteBedname = bName;
        $('.deleteConfirmPopUp').show();
    }

    checkBedDependency() {
        let isBedOccupied = false;
        this.allPatients.every((currentPatient, index, _ary) => {
            if (currentPatient.bedId === this.deleteBedID) {
                isBedOccupied = true;
                return false;
            }
            return true;
        });
        return isBedOccupied;
    }

    getBeds() {
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
         if(hospitalId){
           this.bedServices.getAllBedsByHospitalID(hospitalId).subscribe(
            data => {
                this.allBeds = data;
                this.prepareBedData();
            },
            error => {
                console.log(error);
            }
           );
        }else{
          this.bedServices.getAllBeds().subscribe(
            data => {
                this.allBeds = data;
                this.prepareBedData();
            },
            error => {
                console.log(error);
            }
          );
        }
    }

    clickDelete() {
        let msg = 'Bed ' + this.deleteBedname + ' deleted';
        if (!this.checkBedDependency()) {
            this.bedServices.deleteBed(this.deleteBedID).subscribe(
                data => {
                    this.userCheck();
                    this.getBeds();
                    $('.deleteConfirmPopUp').hide();
                    this.modelMessage.successMessage(msg);
                },
                error => {
                    $('.deleteConfirmPopUp').hide();
                    this.modelMessage.errorMessage(error);
                }
            );
        } else {
            $('.deleteConfirmPopUp').hide();
            let deleteErrMsg = "Unable to delete the Bed since there are other resources linked to it. Delete dependent resources and try again";
            this.modelMessage.successMessage(deleteErrMsg);
        }

    }

    clickClosePopup() {
        $('.deleteConfirmPopUp').hide();
    }

    clickBedGridView() {
        this.activeBtnGrid = "bg-info";
        this.activeBtnList = "";
        this.bedGridView = true;
        this.bedListView = false;
    }

    clickBedListView() {
        this.activeBtnGrid = "";
        this.activeBtnList = "bg-info";
        this.bedGridView = false;
        this.bedListView = true;
    }

    clickSearch() {
        this.bedGridView = false;
        this.bedListView = false;
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeCreateBed() {
        this.router.navigate(['/bed/create']);
    }

    routPatientDetails(id) {
        this.router.navigate(['/patient/details/', id]);
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('bed');
        if (userRole.write) {
            this.roleWrite = true;
        }
        this.allBeds = this.route.snapshot.data['bedList'];
        this.allIcus = this.route.snapshot.data['departmentList'];
        this.allCameras =  this.route.snapshot.data['cameraList'];
        this.allHospitals = this.route.snapshot.data['hospitalList'];
        let patientsList = this.route.snapshot.data['patientList'];
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
            this.allPatients = patientsList.filter(patient => patient.status === "Admitted");
        }else{
            this.allPatients = patientsList;
        }
        this.userRoleType = JSON.parse(sessionStorage.getItem('userRole'));
        this.userCheck();
        this.prepareBedData();

        this.route.params.subscribe(params => {
            if (params['search']) {
                this.bedSearchboxInput= params['search'];
            }
        });
    }
}



