import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { RoleServices } from '../services/presence/role.services';
import { BedServices } from '../services/presence/bed.services';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { CameraServices } from '../services/presence/camera.services';
import { MeetingServices } from '../services/presence/meeting.services';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { ModelMessage } from '../model_message/model.message';
import {CameraSearch} from '../cameras/search';
import * as $ from 'jquery';

@Component({
    moduleId: module.id,
    selector: 'cameras',
    templateUrl: './cameras.component.html',
    styleUrls: ['cameras.component.css']
})

export class CamerasComponent implements OnInit {

    errorMessage: string;
    allCameras: any;
    activeBtnGrid: string = "bg-info";
    activeBtnList: string = "";
    cameraGridView: boolean = true;
    cameraListView: boolean = false;
    allHospitals: any;
    allDepartments: any;
    cameras: any;
    roleWrite: boolean = false;
    loading: boolean = true;
    deleteCameraname:any;
    deleteCameraID:any;
    allBeds:any;
    cameraSearchbox: string='';

    constructor(private router: Router, private route: ActivatedRoute, private roleServices: RoleServices, private bedServices: BedServices, private cameraServices: CameraServices, private hospitalServices: HospitalServices, private departmentServices: DepartmentServices, private meetingServices: MeetingServices, private modelMessage: ModelMessage) { }
    
    prepareCameraData() {
        let data = [];
        this.allCameras.forEach(currentCamera => {
            this.allHospitals.forEach(currentHospital => {
                if (currentHospital.id === currentCamera.hospitalId) {
                    this.allDepartments.forEach(currentDepartment => {
                        if (currentDepartment.id === currentCamera.icuId) {
                            let cameraDetails = {
                                id: currentCamera.id,
                                name: currentCamera.name,
                                description: currentCamera.description,
                                status: currentCamera.status,
                                hospitalId: currentHospital.hospitalId,
                                hospitalName: currentHospital.name,
                                zoomUserId: currentCamera.zoomUserId,
                                icuId: currentDepartment.icuId,
                                icuName: currentDepartment.name,
                            };
                            data.push(cameraDetails);
                        }
                    });
                }
            });
        });
        this.cameras = data;
        this.loading = false;
    }

    clickEdit(id) {
        this.router.navigate(['/camera/create', id]);
    }

     checkCameraDependency() {
       let isCameraMapped = false; 
       this.allBeds.every((currentBed, index, _ary) => {
            if (currentBed.cameraId === this.deleteCameraID) {
                isCameraMapped = true;
                return false;
            }
            return true;
       });
       return isCameraMapped;
    }    

    clickDeleteConfirm(id, camName) {
        this.deleteCameraID = id;
        this.deleteCameraname = camName;
        $('.deleteConfirmPopUp').show();
    }

    clickClosePopup() {
        $('.deleteConfirmPopUp').hide();
    }

    getCameras() {
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
         if(hospitalId){
           this.cameraServices.getAllCamerasByHospitalID(hospitalId).subscribe(
            data => {
                this.allCameras = data;
                this.prepareCameraData();
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
           );
        }else{
          this.cameraServices.getAllCameras().subscribe(
            data => {
                this.allCameras = data;
                this.prepareCameraData();
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
          );
        }
    }

    clickDelete() {
        let msg = 'Camera ' +this.deleteCameraname +' deleted';
        if(!this.checkCameraDependency())
        {
            $('.deleteConfirmPopUp').hide();
            this.modelMessage.successMessage(msg);
            this.cameraServices.deleteCamera(this.deleteCameraID).subscribe(
                data => {
                   this.getCameras();
                   $('.deleteConfirmPopUp').hide();
                   this.modelMessage.successMessage(msg);
                },
                error => {
                    this.modelMessage.errorMessage(error);
                }
            );
        }
        else{
            $('.deleteConfirmPopUp').hide();
            let deleteErrMsg = "Unable to delete the camera since there are other resources linked to it. Delete dependent resources and try again";
            this.modelMessage.successMessage(deleteErrMsg);
        }
    }

    clickCameraGridView() {
        this.activeBtnGrid = "bg-info";
        this.activeBtnList = "";
        this.cameraGridView = true;
        this.cameraListView = false;
    }

    clickCameraListView() {
        this.activeBtnGrid = "";
        this.activeBtnList = "bg-info";
        this.cameraGridView = false;
        this.cameraListView = true;
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeCreateCamera() {
        this.router.navigate(['/camera/create']);
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('camera');
        if (userRole.write) {
            this.roleWrite = true;
        }
        this.allDepartments = this.route.snapshot.data['departmentList'];
        this.allCameras =  this.route.snapshot.data['cameraList'];
        this.allHospitals = this.route.snapshot.data['hospitalList'];
        this.allBeds = this.route.snapshot.data['bedList'];
        this.prepareCameraData();

        this.route.params.subscribe(params => {
            if (params['search']) {
                this.cameraSearchbox= params['search'];
            }
        });
    }
}



