import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { RoleServices } from '../services/presence/role.services';
import { UserServices } from '../services/presence/user.services';
import { PractitionerServices } from '../services/fhir/practitioner.services';
import { CommonServices } from '../services/common.services';
import { ModelMessage } from '../model_message/model.message';
import * as $ from 'jquery';

@Component({
    moduleId: module.id,
    selector: 'users',
    templateUrl: './users.component.html',
    styleUrls: ['users.component.css']
})

export class UsersComponent implements OnInit {

    userGridView: boolean = true;
    userListView: boolean = false;
    activeBtnGrid: string = "bg-info";
    activeBtnList: string = "";
    roleWrite: boolean = false;
    users: any;
    loggedInUserData: any;
    deleteUserID:any;
    deleteWso2UserId:any;
    deleteFhirUserId:any;
    deleteUsername:any;
    deleteUserData:any;
    userSearchBoxInput: any;


    constructor(private router: Router, private route: ActivatedRoute, private roleServices: RoleServices,private practitionerServices: PractitionerServices, private commonServices: CommonServices, private userServices: UserServices, private modelMessage: ModelMessage) { }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeCreateUser() {
        this.router.navigate(['/user/create']);
    }

    getusers() {
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
           return this.userServices.getAllUsersByHospitalID(hospitalId).subscribe(
                    data => {
                        this.users = data;
                        this.mapUserTypes();
                    },
                    error => {
                        this.modelMessage.errorMessage(error);
                    }
                  );      
        }else{
           return this.userServices.getAllUsers().subscribe(
                    data => {
                        this.users = data;
                        this.mapUserTypes();
                    },
                    error => {
                        this.modelMessage.errorMessage(error);
                    }
                  );         
        } 
    }

    clickGridView() {
        this.activeBtnGrid = "bg-info";
        this.activeBtnList = "";
        this.userGridView = true;
        this.userListView = false;
    }

    clickListView() {
        this.activeBtnGrid = "";
        this.activeBtnList = "bg-info";
        this.userGridView = false;
        this.userListView = true;
    }

    clickEdit(id) 
    { 
        this.router.navigate(['/user/create/', id])
    }

    clickDeleteConfirm(deleteUser) {
        this.deleteUserData = deleteUser;
        $('.deleteConfirmPopUp').show();
    }

    clickClosePopup() {
        $('.deleteConfirmPopUp').hide();
    }

    deleteUser(){
       let msg = 'User ' + this.deleteUserData.firstName + ' ' + this.deleteUserData.lastName +' deleted';
       this.userServices.deleteUser(this.deleteUserData.id,this.deleteUserData.userID,this.deleteFhirUserId).subscribe(
            data => {
                this.getusers();
                $('.deleteConfirmPopUp').hide();
                this.modelMessage.successMessage(msg);
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
    }

    clickDelete() {
        if(this.deleteUserData.userType === "HOSPITAL_DOCTOR" || this.deleteUserData.userType === "HOSPITAL_NURSE"){
            this.practitionerServices.getPractitionerByIdentifier(this.deleteUserData.id).subscribe(
                data => {
                    let responseData:any = data || {}; 
                    if(responseData.id){ 
                        this.deleteFhirUserId = responseData.id;
                        this.deleteUser();
                    }else{
                        let errorResponse = {status: 500, statusText: "Failed to update patient"};
                        this.modelMessage.errorMessage(errorResponse);
                    }
                },
                error => {
                    this.modelMessage.errorMessage(error.json());
                }
            );
        }else{
            this.deleteFhirUserId = "notHospitalUser";
            this.deleteUser();
        }
    }

    disableDelete(userId){
        let loggedInUserId = JSON.parse(sessionStorage.getItem('userData')).id;
        
        if(loggedInUserId==userId){
            return false
        }
        return this.roleWrite;
    }
    
    mapUserTypes(){
        this.users = this.users.map(user => {
            let userTypes: any = this.commonServices.getUserTypes();
            let userType = userTypes.find(userType => userType.id == user.userType);
            if(userType){
                user.userType = userType.name;
            }
            return user;
        });
    }



    ngOnInit() {
        let userRole = this.roleServices.getRole('user');
        if (userRole.write) {
            this.roleWrite = true;
        }
        this.users = this.route.snapshot.data['userList'];
        this.mapUserTypes();
        //this.disableDelete();
        this.route.params.subscribe(params => {
             if (params['search']) {
                 this.userSearchBoxInput = params['search'];
             }
         });
    }
}



