import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class NotesServices {

    constructor(private commonServices: CommonServices) {}

    createNote(formData) {
        return this.commonServices.dataServiceHandler("/api/notes",'POST',formData);
    }

    getAllNotes() {
        return this.commonServices.dataServiceHandler('/api/summaryreport/allnotes','GET');
    }

    getNotes(id) {
        return this.commonServices.dataServiceHandler('/api/notes/all/patient/' + id,'GET');
    }

}




