import { Route } from '@angular/router';
import { DepartmentsComponent } from './index';
import { CameraListResolver } from '../services/resolvers/camera-resolver.services';
import { DepartmentListResolver } from '../services/resolvers/department-resolver.services';
import { PatientListResolver } from '../services/resolvers/patient-resolver.services';
import { BedListResolver } from '../services/resolvers/bed-resolver.services';

export const DepartmentsRoutes: Route[] = [
  {
    path: 'departments',
    component: DepartmentsComponent,
    resolve:{
              departmentList:DepartmentListResolver,
              cameraList:CameraListResolver,
              patientList:PatientListResolver,
              bedList:BedListResolver
            }
  },
  {
    path: 'departments/:search',
    component: DepartmentsComponent,
    resolve:{
              departmentList:DepartmentListResolver,
              cameraList:CameraListResolver,
              patientList:PatientListResolver,
              bedList:BedListResolver
            }
  }
];



