import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { HospitalServices } from '../services/presence/hospital.services';
import { HospitalInterface } from './hospital.interface';
import { ModelMessage } from '../model_message/model.message';

@Component({
    moduleId: module.id,
    selector: 'cm-hospital-create',
    templateUrl: 'hospital.create.component.html',
    styleUrls: ['hospital.create.component.css']
})

export class HospitalCreateComponent implements OnInit {
    public createHospitalForm: FormGroup;
    private hospitalId: any;
    private sub: any;
    hospitals: any;
    hospitalData: any;
    public submitted: boolean;
    public events: any[] = [];
    createMode: boolean = true;
    mode: string = 'Create';
    disableCreateAction: boolean = false;

    constructor(private router: Router, private route: ActivatedRoute, private fb: FormBuilder, private hospitalServices: HospitalServices, private modelMessage: ModelMessage) { }

    routeHospitals() {
        this.router.navigate(['/hospitals']);
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    save(model: HospitalInterface, isValid: boolean) {
        this.submitted = true;
        if (isValid) {
            if (this.createMode) {
                this.disableCreateAction = true;
                this.hospitalServices.createHospital(model,sessionStorage.getItem("fhirOrgId")).subscribe(
                    data => {
                        let msg = 'Hospital ' + model.name + ' was created ';
                        this.modelMessage.successMessage(msg);
                        this.routeHospitals();
                    },
                    error => {
                        this.disableCreateAction = false;
                        let errorResponse = {status: 500, statusText: "Failed to create Hospital"}; 
                        this.modelMessage.errorMessage(errorResponse);
                    }
                );
            } else {
                this.disableCreateAction = true;
                this.hospitalServices.getFhirOrgByHospitalId(this.hospitalId).subscribe(
                   data => {
                       let responseData:any = data || {};
                       if(responseData.id){
                        let fhirOrgId = responseData.id;
                        this.hospitalServices.updateHospital(model,fhirOrgId).subscribe(
                            data => {
                                let msg = 'Hospital ' + model.name + ' was updated ';
                                this.modelMessage.successMessage(msg);
                                this.routeHospitals();
                            },
                            error => {
                               this.disableCreateAction = false;
                               let errorResponse = {status: 500, statusText: "Failed to update Hospital"}; 
                               this.modelMessage.errorMessage(errorResponse);
                            }
                        );
                        
                       }else{
                          this.disableCreateAction = false;
                          let error = {status: 500, statusText: "Failed to update Hospital"}; 
                          this.modelMessage.errorMessage(error);
                       }
                   },
                   error => {
                       this.disableCreateAction = false;
                       let errorResponse = {status: 500, statusText: "Failed to update Hospital"}; 
                       this.modelMessage.errorMessage(errorResponse);
                   }
                );
            }
        }
        
    }

    createHospital() {
        this.createHospitalForm = new FormGroup({
            addressline: new FormControl('', <any>Validators.required),
            city: new FormControl('', <any>Validators.required),
            email: new FormControl('', <any>Validators.required),
            id: new FormControl(''),
            name: new FormControl('', <any>Validators.required),
            phone: new FormControl('', <any>Validators.required),
            pin: new FormControl('', <any>Validators.required),
            state: new FormControl('', <any>Validators.required),
        });
    }

    editHospital(data) {
        this.createHospitalForm = new FormGroup({
            addressline: new FormControl(data.addressline, <any>Validators.required),
            city: new FormControl(data.city, <any>Validators.required),
            email: new FormControl(data.email, <any>Validators.required),
            id: new FormControl(data.id, <any>Validators.required),
            name: new FormControl(data.name),
            phone: new FormControl(data.phone, <any>Validators.required),
            pin: new FormControl(data.pin, <any>Validators.required),
            state: new FormControl(data.state, <any>Validators.required),
        });
    }

    ngOnInit() {
        this.createHospital();
        this.hospitals = this.route.snapshot.data['hospitalList'];
        this.sub = this.route.params.subscribe(params => {
            if (params['id']) {
                this.hospitalId = params['id'];
                this.mode = 'Edit';
                this.createMode = false;
                this.hospitalData = this.hospitals.find(hospital => hospital.id == this.hospitalId);
                this.editHospital(this.hospitalData);
            }
        });
    }

    ngOnDestroy() { }
}



