import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { RoleServices } from '../services/presence/role.services';
import { BedServices } from '../services/presence/bed.services';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { PatientServices } from '../services/presence/patient.services';
import { UserServices } from '../services/presence/user.services';
import { CameraServices } from '../services/presence/camera.services';
import { ModelMessage } from '../model_message/model.message';
import * as $ from 'jquery';

@Component({
    moduleId: module.id,
    selector: 'hospitals',
    templateUrl: './hospitals.component.html',
    styleUrls: ['hospitals.component.css']
})

export class HospitalsComponent implements OnInit {

    errorMessage: string;
    hospitals: any;
    subURL: any = 'hospital/all';
    activeBtnGrid: string = "bg-info";
    activeBtnList: string = "";
    hospitalGridView: boolean = true;
    hospitalListView: boolean = false;
    roleWrite: boolean = false;
    loading: boolean = true;
    deleteHospitalID: string;
    deleteHospitalname: string;
    allBeds: any[] = [];
    allIcus: any[] = [];
    allCameras: any[] = [];
    allPatients: any[] = [];
    allUsers: any[] = [];
    allHospitals: any[] = [];
    hospitalSearchBoxInput: any;

    constructor(private router: Router, private route: ActivatedRoute, private roleServices: RoleServices, private bedServices: BedServices, private departmentServices: DepartmentServices, private hospitalServices: HospitalServices, private patientServices:PatientServices, private userServices: UserServices, private cameraServices: CameraServices, private modelMessage: ModelMessage) { }

    clickEdit(id) {
        this.router.navigate(['/hospital/create', id]);
    }

    clickDeleteConfirm(id,hName) {
        this.deleteHospitalID = id;
        this.deleteHospitalname = hName;
        $('.deleteConfirmPopUp').show();
    }

    checkHospitalDependency() {
       let isHospitalMaped = false; 
       this.allPatients.every((currentPatient, index, _ary) => {
            if (currentPatient.hospitalId === this.deleteHospitalID) {
                isHospitalMaped = true;
                return false;
            }
            return true;
       });

       if(!isHospitalMaped){
           this.allBeds.every((currentBed, index, _ary) => {
            if (currentBed.hospitalId === this.deleteHospitalID) {
                isHospitalMaped = true;
                return false;
            }
            return true;
          });
       }

       if(!isHospitalMaped){
           this.allCameras.every((currentCamera, index, _ary) => {
            if (currentCamera.hospitalId === this.deleteHospitalID) {
                isHospitalMaped = true;
                return false;
            }
            return true;
          });
       }

       if(!isHospitalMaped){
           this.allIcus.every((currentIcu, index, _ary) => {
            if (currentIcu.hospitalId === this.deleteHospitalID) {
                isHospitalMaped = true;
                return false;
            }
            return true;
          });
       }

       if(!isHospitalMaped){
           this.allUsers.every((currentUser, index, _ary) => {
            if (currentUser.hospitalId === this.deleteHospitalID) {
                isHospitalMaped = true;
                return false;
            }
            return true;
          });
       }

       return isHospitalMaped;
       
    } 

    getHospitals() {
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
            return this.hospitalServices.getHospitalDataByHospitalId(hospitalId).subscribe(
                        data => {
                            this.hospitals = data;
                            this.loading = false;
                        },
                        error => {
                            console.log(error);
                            this.loading = false;
                        }
                    );
        }else{
             return this.hospitalServices.getAllHospitals().subscribe(
                        data => {
                            this.hospitals = data;
                            this.loading = false;
                        },
                        error => {
                            console.log(error);
                            this.loading = false;
                        }
                    );
        }
    }

    clickDelete() {
        let msg = 'Hospital ' + this.deleteHospitalname + ' deleted';
        if(!this.checkHospitalDependency()){
            this.hospitalServices.getFhirOrgByHospitalId(this.deleteHospitalID).subscribe(
                data => {
                    let responseData: any = data;
                    if(responseData.id){
                        var fhirOrgId = responseData.id;
                        this.hospitalServices.deleteHospital(this.deleteHospitalID,fhirOrgId).subscribe(
                            data => {
                                this.getHospitals();
                                $('.deleteConfirmPopUp').hide();
                                this.modelMessage.successMessage(msg);
                            },
                            error => {
                                let errorResponse = {status: 500, statusText: "Failed to delete hospital"}; 
                                this.modelMessage.errorMessage(errorResponse);
                            }
                        );
                    }else{
                        let error = {status: 500, statusText: "Failed to delete hospital"}; 
                        this.modelMessage.errorMessage(error);
                    }
                },
                error => {
                    let errorResponse = {status: 500, statusText: "Failed to delete hospital"}; 
                    this.modelMessage.errorMessage(errorResponse);
                }
            );
        }else{
          $('.deleteConfirmPopUp').hide();
          let deleteErrMsg = "Unable to delete the Hospital since there are other resources linked to it. Delete dependent resources and try again";
          this.modelMessage.successMessage(deleteErrMsg);
        }
        
    }

    preapreHospitalData() {
        let data = [];
        this.allHospitals.forEach(currentHospital => 
        {
            let hospitalDetails = {
                                    id: currentHospital.id,
                                    name: currentHospital.name,
                                    addressline: currentHospital.addressline,
                                    city: currentHospital.city,
                                    state: currentHospital.state,
                                    pin: currentHospital.pin,
                                    phone: currentHospital.phone,
                                    email: currentHospital.email,
                                    hospitalId: currentHospital.hospitalId    
                                };
                                data.push(hospitalDetails)
        });
        this.hospitals = data;
        this.loading = false;
    }

    clickHospitalGridView() {
        this.activeBtnGrid = "bg-info";
        this.activeBtnList = "";
        this.hospitalGridView = true;
        this.hospitalListView = false;
    }

    clickHospitalListView() {
        this.activeBtnGrid = "";
        this.activeBtnList = "bg-info";
        this.hospitalGridView = false;
        this.hospitalListView = true;
    }

    clickClosePopup() {
        $('.deleteConfirmPopUp').hide();
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeCreateHospital() {
        this.router.navigate(['/hospital/create']);
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('hospital');
        this.roleWrite = true;
        if (userRole.write) {
            this.roleWrite = true;
        }
        
        this.allHospitals =  this.route.snapshot.data['hospitalList'];
        this.allBeds = this.route.snapshot.data['bedList'];
        this.allIcus = this.route.snapshot.data['departmentList'];
        this.allCameras = this.route.snapshot.data['cameralList'];
        this.allPatients = this.route.snapshot.data['patientList'];
        this.allUsers = this.route.snapshot.data['userList'];
   
        this.preapreHospitalData();
        this.route.params.subscribe(params => {
            if (params['search']) {
                this.hospitalSearchBoxInput= params['search'];
            }
        });
    }
}



