import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { CameraServices } from '../services/presence/camera.services';
import { CameraInterface } from './camera.interface';
import { ModelMessage } from '../model_message/model.message';

@Component({
    moduleId: module.id,
    selector: 'cm-camera-create',
    templateUrl: 'camera.create.component.html',
    styleUrls: ['camera.create.component.css']
})

export class CameraCreateComponent implements OnInit, OnDestroy {

    public createCameraForm: FormGroup;
    public submitted: boolean;
    public events: any[] = [];

    cameras: any;
    id: any;
    private sub: any;
    hospitals: any;
    departments: any;
    cameraData: any;
    userData: any;
    hospitalList =  [];
    hospitalName: any;
    mode: string = 'Create';
    createMode: boolean = true;
    icus: any;
    editPatientData: any;
    departmentName: any;
    departmentId: any;
    acceptableCameraName: boolean = true;
    cameraName: any;
    departmentTextboxInput: any;
    department: any;
    cameraNumber: any;
    hospitalId: any;
    disableCreateAction: boolean = false;

    constructor(private router: Router, private route: ActivatedRoute, private fb: FormBuilder, private cameraServices: CameraServices, private hospitalServices: HospitalServices, private departmentServices: DepartmentServices, private modelMessage: ModelMessage) { }

    camNameCheck(cameraSerialNumber) {
        this.cameraName = this.departmentName + '-CAM-' + cameraSerialNumber;
        let existingCameraName = this.cameras.find(camera => camera.name === this.cameraName);
        if(existingCameraName){
            this.acceptableCameraName = false;
        }else{
            this.acceptableCameraName = true;
        }
    }

    getDepartmentName(departmentId) {
        this.departmentName = this.departments.find(department => department.id == departmentId).name;
    }

    save(model: CameraInterface, isValid: boolean) {
        this.submitted = true;
        if (model.hospitalId === '') {
            model.hospitalId = this.userData.hospitalId;
        }
        if (isValid && this.acceptableCameraName) {
            if (!this.createMode) {
                this.disableCreateAction = true;
                this.cameraServices.updateCamera(model).subscribe(
                    data => {
                        let msg = 'Camera ' + model.name + ' was updated ';
                        this.modelMessage.successMessage(msg);
                        this.router.navigate(['/cameras']);
                    },
                    error => {
                        this.disableCreateAction = false;
                        this.modelMessage.errorMessage(error);
                    }
                );
            } else {
                this.disableCreateAction = true;
                this.cameraServices.createCamera(model).subscribe(
                    data => {
                        let msg = 'Camera ' + model.name + ' was created ';
                        this.modelMessage.successMessage(msg);
                        this.router.navigate(['/cameras']);
                    },
                    error => {
                        this.disableCreateAction = false;
                        this.modelMessage.errorMessage(error);
                    }
                );
            }

        }
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeCameras() {
        this.router.navigate(['/cameras']);
    }

    createCamera() {
        this.createCameraForm = new FormGroup
            ({
                description: new FormControl(),
                hospitalId: new FormControl(this.hospitalId),
                icuId: new FormControl('', <any>Validators.required),
                id: new FormControl(''),
                name: new FormControl('', <any>Validators.required),
                status: new FormControl('ACTIVE'),
                zoomUserId: new FormControl(''),
                camNumber: new FormControl('')

            })
    }

    editCamera(data) {
        this.createCameraForm = new FormGroup
            ({
                description: new FormControl(data.description),
                hospitalId: new FormControl(data.hospitalId),
                icuId: new FormControl(data.icuId, <any>Validators.required),
                id: new FormControl(data.id),
                name: new FormControl(data.name, <any>Validators.required),
                status: new FormControl(data.status),
                zoomUserId: new FormControl(data.zoomUserId),
                camNumber: new FormControl(data.name.substring(data.name.lastIndexOf("-") + 1))
            });
    }

    ngOnInit() {
        this.userData = JSON.parse(sessionStorage.getItem('userData'));
        this.hospitalId = this.userData.hospitalId;
        this.hospitalList = this.route.snapshot.data['hospitalList'];
        this.hospitalName = this.hospitalList.find(hospital => hospital.id == this.userData.hospitalId).name;
        this.cameras =  this.route.snapshot.data['cameraList'];
        this.departments = this.route.snapshot.data['departmentList'];
        this.createCamera();
        this.sub = this.route.params.subscribe(params => {
            if (params['id']) {
                this.createMode = false;
                this.mode = 'Edit';
                this.cameraData = this.cameras.find(camera => camera.id == params['id']);
                this.cameraName = this.cameraData.name;
                this.getDepartmentName(this.cameraData.icuId);
                this.editCamera(this.cameraData);
            }
        });
    }

    ngOnDestroy() { }
}


