import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class ProcedureServices {

    constructor( private commonServices: CommonServices) {}

    createProcedure(procedureData) {
        return this.commonServices.dataServiceHandler("/fhir/procedure", 'POST',procedureData);
    }

     getProcedure(patientId, encounterId)
    {
        return this.commonServices.dataServiceHandler("/fhir/procedure/" + patientId + "/" + encounterId, 'GET');
    }
}


