import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ProgressNotesServices } from '../services/fhir/progress.notes.services';
import { ModelMessage } from '../model_message/model.message'
import * as moment from 'moment/moment';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { RoleServices } from '../services/presence/role.services';

@Component({
  moduleId: module.id,
  selector: 'patient-progress-notes',
  templateUrl: 'patient.progress.notes.component.html',
  styleUrls: ['patient.progress.notes.component.css']
})

export class PatientProgressNotesComponent implements OnInit {
    notesName: string = 'Scores as applicable';
    progressNoteEntry:any;
    progressList: any = [];
    roleWrite: boolean = false;
    errorMessage:string;
    encounterId:any;
    tagCode:any = 'cm_patient_scores';
    allProgressNotes: Array<any> = [];

    @Input() patientData: any;
    @Input() practitionerData: any;
    
    constructor(private router: Router, private roleServices: RoleServices, private progressNotesServices: ProgressNotesServices, private fb: FormBuilder, private modelMessage: ModelMessage) { }

    changeCurrentTab(code:string){
        this.tagCode = code;
        this.progressNoteEntry=null;
        this.setProgressListByCode()
    }

    cancelProgressNote(){
      this.progressNoteEntry=null;
    }

    createProgressNote(code) {
      if(this.progressNoteEntry){
        this.errorMessage = null;
        let progressNoteData = {
          "patientId": this.patientData.id,
          "practitionerId": this.practitionerData.id,
          "note": this.progressNoteEntry,
          "name": this.practitionerData.name,
          "encounterId":this.patientData.encounterId,
          "code": code
        };  
        this.progressNotesServices.createProgressNotes(progressNoteData).subscribe(
            data => {
                this.progressNoteEntry=null;
                this.progressList.unshift(data);
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
      }else{
        this.errorMessage = 'notes is Mandatory'
      }
    }

    setProgressListByCode(){
      let list:any = [];
      list = this.allProgressNotes.filter((note)=>{
        if(note.tagCode === this.tagCode){
           this.notesName = note.tagName;
           return note.progress_notes;
        }
      });
      this.progressList = list[0].progress_notes;
    }

    getProgressNotes(patientId,encounterId){
        this.progressNotesServices.getProgressNotes(patientId,encounterId).subscribe(
            data=>{
                let responseData:any = data;
                this.allProgressNotes = responseData;
                this.setProgressListByCode();
            },
            error=>{
              this.modelMessage.errorMessage(error);
            }
        )
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write){
            this.roleWrite = true;
        }
        this.getProgressNotes(this.patientData.id,this.patientData.encounterId);
    }
}



