import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class PatientServices {

    constructor( private commonServices: CommonServices) {}

    createPatient(patientData,fhirOrgId) {
        return this.commonServices.dataServiceHandler("/presence/patient/" + fhirOrgId,'POST',patientData);
    }

    updatePatient(patientData,patientFhirId, fhirOrgId) {
        return this.commonServices.dataServiceHandler("/presence/patient/" + patientFhirId + "/org/" + fhirOrgId,'PUT',patientData);
    }

    getAllPatients() {
        return this.commonServices.dataServiceHandler('/api/patient/all','GET');
    }

    getPatient(id) {
        return this.commonServices.dataServiceHandler('/api/patient/' + id,'GET');
    }

    getFhirPatientById(patientId) {
        return this.commonServices.dataServiceHandler("/presence/patient/" + patientId,'GET');
    }

    getFhirPatientWithEOCById(patientId) {
        return this.commonServices.dataServiceHandler("/presence/patient/eoc/" + patientId,'GET');
    }

    getAllPatientsByHospital(id) {
        return this.commonServices.dataServiceHandler('/api/patient/all/hospital/' + id,'GET');
    }

}


