import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'HospitalSearch'
})

@Injectable()

export class HospitalSearch implements PipeTransform {
    transform(value: any, searchTerm: string) 
    {
       let filter = searchTerm.toUpperCase();
       return filter ? value.filter(hospital=> hospital.name.toUpperCase().indexOf(filter) != -1) : value;
    }
}



