import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class ProgressNotesServices {

    constructor(private commonServices: CommonServices) {}

    createProgressNotes(progressData){
        return this.commonServices.dataServiceHandler("/fhir/progressNotes/", 'POST', progressData)
    }

    getProgressNotes(patientId,encounterId) {
        return this.commonServices.dataServiceHandler("/fhir/progressNotes/" + patientId + "/" + encounterId + "/" , 'GET');
    }
}   


