import { Route } from '@angular/router';
import { PatientCreateComponent } from './index';
import { HospitalListResolver } from '../services/resolvers/hospital-resolver.services';
import { DepartmentsByHospitalResolver } from '../services/resolvers/departmentsByHospital-resolver.services';
import { PatientsByHospitalResolver } from '../services/resolvers/patientsByHospital-resolver.services';
import { NursesListResolver } from '../services/resolvers/nurses-resolver.services';
import { DoctorsListResolver } from '../services/resolvers/doctors-resolver.services';
import { BedListResolver } from '../services/resolvers/bed-resolver.services';

export const PatientCreateRoutes: Route[] = [
    {
        path: 'patient/create',
        component: PatientCreateComponent,
        resolve: {hospitalList:HospitalListResolver,
                  departmentList:DepartmentsByHospitalResolver,
                  patientList:PatientsByHospitalResolver,
                  doctorsList:DoctorsListResolver,
                  bedList:BedListResolver
                 }
    },
    {
        path: 'patient/create/:id/:mode',
        component: PatientCreateComponent,
        resolve: {hospitalList:HospitalListResolver,
                  departmentList:DepartmentsByHospitalResolver,
                  patientList:PatientsByHospitalResolver,
                  doctorsList:DoctorsListResolver,
                  bedList:BedListResolver
                }
    }
    // , {
    //     path: 'patient/create/:id',
    //     component: PatientCreateComponent,
    //     resolve: {hospitalList:HospitalListResolver,
    //               departmentList:DepartmentsByHospitalResolver,
    //               patientList:PatientsByHospitalResolver,
    //               doctorsList:DoctorsListResolver,
    //               bedList:BedListResolver
    //             }
    // }
];



