import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { BedServices } from '../services/presence/bed.services';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { PatientServices } from '../services/presence/patient.services';
import { PatientInterface } from './patient.interface';
import { ModelMessage } from '../model_message/model.message';
import * as moment from 'moment';
import * as $ from 'jquery';



@Component({
    moduleId: module.id,
    selector: 'cm-patient-create',
    templateUrl: 'patient.create.component.html',
    styleUrls: ['patient.create.component.css']
})

export class PatientCreateComponent implements OnInit, OnDestroy {

    public createPatientForm: FormGroup;
    public submitted: boolean;
    public events: any[] = [];
    hospitals: any;
    icus: any;
    allBeds: any;
    beds: any;
    patients: any;
    doctors: any;
    nurses: any;
    createMode: boolean = true;
    mode: string = 'Create';
    patientId: any;
    patientFhirId : any;
    patientIPN : any;
    userData: any;
    hospitalName: string;
    fhirHospitalId: string;
    private sub: any;
    selectedICU: string;
    selectedBed: string;
    editPatientData: any;
    setDateOfBirth: any;
    tempBed: any;
    currentPatientId:any;
    existingBedId: string;
    dischargedPatientId: string;
    patientFhirData: any;
    todayDate:any;
    disableCreateAction: boolean = false;
    

    constructor(private router: Router, private route: ActivatedRoute, private fb: FormBuilder, private bedServices: BedServices, private departmentServices: DepartmentServices, private hospitalServices: HospitalServices, private patientServices:PatientServices, private modelMessage: ModelMessage) { }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routePatients() {
        this.router.navigate(['/patients']);
    }

    onChangeMrn(patientMrn)
    {
        let dischargedPatient = this.patients.find(patient => patient.mrn == patientMrn && patient.status === "Discharged");
        if (dischargedPatient) {
            this.clickDischargeConfirm();
            this.dischargedPatientId = dischargedPatient.id;
        }
    } 

    clickDischargeConfirm()
    {
        $('.dischargeConfirmPopUp').show();
    }    

    clickClosePopup() {
        $('.dischargeConfirmPopUp').hide();
    }

    clickChangeStatus()
    {
        this.router.navigate(['/patient/create/', this.dischargedPatientId, 'Readmit']);
        $('.dischargeConfirmPopUp').hide();
    }

    onChangeIcu(icuId) {
        let icuBeds = this.allBeds.filter(bed => bed.icuId == icuId);
        let emptyBeds = icuBeds.filter(bed => {
                let occupiedPatient =  this.patients.find(patient => (patient.bedId == bed.id && patient.status !== "Discharged"));
                if(occupiedPatient){
                    return false;
                }else{
                    return true;
                }
        });
        if (this.createMode) {
            this.beds = emptyBeds;
        } else {
            let patientBed = icuBeds.find(bed => bed.id == this.editPatientData.bedId);
            if(patientBed){
                emptyBeds.push(patientBed);
                this.selectedBed = this.editPatientData.bedId;
            }
            this.beds = emptyBeds;
        }
    }

    save(model: PatientInterface, isValid: boolean) {
        let currentDate = moment();
        if (moment(model.dateOfBirth) > currentDate) {
            this.modelMessage.errorMessage({ statusText: 'Date of Birth cannot be future date.' });
        } else {
        this.submitted = true;
        if (model.hospitalId === '' || !model.hospitalId) {
            model.hospitalId = this.userData.hospitalId;
        }

        if (isValid) {
            if (this.createMode) {
                this.disableCreateAction = true;
                this.patientServices.createPatient(model,this.fhirHospitalId).subscribe(
                    data => {
                        let msg = 'Patient ' + model.name + ' was created ';
                        this.modelMessage.successMessage(msg);
                        this.routePatients();
                    },
                    
                    error => {
                        this.disableCreateAction=false;
                        let errorResponse = {status: 500, statusText: "Failed to create patient"};
                        this.modelMessage.errorMessage(errorResponse);
                    }  
               );  
            } 
            
            else {
                if (this.mode === "Readmit") {
                  model.episodeOfCareId = this.patientFhirData.episodeOfCareId;  
                }else{
                  if(model.bedId != this.existingBedId){
                     model.currentBedId = this.existingBedId;
                  }
                  model.encounterId = this.patientFhirData.encounterId;
                }
                this.disableCreateAction = true;
                this.patientServices.updatePatient(model,this.patientFhirId,this.fhirHospitalId).subscribe(
                    data => {
                        let msg = 'Patient ' + model.name + ' was updated ';
                        this.modelMessage.successMessage(msg);
                        this.routePatients();
                        
                    },
                    
                    error => {
                        this.disableCreateAction = false;
                        this.modelMessage.errorMessage(error);
                    }
                );
            }
        }
        
        }
    }

    createPatient() {
        this.createPatientForm = new FormGroup({
            name: new FormControl('',<any>Validators.required),
            gender: new FormControl('Male'),
            dateOfBirth: new FormControl(''),
            phoneNumber: new FormControl(''),
            maritialStatus: new FormControl('Single'),
            addressline: new FormControl(''),
            city: new FormControl(''),
            pin: new FormControl(''),
            hospitalId: new FormControl(this.userData.hospitalId),
            icuId: new FormControl(''),
            bedId: new FormControl(''),
            mrn: new FormControl(''),
            status: new FormControl('Admitted'),
            ipno: new FormControl(''),
            emailId: new FormControl(''),
            state: new FormControl('')

        });
    }

    editPatient(data) {
        this.createPatientForm = new FormGroup({
            name: new FormControl(data.name),
            gender: new FormControl(data.gender),
            dateOfBirth: new FormControl(moment(data.dateOfBirth).format('YYYY-MM-DD')),
            phoneNumber: new FormControl(data.phoneNumber, <any>Validators.required),
            maritialStatus: new FormControl(data.maritialStatus),
            addressline: new FormControl(data.addressline, <any>Validators.required),
            city: new FormControl(data.city, <any>Validators.required),
            pin: new FormControl(data.pin, <any>Validators.required),
            hospitalId: new FormControl(data.hospitalId),
            icuId: new FormControl(data.icuId, <any>Validators.required),
            bedId: new FormControl(data.bedId, <any>Validators.required),
            mrn: new FormControl(data.mrn, <any>Validators.required),
            status: new FormControl('Admitted'),
            id: new FormControl(data.id),
            lastSeen: new FormControl(data.lastSeen),
            ipno: new FormControl(this.patientIPN),
            emailId: new FormControl(data.emailId),
            state: new FormControl(data.state)
        });
    }

    readmitPatient(data) {
        this.createPatientForm = new FormGroup({
            name: new FormControl(data.name),
            gender: new FormControl(data.gender),
            dateOfBirth: new FormControl(moment(data.dateOfBirth).format('YYYY-MM-DD')),
            phoneNumber: new FormControl(data.phoneNumber, <any>Validators.required),
            maritialStatus: new FormControl(data.maritialStatus),
            addressline: new FormControl(data.addressline, <any>Validators.required),
            city: new FormControl(data.city, <any>Validators.required),
            pin: new FormControl(data.pin, <any>Validators.required),
            hospitalId: new FormControl(data.hospitalId),
            icuId: new FormControl(''),
            bedId: new FormControl(''),
            mrn: new FormControl(data.mrn, <any>Validators.required),
            status: new FormControl('Admitted'),
            id: new FormControl(data.id),
            state: new FormControl(data.state),
            lastSeen: new FormControl(data.lastSeen),
            ipno: new FormControl(''),
            emailId: new FormControl(data.emailId)
        });
    }

    ngOnInit() {
        let currentDate = moment();
        this.todayDate = currentDate.format('MM/DD/YYYY');
        this.userData = JSON.parse(sessionStorage.getItem('userData'));
        this.fhirHospitalId = sessionStorage.getItem('fhirHospitalId');
        this.hospitals = this.route.snapshot.data['hospitalList'];
        this.hospitalName = this.hospitals.find(hospital => hospital.id == this.userData.hospitalId).name;
        this.icus = this.route.snapshot.data['departmentList']; 
        this.patients = this.route.snapshot.data['patientList'];
        this.doctors = this.route.snapshot.data['doctorsList'];
        this.allBeds = this.route.snapshot.data['bedList'];
        this.createPatient();

        this.sub = this.route.params.subscribe(params => {
            if (params['id']) {
                this.patientId = params['id'];
                this.createMode = false;
                this.mode = params['mode'];
                this.patientServices.getPatient(this.patientId).subscribe(
                    data => {
                        let patientData = data;
                        if (this.mode === "Readmit") {
                            this.patientServices.getFhirPatientWithEOCById(this.patientId).subscribe(
                                data => {
                                    let responseData:any = data || {};
                                    if (responseData.id) {
                                       this.patientFhirData = responseData;
                                       this.patientFhirId = responseData.id;
                                       this.editPatientData = patientData;
                                       this.selectedICU = "";
                                       this.readmitPatient(this.editPatientData);
                                    }else{
                                       let errorResponse = { status: 500, statusText: "Failed to get patient data" };
                                       this.modelMessage.errorMessage(errorResponse);
                                    }
                                },
                                error => {
                                    let errorResponse = { status: 500, statusText: "Failed to get patient data" };
                                    this.modelMessage.errorMessage(errorResponse);
                                }
                            );
                        }else{
                            this.patientServices.getFhirPatientById(this.patientId).subscribe(
                            data => {
                                let responseData:any = data || {};
                                if(responseData.id){
                                    this.patientFhirData = responseData;
                                    this.patientFhirId = responseData.id;
                                    this.patientIPN = responseData.ipn;
                                    this.editPatientData = patientData;
                                    this.selectedICU = this.editPatientData.icuId;
                                    let icuBeds = this.allBeds.filter(bed => bed.icuId == this.editPatientData.icuId);
                                    let emptyBeds = icuBeds.filter(bed => {
                                        let occupiedPatient =  this.patients.find(patient => patient.bedId == bed.id);
                                        if(occupiedPatient){
                                            return false;
                                        }else{
                                            return true;
                                        }
                                    });
                                    let patientBed = icuBeds.find(bed => bed.id == this.editPatientData.bedId);
                                    emptyBeds.push(patientBed);
                                    this.beds = emptyBeds;
                                    this.existingBedId = this.editPatientData.bedId;
                                    this.selectedBed = this.editPatientData.bedId;
                                    this.editPatient(this.editPatientData);
                                }else{
                                    let errorResponse = {status: 500, statusText: "Failed to get patient data"};
                                    this.modelMessage.errorMessage(errorResponse);
                                } 
                            },
                            error => {
                                let errorResponse = {status: 500, statusText: "Failed to get patient data"};
                                this.modelMessage.errorMessage(errorResponse);
                            }
                          );
                       }
                    },
                    error => {
                        let errorResponse = {status: 500, statusText: "Failed to get patient data"};
                        this.modelMessage.errorMessage(errorResponse);
                    }
                );
                    
            }
        }); 
    }

    ngOnDestroy() { }
}



