import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'ConsultantSearch'
})

@Injectable()

export class ConsultantSearch implements PipeTransform {
    transform(value: any, searchTerm: string) 
    {
       let filter = searchTerm.toUpperCase();
       return filter ? value.filter(consultant=> consultant.firstName.toUpperCase().indexOf(filter) != -1) : value;
    }
}



