import { Route } from '@angular/router';
import { PatientsSummaryComponent } from './index';

export const PatientsSummaryRoutes: Route[] = [
  {
    path: 'patients',
    component: PatientsSummaryComponent
  },
  {
    path: 'patients/:search',
    component: PatientsSummaryComponent
  }
];



