import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { RoleServices } from '../services/presence/role.services';

@Component({
    moduleId: module.id,
    selector: 'app-navbar',
    templateUrl: 'navbar.component.html',
    styleUrls: ['navbar.component.css'],
})

export class NavbarComponent implements OnInit {

    navPatients: boolean = false;
    navHospitals: boolean = false;
    navBeds: boolean = false;
    navConsultants: boolean = false;
    navCameras: boolean = false;
    navDepartments: boolean = false;
    navAdmin: boolean = false;
    userName: string;
    roleInfo: any;
    userData: any;
    navbarSearchInput: any;
    selectedSearchOption: any;

    constructor(private router: Router, private roleServices: RoleServices) { }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routePatients() {
        this.router.navigate(['/patients']);
    }

    routeHospitals() {
        this.router.navigate(['/hospitals']);
    }

    routeBeds() {
        this.router.navigate(['/beds']);
    }

    routeConsultants() {
        this.router.navigate(['/consultants']);
    }

    routeCameras() {
        this.router.navigate(['/cameras']);
    }

    routeDepartments() {
        this.router.navigate(['/departments']);
    }

    routeUsers() {
        this.router.navigate(['/users']);
    }

    onChangeSearchOption(selectedSearchOption)
    {
        if(this.navbarSearchInput=="" || this.navbarSearchInput==undefined)
        {
            this.router.navigate(['/']);
        }else{
            this.selectedSearchOption = selectedSearchOption;
            switch(selectedSearchOption)
                {
                    case "Bed" :
                        this.router.navigate(['/beds',this.navbarSearchInput]);
                        break;

                    case "Patient" :
                        this.router.navigate(['/patients',this.navbarSearchInput]);
                        break;

                    case "Camera" :
                        this.router.navigate(['/cameras',this.navbarSearchInput]);
                        break;

                    case "Hospital" :
                        this.router.navigate(['/hospitals',this.navbarSearchInput]);
                        break;

                    case "Consultant" :
                        this.router.navigate(['/consultants',this.navbarSearchInput]);
                        break;

                    case "Department" :
                        this.router.navigate(['/departments',this.navbarSearchInput]);
                        break;
                    
                    case "User" :
                        this.router.navigate(['/users',this.navbarSearchInput]);
                        break;
                    
                    case "" :
                        this.router.navigate(['/']);
                        break;

                    default:
                        this.router.navigate(['/patients',this.navbarSearchInput]);
                }
        }
    }

    search()
    {
        if(this.navbarSearchInput=="" || this.navbarSearchInput==undefined)
        {
            this.router.navigate(['/']);
        }else
        {
            this.onChangeSearchOption(this.selectedSearchOption)
        }
    }

    ngOnInit() {
        if(sessionStorage.getItem('userData')){
            this.userData = JSON.parse(sessionStorage.getItem('userData'));
            this.userName = this.userData.firstName + ' ' + this.userData.lastName;
            let role = JSON.parse(sessionStorage.getItem('userRoleInfo')).role;
            this.roleInfo = this.roleServices.getRoleInfo(role);
            this.navPatients = this.roleInfo.patients;
            this.navHospitals = this.roleInfo.hospital;
            this.navBeds = this.roleInfo.bed;
            this.navConsultants = this.roleInfo.consultant;
            this.navCameras = this.roleInfo.camera;
            this.navDepartments = this.roleInfo.department;
            if (role.indexOf('ADMIN') > -1) {
                this.navAdmin = true;
            }
        }
    }
}



