import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { UserServices } from '../services/presence/user.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { PractitionerServices } from '../services/fhir/practitioner.services';
import { CommonServices } from '../services/common.services';
import { UserInterface } from './user.interface';
import { ModelMessage } from '../model_message/model.message';

@Component({
    moduleId: module.id,
    selector: 'cm-user-create',
    templateUrl: 'user.create.component.html',
    styleUrls: ['user.create.component.css']
})

export class UserCreateComponent implements OnInit, OnDestroy {

    public createUserForm: FormGroup;
    createMode: boolean = true;
    mode: string = 'Create';
    public submitted: boolean;
    private sub: any;
    userData: any;
    hospitalID: any;
    hospitalName: any;
    editUserType: any;
    editUserId: any;
    tenantName: any;
    allUsersType: any;
    hospitals: any;
    selectedHospital: any;
    hospitalUser: boolean = false;
    showHospitalList: boolean = false;
    showHospital: boolean = false;
    disableCreateAction: boolean = false;

    constructor(private router: Router, private route: ActivatedRoute, private fb: FormBuilder, private practitionerServices: PractitionerServices, private commonServices: CommonServices, private userServices: UserServices, private hospitalServices:HospitalServices, private modelMessage: ModelMessage) { }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeUsers() {
        this.router.navigate(['/users']);
    }

    onChangeUserType(type) {
        if (type.startsWith('HOS') && !this.hospitalUser) {
            this.showHospitalList = true;
        } else {
            this.showHospitalList = false;
        }
    }

    updateUser(userData){
        this.disableCreateAction = true; 
        this.userServices.updateUser(userData).subscribe(
            data => {
                let msg = "User " + userData.firstName + " was updated";
                this.modelMessage.successMessage(msg);
                this.routeUsers();
            },
            error => {
                this.disableCreateAction = false; 
                this.modelMessage.errorMessage(error.json());
            }
        ); 
    }

    createUser(userData){
      this.disableCreateAction = true;
      this.userServices.createUser(userData).subscribe(
        data => {
            let msg = "User " + userData.firstName + " was created";
            this.modelMessage.successMessage(msg);
            this.routeUsers();
        },
        error => {
            this.disableCreateAction = false; 
            this.modelMessage.errorMessage(error.json());
        }
     );
    }

    save(model: UserInterface, isValid: Boolean) {
        if (model.password !== model.confirmPassword) {
            this.modelMessage.errorMessage({ statusText: 'Password does not match the confirm password.' });
        } else {
        this.submitted = true;
        if (isValid) {
            if (this.createMode) { 
                this.disableCreateAction = true;
                delete model.confirmPassword;
                if(this.hospitalUser){
                    model.hospitalId = this.hospitalID;
                    model.fhirHospitalId = sessionStorage.getItem('fhirHospitalId');
                  this.createUser(model);
                }else if(!this.hospitalUser && model.userType.startsWith("HOS")){
                    this.disableCreateAction = true;
                    this.hospitalServices.getFhirOrgByHospitalId(model.hospitalId).subscribe(
                        data => {
                            let responseData:any = data || {};
                            if(responseData.id){
                               model.fhirHospitalId = responseData.id;
                               this.createUser(model);
                            }
                            else{
                               this.disableCreateAction = false;
                               let errorResponse = {status: 500, statusText: "Failed to create patient"};
                               this.modelMessage.errorMessage(errorResponse);
                            }
                        },
                        error => {
                            this.disableCreateAction = false;
                            let errorResponse = {status: 500, statusText: "Failed to create patient"};
                            this.modelMessage.errorMessage(errorResponse);
                        }
                    );
                }else{
                   this.createUser(model);
                }
            }
            
            else {
                this.disableCreateAction = true;
                model.zoomUserId = JSON.parse(sessionStorage.getItem('userData')).zoomUserId;
                if(model.userType === "HOSPITAL_DOCTOR" || model.userType === "HOSPITAL_NURSE"){
                   this.practitionerServices.getPractitionerByIdentifier(model.id).subscribe(
                       data => {
                           let responseData:any = data || {};
                           if(responseData.id){
                              model.fhirHospitalId = responseData.fhirOrgId; 
                              model.fhirId = responseData.id;
                              this.updateUser(model);
                           }else{
                               this.disableCreateAction = false;
                               let errorResponse = {status: 500, statusText: "Failed to update patient"};
                               this.modelMessage.errorMessage(errorResponse);
                           }
                       },
                       error => {
                           this.disableCreateAction = false;
                           this.modelMessage.errorMessage(error.json());
                       }
                   );
                }else{
                   this.updateUser(model);
               }
            }
         }
       }
     }
    initiateUserForm() {
        this.createUserForm = new FormGroup({
            id: new FormControl(''),
            designation: new FormControl(''),
            email: new FormControl(''),
            firstName: new FormControl(''),
            lastName: new FormControl(''),
            hospitalId: new FormControl(''),
            mobile: new FormControl(''),
            qualification: new FormControl(''),
            userID: new FormControl(''),
            userType: new FormControl(''),
            password: new FormControl(''),
            confirmPassword: new FormControl('')
        });
    }

    createEditUserForm(data) {
        this.editUserType = this.allUsersType.find(userType => userType.id === data.userType).name;
        this.editUserId = data.userID;
        if (data.userType.startsWith('HOS') && !this.hospitalUser) {
          this.showHospital = true;  
          this.hospitalName = this.hospitals.find(hospital => hospital.id == data.hospitalId).name;
        }else{
            this.showHospital = false;
        }
        this.createUserForm = new FormGroup({
            id: new FormControl(data.id),
            designation: new FormControl(data.designation),
            email: new FormControl(data.email),
            firstName: new FormControl(data.firstName),
            hospitalId: new FormControl(data.hospitalId),
            lastName: new FormControl(data.lastName),
            mobile: new FormControl(data.mobile),
            qualification: new FormControl(data.qualification),
            userID: new FormControl(data.userID),
            userType: new FormControl(data.userType)
        });
    }


    ngOnInit() {
        this.userData = JSON.parse(sessionStorage.getItem('userData'));
        this.hospitalID = this.userData.hospitalId;
        this.tenantName = '@' + sessionStorage.tenantName;
        this.hospitals = this.route.snapshot.data['hospitalList'];
        if (this.userData.userType === 'CC_ADMIN') {
            this.allUsersType = this.commonServices.getUserTypes();
        } else {
            this.showHospitalList = false;
            this.hospitalUser = true;
            this.hospitalName = this.hospitals.find(hospital => hospital.id == this.hospitalID).name;
            this.allUsersType = this.commonServices.getUserTypes().filter(userType => userType.id.startsWith('HOS'));
        }

        this.initiateUserForm();
       
        this.sub = this.route.params.subscribe(params => {
            if (params['id']) {
                this.mode = 'Edit';
                this.createMode = false;
                this.userServices.getUserInfo(params['id']).subscribe(
                    data => {
                        let responseData:any = data || {};
                        this.selectedHospital = responseData.hospitalId;
                        this.createEditUserForm(responseData);
                    },
                    error => {
                        this.modelMessage.errorMessage(error);
                    }
                );
            }
        });
    }

    ngOnDestroy() { }
}


