import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { UserServices } from '../presence/user.services';

@Injectable()
export class UserListResolver implements Resolve<any> {
    constructor( private userServices:UserServices){}

    resolve(){
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
           return this.userServices.getAllUsersByHospitalID(hospitalId).map(userList => userList);        
        }else{
           return this.userServices.getAllUsers().map(userList => userList);         
        }   
   }
}


