import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators, FormsModule } from '@angular/forms';
import { LoginInterface } from './login.interface';
import { CommonServices } from '../services/common.services';
import { LoginServices } from '../services/presence/login.services';

@Component({
    moduleId: module.id,
    selector: 'login-form',
    templateUrl: 'login.component.html',
    styleUrls: ['login.component.css']
})
export class LoginComponent implements OnInit {

   @Output()
   loginEvent: EventEmitter<string> = new EventEmitter(); 

   private loginForm: FormGroup;
   private location: string;
   private username: string;
   private password: string;
   private errorMessage: string;
   private submitted: boolean;
    

   constructor(private router: Router,private commonServices: CommonServices,private loginServices: LoginServices) {
        this.location = router.url;
   }

   getTenantName(userName){
      let tenantName = userName.split('@')[1];
      sessionStorage.setItem('tenantName',tenantName);
   }

   login(model: LoginInterface, isValid: boolean) {
        this.submitted = true;
        if (isValid) {
            this.getTenantName(model.username);
            this.commonServices.prepareReqOptions();
            this.loginServices.login(model).subscribe(
              data => {
                console.log("login response:"+JSON.stringify(data));
                sessionStorage.setItem('userRoleInfo', JSON.stringify(data));
                let userSessionInfo: any = data;
                let scope = userSessionInfo.scope;
                sessionStorage.setItem('userScope', JSON.stringify(scope.split(' ')));
                this.loginEvent.emit("authenticated");
              },
              error => {
                this.username = '';
                this.password = '';
                this.errorMessage = "UserId/Password is invalid";
              }
            );
        }
    }

   createLogin() {
        this.loginForm = new FormGroup({
            username: new FormControl('', <any>Validators.required),
            password: new FormControl('', <any>Validators.required)
        });
    }

   ngOnInit() {
       this.createLogin();
   }
}



