import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import * as $ from 'jquery';

@Injectable()
export class ModelMessage {

    constructor(private router:Router) { }

    infoMessage(data) {
        $('.infoPopUp').show(function() {
            $('.infoPopUpMessage').html(data);
            $('.infoPopUp').delay(5000).hide(0);
        });
    }

    successMessage(data) {
        $('.successPopUp').show(function() {
            $('.successPopUpMessage').html(data);
            $('.successPopUp').delay(5000).hide(0);
        });
    }

    warningMessage(data) { }

    errorMessage(data) {
        $('.errorPopUp').show(function() {
            $('.errorPopUpMessage').html(data.statusText);
            $('.errorPopUp').delay(5000).hide(0, () => {
                if (data.status === 401) {
                    //this.router.navigate(['/']);
                    sessionStorage.clear();
                    location.reload();
                }
            });
        });
    }

    ngOnInit() { }
}



