import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class SpecialInstructionsServices {

    constructor( private commonServices: CommonServices) {}

    postSpecialInstructionsNote(specialinstructionobrData)
    {
        return this.commonServices.dataServiceHandler("/fhir/specialInstructionsNotes/", 'POST', specialinstructionobrData)
    }

    getSpecialInstructionsNotes(patientId, encounterID)
    {
        return this.commonServices.dataServiceHandler("/fhir/specialInstructionsNotes/" + patientId + "/" + encounterID, 'GET');
    }

    getSpecialInstructionsVitals(patientId,encounterID)
    {
        return this.commonServices.dataServiceHandler("/fhir/specialInstructionsVitals/" + patientId + "/" + encounterID, 'GET');
    }
}


