import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class BedServices {

    constructor(private commonServices: CommonServices) {}

    createBed(bedData) {
        return  this.commonServices.dataServiceHandler("/presence/bed",'POST',bedData);
    }

    updateBed(bedData) {
        return  this.commonServices.dataServiceHandler("/presence/bed/" + bedData.id,'PUT',bedData);
    }

    getAllBeds() {
        return  this.commonServices.dataServiceHandler('/api/bed/all','GET');
    }

    getBedDataByBedID(id) {
        return  this.commonServices.dataServiceHandler('/api/bed/' + id,'GET');
    }

    getAllBedsByHospitalID(id) {
        return  this.commonServices.dataServiceHandler('/api/bed/all/hospital/' + id,'GET');
    }

    getAllBedsByIcu(id) {
        return this.commonServices.dataServiceHandler('/api/bed/all/icu/' + id,'GET');
    }

    deleteBed(id) {
        return this.commonServices.dataServiceHandler("/presence/bed/" + id,'DELETE');
    }
}


