import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()

export class PhysicalExamServices {

    constructor(private commonServices:CommonServices){}

    getClinicalDocumentPhysicalForm(patientId,encounterId){
        return this.commonServices.dataServiceHandler("/fhir/clinicalDocumentPhysicalExamForm/" + patientId + "/" + encounterId, 'GET')
    }

    createClinicalDocumentPhysicalForm(physicalExamData){
        return this.commonServices.dataServiceHandler("/fhir/clinicalDocumentPhysicalExamForm/", 'POST', physicalExamData)
    }

    updateClinicalDocumentPhysicalForm(physicalExamData){
        return this.commonServices.dataServiceHandler("/fhir/clinicalDocumentPhysicalExamForm/",'PUT', physicalExamData);
    }
}



