import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { PatientServices } from '../presence/patient.services';

@Injectable()
export class PatientListResolver implements Resolve<any> {
    constructor( private patientServices:PatientServices){

    }

    resolve(){
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
            return this.patientServices.getAllPatientsByHospital(hospitalId).map(patientList => patientList);
        }else{
        return this.patientServices.getAllPatients().map(patientList => patientList);
            }
        }
}


