import { Component, OnInit, Input,  Output, EventEmitter  } from '@angular/core';
import { ModelMessage } from '../model_message/model.message';
import { RoleServices } from '../services/presence/role.services';
import { ProcedureServices } from '../services/fhir/procedure.services';

@Component({
    moduleId: module.id,
    selector: 'patient-procedure',
    templateUrl: 'patient.procedure.component.html',
    styleUrls: ['patient.procedure.component.css'],
})

export class PatientProcedureComponent implements OnInit {
    
    @Output()
    cancelEvent: EventEmitter<string> = new EventEmitter(); 

    loading: boolean = true;
    roleWrite: boolean = false;
    errorMessage: string;
    procedureNotesList:any;
    entryProcedureNote:string;
    

    @Input() patientData: any;
    @Input() practitionerData: any;
    @Input() patientName:string;

    constructor(private roleServices: RoleServices, private procedureServices: ProcedureServices, private modelMessage: ModelMessage) { }

    clickProcedureCancel(){
        this.procedureNotesList=null;
        this.cancelEvent.emit("cancel");
    }

    clickProcedureAdd() {
        if(this.entryProcedureNote){
            let procedureData =
                {
                    "patientId": this.patientData.id,
                    "practitionerId": this.practitionerData.id,
                    "note": this.entryProcedureNote,
                    "name": this.practitionerData.name,
                    "encounterId": this.patientData.encounterId
                };
            this.procedureServices.createProcedure(procedureData).subscribe(
                data => {
                    this.procedureNotesList.splice(0,0,data);
                    this.entryProcedureNote=null;
                },
                error => {
                    this.modelMessage.errorMessage(error);
                });
        }else{
            this.errorMessage = 'Please Enter the Procedure notes';
        }  
    }

    getProcedure(patientId,encounterId) {
        this.procedureServices.getProcedure(patientId,encounterId).subscribe(
            data => {
                let responseData:any = data;
                if(responseData.length){
                  this.procedureNotesList = responseData;
                  this.loading=false; 
                }else{
                    if(!this.roleWrite){
                        this.cancelEvent.emit();
                        this.modelMessage.infoMessage("No Procedure Notes");
                        this.loading=false;
                    }else{
                        this.loading=false;
                    }
                }
            },
            error => {
                this.loading=false;
                this.cancelEvent.emit();
                this.modelMessage.errorMessage(error);
            });
    }

    ngOnInit() {
        let userRole = this.roleServices.getRole('patient-detail');
        if (userRole.write) {
            this.roleWrite = true;
        }
        this.getProcedure(this.patientData.id, this.patientData.encounterId);
    }
}



