import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';
import { PatientsSummaryComponent } from './patients.summary.component';
import {PatientSearch} from '../patients_summary/search';
import { PatientDiagnosisComponent } from '../patient_diagnosis/index';
import { PatientProcedureComponent } from '../patient_procedure/index';
import { PatientSpecialInstructionsComponent } from '../patient-special-instructions/index';

@NgModule({
    imports: [CommonModule, FormsModule,ReactiveFormsModule],
    declarations: [PatientsSummaryComponent,PatientSearch,PatientDiagnosisComponent,PatientProcedureComponent,PatientSpecialInstructionsComponent],
    exports: [PatientsSummaryComponent,PatientDiagnosisComponent,PatientProcedureComponent,PatientSpecialInstructionsComponent]
})

export class PatientsSummaryModule { }



