import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationStart } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { RoleServices } from '../services/presence/role.services';
import { BedServices } from '../services/presence/bed.services';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { PatientServices } from '../services/presence/patient.services';
import { PatientFhirServices } from '../services/fhir/patient.services';
import { PractitionerServices } from '../services/fhir/practitioner.services';
import { CameraServices } from '../services/presence/camera.services';
import { MeetingServices } from '../services/presence/meeting.services';
import { NotesServices } from '../services/presence/notes.services';
import { ModelMessage } from '../model_message/model.message';
import {PatientSearch} from '../patients_summary/search';
import * as moment from 'moment';
import * as $ from 'jquery';

@Component({
    moduleId: module.id,
    selector: 'patients',
    templateUrl: './patients.summary.component.html',
    styleUrls: ['patients.summary.component.css']
})

export class PatientsSummaryComponent implements OnInit {
    errorMessage: string = '';
    patientName: string;
    enteryPatientNotes: string;
    patientsList: any = [];
    activeBtnGrid: string = "bg-info";
    activeBtnList: string = "";
    consultantID: string;
    patientGridView: boolean = true;
    patientListView: boolean = false;
    notePatientID: string;
    noteStartTime: any;
    noteHospitalId: string;
    deletePatientID: string;
    deletePatientName: string;
    roleWrite: boolean = false;
    roleNoteRead: boolean = false;
    roleNoteWrite: boolean = false;
    roleCameraAccess: boolean = false;
    roleDiagnosisAccess: boolean = false;
    roleprocedureAccess: boolean = false;
    roleFavPatientAccess: boolean = true;
    rolePatientClinicalInformationAccess: boolean = false;
    loading: boolean = true;
    userScope: any[];
    hospitalID: string = '';
    allPatients: any;
    hospitalAllPatients: any;
    allHospitals: any;
    allBeds: any;
    allDepartments: any;
    userData: any;
    patientSearchbox: any;
    patientsRouting:any;
    fhirPatients:any;
    practitionerData:any;
    currentPatientData: any;
    enableDiagnosis:boolean = false;
    enableProcedure:boolean = false;
    enableSpecialInstructions:boolean = false;
    fhirHospitalId: string;

    constructor(private router: Router, private route: ActivatedRoute, private roleServices: RoleServices, private bedServices: BedServices, private departmentServices: DepartmentServices, private hospitalServices: HospitalServices, private patientServices:PatientServices, private patientFhirServices:PatientFhirServices, private cameraServices: CameraServices,private meetingServices: MeetingServices, private notesServices: NotesServices, private modelMessage: ModelMessage, private practitionerServices:PractitionerServices) { }

    preparePatientData(resolve, reject) {
        this.allPatients.forEach(currentPatient => {
            this.allBeds.forEach(currentBed => {
                if (currentBed.id === currentPatient.bedId) {
                    this.allDepartments.forEach(currentDepartment => {
                        if (currentDepartment.id === currentBed.icuId) {
                            let birthdate = moment(currentPatient.dateOfBirth).format('MM/DD/YYYY');
                            let age  = this.getAge(birthdate);
                            currentPatient['hospitalName']=this.allHospitals.name;
                            currentPatient['department']=currentDepartment.name;
                            currentPatient['bedName'] = currentBed.name;
                            currentPatient['age']=age;
                        }
                    });
                }
            });
        });
        if(this.allPatients.length){
          resolve(this.allPatients);
        }else{
          reject("Unable to fetch Patient Details")
        }
    }
        
    getAge(dateString) {
        let now : any = new Date();
        let today : any = new Date(now.getYear(),now.getMonth(),now.getDate());

        let yearNow = now.getYear();
        let monthNow = now.getMonth();
        let dateNow = now.getDate();

        let dob : any = new Date(dateString.substring(6,10),
                            dateString.substring(0,2)-1,                   
                            dateString.substring(3,5)                  
                            );

        let yearDob = dob.getYear();
        let monthDob = dob.getMonth();
        let dateDob = dob.getDate();
        let age : any;
        let ageString = "";
        let yearString = "";
        let monthString = "";
        let dayString = "";
        let monthAge : any;
        let dateAge : any;


        let yearAge = yearNow - yearDob;

        if (monthNow >= monthDob)

            monthAge = monthNow - monthDob;
        else {
            yearAge--;
            monthAge = 12 + monthNow -monthDob;
        }

        if (dateNow >= dateDob)
            dateAge = dateNow - dateDob;
        else {
            monthAge--;
            dateAge = 31 + dateNow - dateDob;

            if (monthAge < 0) {
            monthAge = 11;
            yearAge--;
            }
        }

        age = {
            years: yearAge,
            months: monthAge,
            days: dateAge
            };

        if ( age.years > 1 ) yearString = " years";
        else yearString = " year";
        if ( age.months> 1 ) monthString = " months";
        else monthString = " month";
        if ( age.days > 1 ) dayString = " days";
        else dayString = " day";


        if ( (age.years > 0) && (age.months > 0) && (age.days > 0) )
            ageString = age.years + yearString;
        else if ( (age.years == 0) && (age.months == 0) && (age.days > 0) )
            ageString = age.days + dayString;
        else if ( (age.years > 0) && (age.months == 0) && (age.days == 0) )
            ageString = age.years + yearString;
        else if ( (age.years > 0) && (age.months > 0) && (age.days == 0) )
            ageString = age.years + yearString;
        else if ( (age.years == 0) && (age.months > 0) && (age.days > 0) )
            ageString = age.months + monthString + " and " + age.days + dayString;
        else if ( (age.years > 0) && (age.months == 0) && (age.days > 0) )
            ageString = age.years + yearString;
        else if ( (age.years == 0) && (age.months > 0) && (age.days == 0) )
            ageString = age.months + monthString;
        else ageString = "-";

        return ageString;
    }


    getDepartments(resolve, reject) {
        this.departmentServices.getAllDepartmentsByHospitalID(this.userData.hospitalId).subscribe(
            data => {
                this.allDepartments = data;
                this.preparePatientData(resolve, reject);
            },
            error => {
                reject(error);
            }
        );
    }

    getBeds(resolve, reject) {
        this.bedServices.getAllBedsByHospitalID(this.hospitalID).subscribe(
            data => {
                this.allBeds = data;
                this.getDepartments(resolve, reject);
            },
            error => {
                reject(error);
            }
        );
    }

    getHospitals(resolve, reject) {
        this.hospitalServices.getHospitalDataByHospitalId(this.hospitalID).subscribe(
            data => {
                this.allHospitals = data;
                this.getBeds(resolve, reject);
            },
            error => {
                reject(error);
            }
        );
    }


    getAllPresencePatientsData():Promise<any> {
      return new Promise((resolve, reject)=>{
            this.patientServices.getAllPatients().subscribe(
            data => {
                let patientsList: any;
                let dataPatient = [];
                patientsList = data;
                patientsList.forEach(currentPatient => {
                    let responseCurrrentPatient:any = currentPatient;
                    let birthdate = moment(responseCurrrentPatient.dateOfBirth).format('MM/DD/YYYY');
                    let age  = this.getAge(birthdate);
                    let patientDetails = {
                        id: responseCurrrentPatient.id,
                        name: responseCurrrentPatient.name,
                        gender: responseCurrrentPatient.gender,
                        dateOfBirth: age,
                        maritialStatus: responseCurrrentPatient.maritialStatus,
                        mrn: responseCurrrentPatient.mrn,
                        lastSeen: responseCurrrentPatient.lastSeen,
                        hospitalName: responseCurrrentPatient.hospitalName,
                        department: responseCurrrentPatient.icuName,
                        bedName: responseCurrrentPatient.bedName,
                        cameraId: responseCurrrentPatient.cameraId,
                        cameraName: responseCurrrentPatient.cameraName
                    };
                    dataPatient.push(patientDetails);
                });
                resolve(data);
            },
            error => {
                reject(error);
            });
        });
    }

    getAllFhirPatientsData():Promise<any>{
      return new Promise((resolve, reject)=>{
        this.patientFhirServices.getAllFhirPatients().subscribe(
          data=>{
            resolve(data);
          },
          error=>{
            reject(error);
          }
       )
      });
    }

    getPractitionerData():Promise<any>{
      let userId=JSON.parse(sessionStorage.getItem('userData')).id;
      return new Promise((resolve, reject)=>{
        this.practitionerServices.getPractitionerByIdentifier(userId).subscribe(
          data=>{
            resolve(data);
          },
          error=>{
            reject(error);
          }
       )
      });
    }

    getPatients() {
        let role = JSON.parse(sessionStorage.getItem('userRoleInfo')).role;
        let presencePatientPromise:any;
        let fhirPatientPromise:any;
        let practitionerPromise:any;

        //get Presence patients details
        if (this.hospitalID) {
          presencePatientPromise = new Promise((resolve, reject)=>{
            this.patientServices.getAllPatientsByHospital(this.hospitalID).subscribe(
                data => {
                    this.hospitalAllPatients = data;
                    this.allPatients = this.hospitalAllPatients.filter(patient => patient.status === "Admitted");
                    this.getHospitals(resolve, reject);
                },
                error => {
                    reject(error);
                }
            );
          });
        } else {
           presencePatientPromise = this.getAllPresencePatientsData();
        }

        //depends on role, get FhirPatients and Practitioner Data
        if(role === 'HOSPITAL_DOCTOR' || role === 'HOSPITAL_NURSE' || role === 'CC_CONSULTANT'){
            fhirPatientPromise = this.getAllFhirPatientsData();
            practitionerPromise = this.getPractitionerData();
        }

        //setting the values
        Promise.all([presencePatientPromise,fhirPatientPromise,practitionerPromise]).then(data=>{
            this.patientsList = data[0] || [];
            this.fhirPatients = data[1];
            this.practitionerData=data[2];
            if(this.patientsList.length && this.fhirPatients){
              this.patientsList.forEach((patient)=>{
                let fhirPatient = this.fhirPatients.find(item=>item.pid === patient.id)
                if(fhirPatient){
                  patient.fhirData=fhirPatient;
                }
              })
            }
            this.loading = false;
        },
        err=>{
            this.modelMessage.errorMessage(err);
            this.loading = false;
        });
    }
    callZoomMeeting(data) {
        let zoomURL = JSON.parse(data._body);
        window.open(zoomURL.startUrl, '_blank');
    }

    clickCamera(id) {
        let meeting = {
            "hostId": JSON.parse(sessionStorage.getItem('userData')).zoomUserId,
            "invitees": [id]
        };

        this.meetingServices.createMeeting(meeting).subscribe(
            data => {
                this.callZoomMeeting(data);
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
    }

    clickNote(id, pname, hospitalId) {
        this.notePatientID = id;
        this.patientName = pname;
        this.noteHospitalId = hospitalId;
        this.noteStartTime = new Date();
        $('#editor-lightbox .patient-note').show();
    }

    clickEdit(id) {
        this.router.navigate(['/patient/create/', id,'Edit']);
    }

    clickFavPatient(id) {
        console.log('clickFavPatient');
    }

    clickNotesAdd() {
        let notes = $("<div/>").text(this.enteryPatientNotes).html();
        if (notes !== '') {
            this.errorMessage = '';
            let noteObject = {
                consultantid: this.consultantID,
                details: [notes],
                endtime: new Date(),
                hospitalId: this.noteHospitalId,
                patientid: this.notePatientID,
                starttime: this.noteStartTime,
                status: 'ACTIVE'
            };

            this.notesServices.createNote(noteObject).subscribe(
                data => {
                    let msg = 'Patient ' + this.patientName + ' notes saved ';
                    this.modelMessage.successMessage(msg);
                },
                error => {
                    this.modelMessage.errorMessage(error);
                }
            );

            this.enteryPatientNotes = '';
            $('#editor-lightbox .patient-note').hide();
        } else {
            this.errorMessage = 'Please enter notes';
        }
    }

    clickNotesCancel() {
        this.enteryPatientNotes = '';
        this.errorMessage = '';
        $('#editor-lightbox .patient-note').hide();
    }

    clickDiagnosis(patient) {
        this.currentPatientData = patient;
        this.enableDiagnosis=true;
        $('#editor-lightbox .patient-diagnosis').show();
    }

    clickDiagnosisCancel() {
        this.currentPatientData = null;
        this.enableDiagnosis=false;
        $('#editor-lightbox .patient-diagnosis').hide();
    }

    clickProcedure(patient) {
        this.currentPatientData = patient;
        this.enableProcedure=true;
        $('#editor-lightbox .patient-procedure').show();
    }

    clickProcedureCancel() {
        this.currentPatientData = null;
        this.enableProcedure=false;
        $('#editor-lightbox .patient-procedure').hide();
    }

    clickSpecialInstructions(patient) {
        this.currentPatientData = patient;
        this.enableSpecialInstructions=true;
        $('#editor-lightbox .patient-special-instructions').show();
    }

    clickSpecialInstructionsCancel() {
        this.currentPatientData = null;
        this.enableSpecialInstructions=false;
        $('#editor-lightbox .patient-special-instructions').hide();
    }

    clickDischargeConfirm(patient) {
        this.currentPatientData = patient;
        this.patientName = patient.name;
        $('.dischargeConfirmPopUp').show();
    }

    clickCancelDischarge() {
        this.currentPatientData = null;
        this.patientName = null;
        $('.dischargeConfirmPopUp').hide();
    }

    clickDischargePatient(){
        if (this.currentPatientData.status === 'Admitted') {
            let patientData = this.currentPatientData;
            patientData.dateOfBirth = moment(patientData.dateOfBirth).format('YYYY-MM-DD');
            patientData.status = 'Discharged';
            //patientData.bedId = '';
            patientData.ipno = this.currentPatientData.fhirData.ipn;
            patientData.encounterId = this.currentPatientData.fhirData.encounterId;
            this.patientServices.updatePatient(patientData,this.currentPatientData.fhirData.id,this.fhirHospitalId).subscribe(
                data => {
                    this.clickCancelDischarge();
                    let msg = 'Patient ' + patientData.name + ' was discharged ';
                    this.modelMessage.successMessage(msg);
                    setTimeout(()=>{ location.reload(); }, 1000);
                },
                error => {
                    let errorResponse = {status: 500, statusText: "Failed to discharge patient"};
                    this.modelMessage.errorMessage(errorResponse);
                }
            );
        } else {
            this.router.navigate(['/patient/create/', this.currentPatientData.id, 'Edit']);
        }
    }

    clickPatientGridView() {
        this.activeBtnGrid = "bg-info";
        this.activeBtnList = "";
        this.patientGridView = true;
        this.patientListView = false;
    }

    clickPatientListView() {
        this.activeBtnGrid = "";
        this.activeBtnList = "bg-info";
        this.patientGridView = false;
        this.patientListView = true;
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeCreatePatient() {
        this.router.navigate(['/patient/create']);
    }

    routPatientDetails(id) {
        this.router.navigate(['/patient/details/', id]);
    }

    ngOnInit() {
        $('#editor-lightbox .patient-diagnosis, #editor-lightbox .patient-procedure').hide();
        let userRole = this.roleServices.getRole('patient');
        if (userRole.write) {
            this.roleWrite = true;
        }
        if (userRole.write) {
            this.roleWrite = true;
        }
        let userNotesRole = this.roleServices.getRole('notes');
        if (userNotesRole.write) {
            this.roleNoteWrite = true;
        }
        if (userNotesRole.read) {
            this.roleNoteRead = true;
        }
        let patientClinicalInformation = this.roleServices.getRole('patient-detail');
        if (patientClinicalInformation.read) {
            this.rolePatientClinicalInformationAccess = true;
            this.roleDiagnosisAccess = true;
            this.roleprocedureAccess=true;
        }
        let meetingRole = this.roleServices.getRole('meeting');
        if (meetingRole.write) {
            this.roleCameraAccess = true;
        }
        
        this.userData = JSON.parse(sessionStorage.getItem('userRoleInfo'));
        this.consultantID = this.userData.uuid;
        this.hospitalID = this.userData.hospitalId;
        this.fhirHospitalId = sessionStorage.getItem('fhirHospitalId');
        this.getPatients();

       this.route.params.subscribe(params => {
            if (params['search']) {
                this.patientSearchbox= params['search'];
            }
        });
    }
}



