import { Component, AfterViewInit, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment-timezone';
import { GridOptions } from "ag-grid/main";
import { CommonServices } from "../services/common.services";
import { FlowsheetServices } from "../services/fhir/flowsheet.services";
import { ModelMessage } from '../model_message/model.message';
import { Socket } from 'ng-socket-io';

const observationWidth: number = 150;
const timestampWidth: number = 100;

@Component({
    moduleId: module.id,
    selector: 'patient-vitals-flowsheet',
    templateUrl: 'patient.vitals.flowsheet.component.html',
    styleUrls: ['patient.vitals.flowsheet.component.css'],
})

export class PatientVitalsFlowsheetComponent implements OnInit {

    @Input() patientData:any;

    columnDefs: any[];
    rowData: any;
    vitalsDataIndex: any = 0;
    observedDate: any;
    private colEditable: boolean = true;
    orgTimeZone:any; 
    disablePageDownButton:boolean=true;
    disablePageUpButton:boolean=true;
    currrentRowData:Array<any> = [];
    public gridOptions: GridOptions;
    private coldefs: any;
    private flowsheetData: any;
    loading:boolean = true;

    startGrid:boolean = false;

    constructor(private router: Router, private commonServices: CommonServices, private flowsheetServices: FlowsheetServices, private modelMessage:ModelMessage, private socket:Socket) { 
        this.orgTimeZone = this.commonServices.getAppTimeZone();
    }

    getSocket(){
      this.socket.on('flowsheet',data=>{
          let responseData: any = data;

          /*Ag-Grid -> setRowData*/
          // this.currrentRowData = responseData.filter(data => moment(data.observedDate).tz(this.orgTimeZone).format('YYYY-MM-DD') === moment(this.observedDate,'DD/MM/YYYY h:mm a').tz(this.orgTimeZone).format('YYYY-MM-DD')) || [];
          // if(this.currrentRowData.length){
          //     let vitalRowData:any = this.currrentRowData.find(data=>data.observationData[0].observation === "Vital Signs")
          //     if(vitalRowData){
          //         this.setUpdatedRowData(vitalRowData.observationData[0].subcategory);
          //     }
          // }


          /*Ag-Grid -> Refresh Cell*/
          if( moment(responseData.observedDateTime).tz(this.orgTimeZone).format('YYYY-MM-DD') === moment(this.observedDate,'DD/MM/YYYY h:mm a').tz(this.orgTimeZone).format('YYYY-MM-DD') ){
              this.setUpdatedRowData(responseData.patientObservationsData);
          }
      });
    }

    setUpdatedRowData(observationRowData) {

        /*Ag-Grid -> Refresh Cell*/
        this.gridOptions.api.forEachNode( function(node) {
            if(node.data.id === observationRowData.id){
                node.data = observationRowData
            }
        });
        var params:any = {
            force: false
        };
        this.gridOptions.api.refreshCells(params);

        /*Ag-Grid -> setRowData*/
        // this.rowData = observationRowData;
        // this.gridOptions.api.setRowData(this.rowData)
    }

    setDisableButton(){
        if(this.vitalsDataIndex === 0 && this.flowsheetData.length >1){
            this.disablePageDownButton = false;
            this.disablePageUpButton = true;
        }else if( this.vitalsDataIndex >= this.flowsheetData.length - 1){
            this.disablePageDownButton = true;
            this.disablePageUpButton = false;
        }else{
            this.disablePageDownButton = false;
            this.disablePageUpButton = false;
        }
    }

    pageHandler(pageActition) {
        if (pageActition == "pageDown" && this.vitalsDataIndex < this.flowsheetData.length - 1 ) {
            this.loading = true;
            this.vitalsDataIndex++;
            this.observedDate = moment(this.flowsheetData[this.vitalsDataIndex].observedDate).tz(this.orgTimeZone).format('DD/MM/YYYY h:mm a');
            let coloumnDefs = this.createColumnDefs(this.flowsheetData[this.vitalsDataIndex].observedDate);
            this.gridOptions.api.setColumnDefs(coloumnDefs)
            this.rowData = this.flowsheetData[this.vitalsDataIndex].observationData[0].subcategory
            this.gridOptions.api.sizeColumnsToFit();
            this.setDisableButton();
            /*reset the observations in cache for previous day*/
            this.flowsheetServices.setChangedObservations([]);
            this.loading=false;
        } else if (pageActition == "pageUp" && this.vitalsDataIndex > 0) {
            this.loading = true;
            this.vitalsDataIndex--;
            this.observedDate = moment(this.flowsheetData[this.vitalsDataIndex].observedDate).tz(this.orgTimeZone).format('DD/MM/YYYY h:mm a');
            let coloumnDefs = this.createColumnDefs(this.flowsheetData[this.vitalsDataIndex].observedDate);
            this.gridOptions.api.setColumnDefs(coloumnDefs)
            this.rowData = this.flowsheetData[this.vitalsDataIndex].observationData[0].subcategory
            this.gridOptions.api.sizeColumnsToFit();
            this.setDisableButton();
            /*reset the observations in cache for previous day*/
            this.flowsheetServices.setChangedObservations([]);
            this.loading=false;
        }
    }


    private createColumnDefs(observedDateTime) {
        let globalWidth = 67;
        let colDefs = [];
        let patientadmittedDate = moment(this.patientData.admittedDate).tz(this.orgTimeZone);
        let observedDate = moment(observedDateTime).tz(this.orgTimeZone);
        let morningDate:any = moment(observedDateTime).tz(this.orgTimeZone).startOf('day');
        let eveningDate:any = moment(observedDateTime).tz(this.orgTimeZone).startOf('day');
        let nightDate:any = moment(observedDateTime).tz(this.orgTimeZone).startOf('day');
        let isMorningShift:boolean = false;
        let isEveningShift:boolean = false;
        let isNightShift:boolean = false;
        this.coldefs = null;
        colDefs.push({ headerName: "Observation", field: "observation", cellRenderer: "group", pinned: true, suppressMovable: true, minWidth: 200, width:320 });

        if( patientadmittedDate.format('YYYY-MM-DD') === observedDate.format('YYYY-MM-DD') ){

            let time = moment(patientadmittedDate).tz(this.orgTimeZone);

            //check whether the admitted time lies in Morning Shift
            if(time.format('HH:mm') > "08:00" && time.format('HH:mm') < "13:30"){
                morningDate = moment(patientadmittedDate).tz(this.orgTimeZone).startOf('hour');
                if(time.format('mm') > '30'){
                    morningDate = morningDate.add(1, 'hours');
                }
                eveningDate = eveningDate.set({hour:14});
                nightDate = nightDate.set({hour:20});
                isMorningShift = true;
                isEveningShift = true;
                isNightShift = true;
            } 
            //check whether the admitted time lies in Evening Shift
            else if(time.format('HH:mm') > "14:00" && time.format('HH:mm') < "19:30"){
                eveningDate = moment(patientadmittedDate).tz(this.orgTimeZone).startOf('hour');
                if(time.format('mm') > '30'){
                    eveningDate.add(1, 'hours');
                }
                nightDate = nightDate.set({hour:20});
                isEveningShift = true;
                isNightShift = true;
            } 
            //check whether the admitted time lies in Night Shift
            else if(time.format('HH:mm') > "20:00" && time.format('HH:mm') < "7:30"){
                nightDate = moment(patientadmittedDate).tz(this.orgTimeZone).startOf('hour');
                if(time.format('mm') > '30'){
                    nightDate.add(1, 'hours');
                }
                isNightShift = true;
            }

        }else{

            morningDate = morningDate.set({hour:8});
            eveningDate = eveningDate.set({hour:14});
            nightDate = nightDate.set({hour:20});
            isMorningShift = true;
            isEveningShift = true;
            isNightShift = true;

        }

        if (isMorningShift) {
            let morningColDef = {
                headerName: "Morning",
                suppressMovable: true,
                children: []
            };

            let morningColData = {
                headerName: morningDate.format('HH:mm'),
                newValueHandler: this.checkNewValue,
                suppressMovable: true,
                field: morningDate.format(),
                editable: this.colEditable, minWidth: globalWidth,
                cellRenderer: 'animateSlide'
            };
            morningColDef.children.push(morningColData);

            while (morningDate.format('HH:mm') !== '13:30') {
                morningColData = {
                    headerName: morningDate.add(30, 'minutes').format('HH:mm'),
                    newValueHandler: this.checkNewValue,
                    suppressMovable: true,
                    field: morningDate.format(),
                    editable: this.colEditable, minWidth: globalWidth,
                    cellRenderer: 'animateSlide'
                };
                morningColDef.children.push(morningColData);
            }
            colDefs.push(morningColDef);
        }

        if (isEveningShift) {
            let eveningColDef = {
                headerName: "Evening",
                suppressMovable: true,
                children: []
            };
            let eveningColData = {
                headerName: eveningDate.format('HH:mm'),
                newValueHandler: this.checkNewValue,
                suppressMovable: true,
                field: eveningDate.format(),
                editable: this.colEditable, minWidth: globalWidth,
                cellRenderer: 'animateSlide'
            };
            eveningColDef.children.push(eveningColData);

            while (eveningDate.format('HH:mm') !== '19:30') {
                eveningColData = {
                    headerName: eveningDate.add(30, 'minutes').format('HH:mm'),
                    newValueHandler: this.checkNewValue,
                    suppressMovable: true,
                    field: eveningDate.format(),
                    editable: this.colEditable, minWidth: globalWidth,
                    cellRenderer: 'animateSlide'
                };
                eveningColDef.children.push(eveningColData);
            }
            colDefs.push(eveningColDef);
        }

        if (isNightShift) {
            let nightColDef = {
                headerName: "Night",
                suppressMovable: true,
                children: []
            };
            let nightColData = {
                headerName: nightDate.format('HH:mm'),
                newValueHandler: this.checkNewValue,
                suppressMovable: true,
                field: nightDate.format(),
                editable: this.colEditable, minWidth: globalWidth,
                cellRenderer: 'animateSlide'
            };
            nightColDef.children.push(nightColData);
            while (nightDate.format('HH:mm') !== '07:30') {
                nightColData = {
                    headerName: nightDate.add(30, 'minutes').format('HH:mm'),
                    newValueHandler: this.checkNewValue,
                    suppressMovable: true,
                    field: nightDate.format(),
                    editable: this.colEditable, minWidth: globalWidth,
                    cellRenderer: 'animateSlide'
                };
                nightColDef.children.push(nightColData);
            }
            colDefs.push(nightColDef);
        }

        this.coldefs = colDefs;
        return this.coldefs;
    }

    checkNewValue(params) {
        let valueAsNumber = params.newValue
        if (isNaN(valueAsNumber) || params.newValue == "") {
            console.log("Invalid value " + params.newValue + ", must be a number");
        } else {
            params.data[params.colDef.field] = valueAsNumber;
        }
    }

    public getNodeChildDetails(rowItem) {
        if (rowItem && rowItem.maincategory) {
            return {
                group: true,
                expanded: rowItem.open,
                children: rowItem.subcategory,
                field: 'observation',
                key: rowItem.observation
            };
        } else {
            return null;
        }
    }
    

    private saveChangedObservations(){
        let changedObservations = this.flowsheetServices.getChangedObservations();
        let effectiveDate = moment(this.observedDate,'DD/MM/YYYY h:mm a').tz(this.orgTimeZone).format();
        if(changedObservations.length > 0){
            this.flowsheetServices.updateFlowsheetData(this.patientData.id,this.patientData.encounterId,'vitalsigns',effectiveDate,changedObservations).subscribe(
                data => {
                    let responseData:any = data || {};
                    changedObservations = [];
                    this.flowsheetServices.setChangedObservations(changedObservations);
                    this.modelMessage.successMessage(responseData.message);
                },
                error => {
                this.modelMessage.errorMessage(error);
                }
            );
        }
    }

    private onCellValueChanged($event) {
        let changedObservations = this.flowsheetServices.getChangedObservations();
        if((!$event.oldValue && $event.newValue !== "") || ($event.newValue !== "" && $event.oldValue !== $event.newValue && changedObservations.length == 0)){
            let observation:any = {};
            observation.effectiveDataTime = $event.colDef.field;
            observation.value = $event.newValue;
            observation.category = $event.data.category;
            if($event.data[$event.colDef.field+"-id"]){
                observation.id = $event.data[$event.colDef.field+"-id"];
            }
            changedObservations.push(observation);

        }else if($event.newValue !== "" && $event.oldValue !== $event.newValue){
            let observationCategoryId = $event.data.category.subCategory.id;
            let observationIndex = changedObservations.findIndex(observation => observation.effectiveDataTime == $event.colDef.field && observation.category.subCategory.id == observationCategoryId);
            if(observationIndex !== -1 && changedObservations[observationIndex].value !== $event.newValue){
                changedObservations[observationIndex].value = $event.newValue;
            }else{
                let observation:any = {};
                observation.effectiveDataTime = $event.colDef.field;
                observation.value = $event.newValue;
                observation.category = $event.data.category;
                if($event.data[$event.colDef.field+"-id"]){
                    observation.id = $event.data[$event.colDef.field+"-id"];
                }
                changedObservations.push(observation);
            } 
        }
        console.log('changedObservations:'+JSON.stringify(changedObservations));
        this.flowsheetServices.setChangedObservations(changedObservations);
    }

    onGridReady(params) {
        this.gridOptions.api.sizeColumnsToFit();
        this.getSocket();
    }

    ngOnInit() {
        this.flowsheetServices.getFlowSheetData(this.patientData.id,"vitalsigns").subscribe(
            data => {
                this.flowsheetData = data;
                this.flowsheetData.sort((first, second) => {
                    let firstDateTime: any = moment(first.observedDate)
                    let secondDateTime: any = moment(second.observedDate)
                    return secondDateTime - firstDateTime;
                })
                this.gridOptions = <GridOptions>{};
                this.gridOptions.defaultColDef = {};
                this.gridOptions.defaultColDef.enableCellChangeFlash = true;
                this.gridOptions.rowData = this.flowsheetData[0].observationData[0].subcategory;
                this.observedDate = moment(this.flowsheetData[0].observedDate).format('DD/MM/YYYY h:mm a');
                this.gridOptions.columnDefs = this.createColumnDefs(this.flowsheetData[0].observedDate);
                this.startGrid = true;
                this.loading = false;
                this.setDisableButton();
            },
            error => {
                console.log(error)
            }  
        );
    }
}



