import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { HistoryServices } from '../services/fhir/history.services';
import { ClinicalDocumentHistoryInterface } from './patient.history.interface';
import { ModelMessage } from '../model_message/model.message';
import * as moment from 'moment/moment';

@Component({
    moduleId: module.id,
    selector: 'patient-history',
    templateUrl: 'patient.history.component.html',
    styleUrls: ['patient.history.component.css'],
})

export class PatientHistoryComponent implements OnInit {

    public createclinicalDocForm: FormGroup;
    modeForHistory: string = 'Create';
    enableSaveButtonHistory: boolean = false;
    public submitted: boolean;
    patientId: any;
    encounterId: any;
    historyData: any;

    @Input() patientData: any;
    @Input() practitioner: any;

    constructor(private historyServices: HistoryServices, private router: Router, private fb: FormBuilder, private modelMessage: ModelMessage) { }

    prepareClinicalDocDataHistoryForm(historyData) {
        this.createclinicalDocForm = new FormGroup({
            fhirLogicalId: new FormControl(historyData.id),
            presentingComplaint: new FormControl(historyData.presentingComplaint),
            historyOfPresentingComplaint: new FormControl(historyData.historyOfPresentingComplaint),
            pastMedicalHistory: new FormControl(historyData.pastMedicalHistory),
            familyHistory: new FormControl(historyData.familyHistory),
            smokingAlcoholHistory: new FormControl(historyData.smokingAlcoholHistory),
            occupationalHistory: new FormControl(historyData.occupationalHistory),
            socialHistory: new FormControl(historyData.socialHistory)
        });
    }

    createClinicalDocumentHistoryForm() {
        this.createclinicalDocForm = new FormGroup({
            presentingComplaint: new FormControl(''),
            historyOfPresentingComplaint: new FormControl(''),
            pastMedicalHistory: new FormControl(''),
            familyHistory: new FormControl(''),
            smokingAlcoholHistory: new FormControl(''),
            occupationalHistory: new FormControl(''),
            socialHistory: new FormControl('')
        });
    }

    keyUpCheckHistoryForm() {
        this.enableSaveButtonHistory = true;
    }

    getClinicalDocument(patientId, encounterId) {
        this.historyServices.getClinicalDocumentHistoryForm(patientId, encounterId).subscribe(
            data => {
                    this.historyData = data;
                    if (this.historyData.id) {
                        this.modeForHistory = 'Edit';
                        this.prepareClinicalDocDataHistoryForm(this.historyData);
                    }
                
            },
            error => {
                this.modelMessage.errorMessage(error);
            }
        );
    }

    saveHistoryForm(model: ClinicalDocumentHistoryInterface, isValid: Boolean) {
        if ((this.modeForHistory == 'Create') || (this.modeForHistory == 'Edit')) {
            this.submitted = true;
            if (isValid) {
                let effectiveDate = moment().format('YYYY-MM-DDTHH:mm:ss+00:00')
                let clinicalDataHistoryForm = {
                    "historyFormData": model,
                    "patientData": this.patientData,
                    "effectiveDate": effectiveDate,
                    "practitionerData": this.practitioner
                }
                if (this.modeForHistory == 'Create') {
                    this.historyServices.createClinicaldocumentHistoryForm(clinicalDataHistoryForm).subscribe(
                        data => {
                            this.historyData = data;
                            this.prepareClinicalDocDataHistoryForm(this.historyData);
                            let msg = "Created Clinical Documentation History";
                            this.modelMessage.successMessage(msg);
                            this.modeForHistory = 'Edit';
                        },
                        error => {
                            let errorMessage = error;
                            this.modelMessage.errorMessage(errorMessage);
                        });
                }
                else if (this.modeForHistory == 'Edit') {
                    this.historyServices.updateClinicaldocumentHistoryForm(clinicalDataHistoryForm).subscribe(
                        data => {
                            this.historyData = data;
                            this.prepareClinicalDocDataHistoryForm(this.historyData);
                            let msg = "Updated Clinical Documentation History";
                            this.modelMessage.successMessage(msg);
                        },
                        error => {
                            let errorMessege = error;
                            this.modelMessage.errorMessage(errorMessege);
                        });
                }
            }
        }
    }

    ngOnInit() {
        this.patientId = this.patientData.id;
        this.encounterId = this.patientData.encounterId;
        this.createClinicalDocumentHistoryForm();
        this.getClinicalDocument(this.patientId, this.encounterId);
    }
}


