import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class ClinicalProgressNoteServices{

    constructor(private commonServices: CommonServices) {}

    getProgressNote(patientId,encounterId){
        return this.commonServices.dataServiceHandler("fhir/clinicalProgressNote/" + patientId + "/" + encounterId, 'GET')
    }

    createProgressNote(progressData){
        return this.commonServices.dataServiceHandler("fhir/clinicalProgressNote", 'POST', progressData)
    }
}



