import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { PhysicalExamServices } from '../services/fhir/physical.exam.services';
import { ClinicalDocumentPhysicalExamInterface  } from './patient.physical.exam.interface';
import { ModelMessage } from '../model_message/model.message';
import * as moment from 'moment/moment';

@Component({
    moduleId: module.id,
    selector: 'patient-physical-exam',
    templateUrl: 'patient.physical.exam.component.html',
    styleUrls: ['patient.physical.exam.component.css'],
})

export class PatientPhysicalExamComponent implements OnInit {

  public createclinicalPhysicalExamForm: FormGroup;
  modeForPhysicalExam: string = 'Create';
  enableSaveButtonPhysicalExam: boolean = false;
  public submitted: boolean;
  patientId: any;
  encounterId: any;
  physicalExamData: any;

  @Input() patientData: any;
  @Input() practitioner: any;

  constructor(private router: Router, private physicalExamServices: PhysicalExamServices, private fb: FormBuilder, private modelMessage: ModelMessage) { }


   createClinicalPhysicalExamDocument() {
    this.createclinicalPhysicalExamForm = new FormGroup({
      generalExamination: new FormControl(''),
      headEyeEnt: new FormControl(''),
      pulse: new FormControl(''),
      bloodPressure: new FormControl(''),
      temperature: new FormControl(''),
      height: new FormControl(''),
      weight: new FormControl(''),
      jugularVenousPressure: new FormControl(''),
      cardioVascular: new FormControl(''),
      respiratorySystem: new FormControl(''),
      centralNervousSystem: new FormControl(''),
      otherSystem: new FormControl('')
    });
  }

  keyUpCheckPhysicalExam() {
    this.enableSaveButtonPhysicalExam = true;
  }

  prepareClinicalDocDataPhysicalForm(physicalExamData) {
    this.createclinicalPhysicalExamForm = new FormGroup({
      fhirLogicalId: new FormControl(physicalExamData.id),
      generalExamination: new FormControl(physicalExamData.generalExamination),
      headEyeEnt: new FormControl(physicalExamData.headEyeEnt),
      pulse: new FormControl(physicalExamData.pulse),
      bloodPressure: new FormControl(physicalExamData.bloodPressure),
      temperature: new FormControl(physicalExamData.temperature),
      height: new FormControl(physicalExamData.height),
      weight: new FormControl(physicalExamData.weight),
      jugularVenousPressure: new FormControl(physicalExamData.jugularVenousPressure),
      cardioVascular: new FormControl(physicalExamData.cardioVascular),
      respiratorySystem: new FormControl(physicalExamData.respiratorySystem),
      centralNervousSystem: new FormControl(physicalExamData.centralNervousSystem),
      otherSystem: new FormControl(physicalExamData.otherSystem)
    });
  }


  getClinicalDocument(patientId, encounterId) {
    this.physicalExamServices.getClinicalDocumentPhysicalForm(patientId, encounterId).subscribe(
        data => {
                this.physicalExamData = data;
                if (this.physicalExamData.id) {
                    this.modeForPhysicalExam = 'Edit';
                    this.prepareClinicalDocDataPhysicalForm(this.physicalExamData);
                }
        },
        error => {
            this.modelMessage.errorMessage(error);
        }
    );
  }

  savePhysicalExamForm(model: ClinicalDocumentPhysicalExamInterface, isValid: Boolean) {
    if ((this.modeForPhysicalExam == 'Create') || (this.modeForPhysicalExam == 'Edit')) {
        this.submitted = true;
        if (isValid) {
            let effectiveDate = moment().format('YYYY-MM-DDTHH:mm:ss+00:00')
            let clinicalData = [];
            clinicalData.push(model);
            clinicalData.push(this.patientData);
            clinicalData.push(effectiveDate);
            let clinicalDataPhysicalExam = {
                "physicalExamData": model,
                "patientData": this.patientData,
                "effectiveDate": effectiveDate,
                "practitionerData": this.practitioner
            }
            if (this.modeForPhysicalExam == 'Create') {
                this.physicalExamServices.createClinicalDocumentPhysicalForm(clinicalDataPhysicalExam).subscribe(
                    data => {
                        this.physicalExamData = data;
                        this.prepareClinicalDocDataPhysicalForm(this.physicalExamData);
                        this.modeForPhysicalExam = 'Edit';
                        let msg = "Created Clinical Documentation Physical Exam";
                        this.modelMessage.successMessage(msg);
                    },
                    error => {
                        let errorMessege = error;
                        this.modelMessage.errorMessage(errorMessege);
                    });
            }
            else if (this.modeForPhysicalExam == 'Edit') {
                this.physicalExamServices.updateClinicalDocumentPhysicalForm(clinicalDataPhysicalExam).subscribe(
                    data => {
                        this.physicalExamData = data;
                        this.prepareClinicalDocDataPhysicalForm(this.physicalExamData);
                        let msg = "Updated Clinical Documentation Physical Exam";
                        this.modelMessage.successMessage(msg);
                    },
                    error => {
                        let errorMessege = error;
                        this.modelMessage.errorMessage(errorMessege);
                    });
            }
        }
    }
  }

  ngOnInit() {
    this.patientId = this.patientData.id;
    this.encounterId = this.patientData.encounterId;
    this.createClinicalPhysicalExamDocument()
    this.getClinicalDocument(this.patientId, this.encounterId);
  }
}



