import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class DeviceServices {

    constructor(private commonServices: CommonServices) {}

    getAvailableDevicesByOrg(fhirOrgId) 
    {
        return this.commonServices.dataServiceHandler("/fhir/device/" + fhirOrgId ,'GET');
    }

    getAllDevicesByLocationIdentifier(locationIdentifier) 
    {
        return this.commonServices.dataServiceHandler("/fhir/device/all/location/" + locationIdentifier ,'GET');
    }
}   


