import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { BedServices } from '../presence/bed.services';

@Injectable()
export class BedListResolver implements Resolve<any> {
    constructor( private bedServices:BedServices){

    }

    resolve(){
        let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
        if(hospitalId){
           return this.bedServices.getAllBedsByHospitalID(hospitalId).map(bedList => bedList);        
        }else{
           return this.bedServices.getAllBeds().map(bedList => bedList);         
            }   
        }
}


