import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class CameraServices {

    constructor(private commonServices: CommonServices) {}

    createCamera(formData) {
        return this.commonServices.dataServiceHandler("/api/camera",'POST',formData);
    }

    updateCamera(formData) {
        return this.commonServices.dataServiceHandler("/api/camera/" + formData.id,'PUT',formData);
    }

    getCamera(id) {
        return this.commonServices.dataServiceHandler('/api/camera/' + id,'GET');
    }

    getAllCameras() {
        return this.commonServices.dataServiceHandler('/api/camera/all','GET');
    }

    getAllCamerasByHospitalID(id) {
        return this.commonServices.dataServiceHandler('/api/camera/all/hospital/' + id,'GET');
    }

    getAllCamerasByDepartmentID(id) {
        return this.commonServices.dataServiceHandler('/api/camera/all/icu/' + id,'GET');
    }

    deleteCamera(id) {
        return this.commonServices.dataServiceHandler("/api/camera/" + id,'DELETE');
    }
}


