import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { UserServices } from '../services/presence/user.services';
import { ConsultantSearch } from '../consultants/search';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';


@Component({
    moduleId: module.id,
    selector: 'cm-consultants',
    templateUrl: 'consultants.component.html',
    styleUrls: ['consultants.component.css']
})

export class ConsultantsComponent implements OnInit {

    errorMessage: string;
    activeBtnGrid: string = "bg-info";
    activeBtnList: string = "";
    consultants: any[] = [];
    consultantsGridView: boolean = true;
    consultantsListView: boolean = false;
    loading: boolean = true;
    consultantsSearchbox: any;

    constructor(private router: Router, private route: ActivatedRoute, private userServices: UserServices) { }

    getConsultants() {
        this.userServices.getAllUsers().subscribe(data => {
            let responseData:any = data || [];
            responseData.forEach(currentConsultant => {
                if (currentConsultant.userType == "CC_CONSULTANT") {
                    let consultantDetails = {
                        id: currentConsultant.id,
                        userID: currentConsultant.userID,
                        firstName: currentConsultant.firstName,
                        lastName: currentConsultant.lastName,
                        email: currentConsultant.email,
                        designation: currentConsultant.designation,
                        mobile: currentConsultant.mobile,
                        landline: currentConsultant.landline,
                        userType: currentConsultant.userType,
                        qualification: currentConsultant.qualification,
                        hospitalId: currentConsultant.hospitalId,
                        zoomUserId: currentConsultant.zoomUserId,
                        pmi: currentConsultant.pmi,
                    };
                    this.consultants.push(consultantDetails);
                }
            });
            this.loading = false;
        });
    }

    clickConsultantGridView() {
        this.activeBtnGrid = "bg-info";
        this.activeBtnList = "";
        this.consultantsGridView = true;
        this.consultantsListView = false;
    }

    clickConsultantListView() {
        this.activeBtnGrid = "";
        this.activeBtnList = "bg-info";
        this.consultantsGridView = false;
        this.consultantsListView = true;
    }

    routeCreateConsultant() { }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    ngOnInit() {
        this.getConsultants()
        this.route.params.subscribe(params => {
            if (params['search']) {
                this.consultantsSearchbox= params['search'];
            }
        });
    }
}



