import { Injectable } from '@angular/core';

@Injectable()
export class RoleServices {

    scope: any[];
    role: any;

    constructor() { }

    resetRole() {
        this.role = { read: false, write: false, delete: false };
    }

    getRole(moduleRole) {
        this.scope = JSON.parse(sessionStorage.getItem('userScope'));

        this.resetRole();

        if (moduleRole === 'patient') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'PAT_READ') {
                    this.role.read = true;
                } else if (currentScope === 'PAT_WRITE') {
                    this.role.write = true;
                } else if (currentScope === 'PAT_DELETE') {
                    this.role.delete = true;
                }
            });
            return this.role;
        }else if (moduleRole === 'patient-detail') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'PAT_DETAIL_READ') {
                    this.role.read = true;
                } else if (currentScope === 'PAT_DETAIL_WRITE') {
                    this.role.write = true;
                }
            });
            return this.role;
        } else if (moduleRole === 'hospital') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'HOS_READ') {
                    this.role.read = true;
                } else if (currentScope === 'HOS_WRITE') {
                    this.role.write = true;
                } else if (currentScope === 'HOS_DELETE') {
                    this.role.delete = true;
                }
            });
            return this.role;
        } else if (moduleRole === 'bed') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'BED_READ') {
                    this.role.read = true;
                } else if (currentScope === 'BED_WRITE') {
                    this.role.write = true;
                } else if (currentScope === 'BED_DELETE') {
                    this.role.delete = true;
                }
            });
            return this.role;
        } else if (moduleRole === 'consultant' || moduleRole === 'user') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'USER_READ') {
                    this.role.read = true;
                } else if (currentScope === 'USER_WRITE') {
                    this.role.write = true;
                } else if (currentScope === 'USER_DELETE') {
                    this.role.delete = true;
                }
            });
            return this.role;
        } else if (moduleRole === 'camera') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'CAM_READ') {
                    this.role.read = true;
                } else if (currentScope === 'CAM_WRITE') {
                    this.role.write = true;
                } else if (currentScope === 'CAM_DELETE') {
                    this.role.delete = true;
                }
            });
            return this.role;
        } else if (moduleRole === 'department') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'DEP_READ') {
                    this.role.read = true;
                } else if (currentScope === 'DEP_WRITE') {
                    this.role.write = true;
                } else if (currentScope === 'DEP_DELETE') {
                    this.role.delete = true;
                }
            });
            return this.role;
        } else if (moduleRole === 'notes') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'NOTES_READ') {
                    this.role.read = true;
                } else if (currentScope === 'NOTES_WRITE') {
                    this.role.write = true;
                } else if (currentScope === 'NOTES_DELETE') {
                    this.role.delete = true;
                }
            });
            return this.role;
        } else if (moduleRole === 'meeting') {
            this.resetRole();
            this.scope.forEach(currentScope => {
                if (currentScope === 'MTG_READ') {
                    this.role.read = true;
                } else if (currentScope === 'MTG_WRITE') {
                    this.role.write = true;
                }
            });
            return this.role;
        }
    }

    getRoleInfo(role) {
        if (role === 'CC_ADMIN') {
            return {
                "patients": true,
                "hospital": true,
                "bed": true,
                "consultant": true
            }
        } else if (role === 'CC_CONSULTANT') {
            return {
                "patients": true,
                "hospital": true,
                "bed": true,
                "consultant": true
            }
        } else if (role.startsWith('HOS')) {
            return {
                "patients": true,
                "bed": true,
                "camera": true,
                "department": true
            }
        } else {
            return {
                "patients": false,
                "bed": false,
                "camera": false,
                "department": false
            }
        }
    }

    ngOnInit() {}
}



