
import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';
import { Router } from '@angular/router';

@Injectable()
export class LoginServices {
    
    constructor(private commonServices: CommonServices, private router:Router) { }

    login(loginCredentials) {
        return this.commonServices.dataServiceHandler("/auth/login",'POST',loginCredentials);
    }

    logout(){
        this.commonServices.dataServiceHandler("/auth/logout",'POST').subscribe(
           data => {
             sessionStorage.clear();
             this.router.navigate(['/']);
             location.reload();
           }
        );
    }
}



