import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DepartmentInterface } from './department.interface';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { DepartmentServices } from '../services/presence/department.services';
import { HospitalServices } from '../services/presence/hospital.services';
import { ModelMessage } from '../model_message/model.message';

@Component({
    moduleId: module.id,
    selector: 'cm-department-create',
    templateUrl: 'department.create.component.html',
    styleUrls: ['department.create.component.css']
})

export class DepartmentCreateComponent implements OnInit {
    public createDepartmentForm: FormGroup;
    private sub: any;
    public submitted: boolean;
    departments: any;
    hospitals: any;
    userData: any;
    depratmentData: any;
    hospitalName: string;
    mode: string = 'Create';
    hospitalId: any;
    createMode: boolean = true;
    disableCreateAction: boolean = false;

    constructor(private router: Router, private route: ActivatedRoute, private fb: FormBuilder, private hospitalServices: HospitalServices, private departmentServices: DepartmentServices, private modelMessage: ModelMessage) {

    }

    
    save(model: DepartmentInterface, isValid: boolean) {
        this.submitted = true;
        if (isValid) {
            model.hospitalId = this.hospitalId;
            if (!this.createMode) {
                this.disableCreateAction = true;
                this.departmentServices.updateDepartment(model).subscribe(
                    data => {
                        let msg = 'Department ' + model.name + ' was updated ';
                        this.modelMessage.successMessage(msg);
                        this.router.navigate(['/departments']);
                    },
                    error => {
                        this.disableCreateAction=false;
                        this.modelMessage.errorMessage(error);
                    }
                );
            } else {
                this.disableCreateAction = true;
                this.departmentServices.createDepartment(model).subscribe(
                    data => {
                        let msg = 'Department ' + model.name + ' was created ';
                        this.modelMessage.successMessage(msg);
                        this.router.navigate(['/departments']);
                    },
                    error => {
                        this.disableCreateAction=false;
                        this.modelMessage.errorMessage(error);
                    }
                );
            }
        }
    }

    createDepartment() {
        this.createDepartmentForm = new FormGroup({
            hospitalId: new FormControl(this.hospitalId),
            id: new FormControl('', ),
            name: new FormControl('', <any>Validators.required),
            type: new FormControl('', <any>Validators.required),

        });
    }


    editDepartment(data) {
        this.createDepartmentForm = new FormGroup({
            hospitalId: new FormControl(this.hospitalId),
            id: new FormControl(data.id),
            name: new FormControl(data.name),
            type: new FormControl(data.type, <any>Validators.required),

        });
    }

    clickDepartmentGridView() {
        this.router.navigate(['/departments']);
    }

    routeDashboard() {
        this.router.navigate(['/']);
    }

    routeDepartments() {
        this.router.navigate(['/departments']);
    }

    ngOnInit() {
        this.userData = JSON.parse(sessionStorage.getItem('userData'));
        this.hospitalId = this.userData.hospitalId;
        this.hospitals = this.route.snapshot.data['hospitalList'];
        this.hospitalName = this.hospitals.find(hospital => hospital.id == this.hospitalId).name;
        this.departments = this.route.snapshot.data['departmentList'];
        this.createDepartment();
        this.sub = this.route.params.subscribe(params => {
            if (params['id']) {
                this.createMode = false;
                this.mode = 'Edit';
                this.depratmentData = this.departments.find(department => department.id == params['id']);
                this.editDepartment(this.depratmentData);
            }
        });
    }

    ngOnDestroy() {
        this.sub.unsubscribe();
    }


}



