import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'PatientSearch'
})

@Injectable()

export class PatientSearch implements PipeTransform {
    transform(value: any, searchTerm: string) 
    {
       let filter = searchTerm.toUpperCase();
       return filter ? value.filter(patient=> patient.name.toUpperCase().indexOf(filter) != -1 
                                           || patient.mrn.toUpperCase().indexOf(filter) != -1) : value 
    }
}


