import { Injectable } from '@angular/core';
import { Resolve} from '@angular/router';
import { DeviceServices } from '../fhir/devices.services';

@Injectable()
export class DeviceListResolver implements Resolve<any> {
    constructor(private deviceServices:DeviceServices){}

    resolve(){
        let fhirOrgId = sessionStorage.getItem('fhirHospitalId');
        return this.deviceServices.getAvailableDevicesByOrg(fhirOrgId).map(devicesList => devicesList); 
    }
}


