import { Injectable } from '@angular/core';
import { CommonServices } from '../common.services';

@Injectable()
export class FlowsheetServices {

    private changedObservations = [];
    constructor(private commonServices: CommonServices) {}

    //changedObservations methods
    setChangedObservations(observations){
        if(observations.length == 0){
         this.changedObservations = [];
        }else{
         this.changedObservations = observations;
        }
    }

    getChangedObservations(){
         return this.changedObservations;
    }

    getFlowSheetData(patientId,flowsheetId){
        return this.commonServices.dataServiceHandler("/fhir/flowsheet/" + patientId + "/" + flowsheetId,'GET');
    }

    updateFlowsheetData(patientId,encounterId,flowsheetId,effectiveDate,flowSheetData){
        return this.commonServices.dataServiceHandler("/fhir/flowsheet/" + patientId + "/" + encounterId+"/"+flowsheetId+"/"+effectiveDate, 'POST', flowSheetData);
    }
}   


