import {Injectable, Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'UserSearch'
})

@Injectable()

export class UserSearch implements PipeTransform {
    transform(value: any, searchTerm: string) 
    {
       let filter = searchTerm.toUpperCase();
       return filter ? value.filter(patient=> patient.firstName.toUpperCase().indexOf(filter) != -1 
                                           || patient.lastName.toUpperCase().indexOf(filter) != -1
                                           || patient.userType.toUpperCase().indexOf(filter) != -1) : value 
    }
}


