import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { CameraServices } from '../presence/camera.services';

@Injectable()
export class CameraListResolver implements Resolve<any> {
    constructor( private cameraServices:CameraServices){
    }

    resolve(){
         let hospitalId = JSON.parse(sessionStorage.getItem('userData')).hospitalId;
         if(hospitalId){
             return this.cameraServices.getAllCamerasByHospitalID(hospitalId).map(cameraList => cameraList);
         }else{
             return this.cameraServices.getAllCameras().map(cameraList => cameraList);
         }
    }
}


